import * as d3 from 'd3';
import { CatameraGraph } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import ToolTip from '../../tooltip';

let toolTipClass;
class CMBubbleGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMBubbleGraph';
    toolTipClass = new ToolTip($ele);
  }

  setData(origData) {
    this.origData = Object.assign({}, origData);
    this.summaryData = Object.assign({}, origData);
    this.processData();
  }

  processData() {
    const origData = Object.assign({}, this.origData);
    const data = origData.data ? origData.data : origData;
    this.labels =
      origData.labels && origData.labels.length > 0 ? origData.labels : [{ label: 'label', value: 'value' }];

    this.dataType = data.labels && data.labels.length > 0 ? '2D' : '1D';
    if (data.labels && data.labels.length === 1) {
      const labelObj = data.labels[0];
      if (labelObj.label === 'label' && labelObj.value === 'value') {
        this.dataType = '1D';
      }
    }
    let formatedData = data.filter(d => {
      const _d = d;
      // filter out data object with one or more value undefined
      for (let i = 0; i < this.labels.length; i += 1) {
        if (!d[this.labels[i].value]) {
          _d.pass = false;
        }
      }
      return _d.pass !== false;
    });

    // handle data with multiple columns
    // Graph Specfic not applicable for 2D graphs
    if (this.labels.length > 1) {
      const averageData = [];
      this.labels.map(d =>
        averageData.push({
          label: d.label,
          value: d3.mean(formatedData.map(k => k[d.value])).toFixed(2),
        })
      );
      formatedData = averageData;
      this.dataType = '1D';
    }
    this.data = formatedData;
    this.formatedData_backup = [...formatedData];
  }

  viewTypeChanges(viewType) {
    const { preferences } = this || { preferences: {} };
    if (viewType == 'no-legends' || viewType == 'split-legends' || viewType == 'split') {
      this.displayLegends = preferences.showLegends || false;
    }
  }

  getBubbleHeight() {
    if (this.preferences.isFooterDisplayed) {
      return this.height * 0.9;
    }
    return this.height;
  }

  sortBubbleData(sortData, sortType) {
    if (sortType !== 'no-sort') {
      sortData.sort((a, b) => {
        if (sortType === 'asc') return a.value - b.value;
        else return b.value - a.value;
      });
    }
    return sortData;
  }

  render(animate = true, relayout = true) {
    let dataToBeRendered = null;
    super.render(animate, relayout);
    const { preferences } = this || { preferences: {} };
    const { normalize = false, groupExtraValues = false } = preferences;
    let config = getGraphConfigurations.call(this);

    if (config.sort.data) {
      const sortedData = this.sortBubbleData(this.formatedData_backup, config.sort.type);
      this.formatedData_backup = sortedData;
    }

    if (normalize) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      dataToBeRendered = this.normalizeData(this.formatedData_backup);
    } else if (groupExtraValues) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      dataToBeRendered = this.groupExtraValues(this.formatedData_backup);
    } else {
      dataToBeRendered = this.formatedData_backup;
    }
    this.data = dataToBeRendered;

    this.$baseGrp.selectAll('*').remove();

    if (config.viewType !== 'NA') {
      if (config.viewType === 'split') {
        var bubblepacks = CatameraGraph.circlesStraightPack().config(config);
        this.$baseGrp.datum(dataToBeRendered).call(bubblepacks);
      } else {
        var bubblepack = CatameraGraph.circlesPack().config(config);
        this.$baseGrp.datum(dataToBeRendered).call(bubblepack);
      }
    }

    if (config.viewType !== 'NA') {
      if (config.viewType === 'split-legends') {
        let legendGroup = CatameraGraph.splitLegends().config(config);
        this.$baseGrp.datum(dataToBeRendered).call(legendGroup);
      } else if (config.viewType === 'split') {
        let legendGroup = CatameraGraph.legendGroup().config(config);
        this.$baseGrp.datum(dataToBeRendered).call(legendGroup);
      } else {
        let legendGroup = CatameraGraph.legendGroup().config(config);
        this.$baseGrp.datum(dataToBeRendered).call(legendGroup);
      }
    }

    ////  Handling custom mouse events ////

    // tooltip
    // if (config.tooltipsEnabled) {
    var $circleBgs = this.$baseGrp.selectAll('.bubble-node');

    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;
      let temp = {};
      if (d.data) {
        const { label, value } = d.data;
        temp = { label, value };
      }
      return temp;
    };

    if ($circleBgs) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: dataToBeRendered,
        ...config,
      });
      $circleBgs
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this);
    }
    // }
    //onLoaded
  }
}
export default CMBubbleGraph;
