import MRFormatter from './mr-formatter';
import MRParseColors from './mr-parsecolor';

export function TooltipText(d) {
  let value;
  if (d.textValue) {
    value = d.textValue;
  } else {
    const formatted = MRFormatter.format(parseFloat(d.value), 'toNumber');
    value = formatted.fullValue + formatted.unit;
  }
  let label;
  if (d.labelText && d.labelText !== 'label') {
    label = d.labelText;
  } else if (d.label && d.label !== 'label') {
    label = d.label;
  } else if (d.data && d.data.label && d.data.label !== 'label') {
    label = d.data.label;
  } else if (d.subtext && d.subtext !== 'label') {
    // for donut chart
    label = d.subtext;
  } else {
    label = '';
  }

  // TODO: Needs to be tested
  const parent = this && this.showYAxislabel && this.showYAxislabel ? ' , ' + d.parrent : '';
  const percentage = this && this.showPercentage && this.showPercentage ? '%' : '';

  // console.log(label);
  return label
    ? `<div class=tooltip-row><div><span style=font-weight:400>${label} ${parent}</span>:<span>&nbsp;&nbsp;${value} ${percentage}</span></div></div>`
    : value;
}

export function Value(d, labels, graphColors) {
  let values = '';
  for (let i = 0; i < labels.length; i += 1) {
    if (labels[i].label && labels[i].value && d[labels[i].value]) {
      const icon = `<svg width="1.1em" height="1.1em" viewBox="0 0 10 10" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                        <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <g fill=" ${MRParseColors((labels[i] && labels[i].labelColor) || graphColors[i])} ">
                                <circle id="Oval-6" cx="3.5" cy="5.5" r="3.5"></circle>
                            </g>
                        </g>
                    </svg>`;
      const formatted = MRFormatter.format(parseFloat(d[labels[i].value]), 'toNumber');

      let formattedStr = d[labels[i].value];
      if (formatted && formatted.fullValue) {
        formattedStr = formatted.fullValue + formatted.unit;
      }
      const value = `<div class=tooltip-row style=text-align:left>
        ${icon}
        <div style="vertical-align:text-bottom;display:inline-block;max-width:calc(100% - 1.1em);"><span style=font-weight:400>
        ${labels[i].label} 
        </span>
        <span> :&nbsp;&nbsp;
        ${formattedStr}
        </span></div></div>`;
      values += value;
    }
  }
  return values;
}

export function ElectionResult(d, graphColors) {
  const entry = d || {};

  const party1 = entry['Party 1'];
  const party1Seats = entry['Party 1 Seats'];
  const party2 = entry['Party 2'];
  const party2Seats = entry['Party 2 Seats'];

  const _data = {
    Name: entry.Name,
  };
  const _labels = ['Name'];

  if (party1) {
    _data[party1] = party1Seats;
    _labels.push(party1);
  }
  if (party2 && party2 !== '0') {
    _data[party2] = party2Seats;
    _labels.push(party2);
  }

  _data['Total Seats'] = entry['Total Seats'];

  _labels.push('Total Seats');

  let values = '';
  for (let i = 0; i < _labels.length; i += 1) {
    if (_labels[i] && _data[_labels[i]]) {
      const icon = `<svg width="1.1em" height="1.1em" viewBox="0 0 10 10" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                        <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <g fill="${MRParseColors(graphColors[i])}">
                            <circle id="Oval-6" cx="3.5" cy="5.5" r="3.5"></circle>
                            </g>
                        </g>
                    </svg>`;
      const formatted = MRFormatter.format(parseFloat(_data[_labels[i]]), 'toNumber');

      let formattedStr = _data[_labels[i]];

      if (formatted && formatted.fullValue) {
        formattedStr = formatted.fullValue + formatted.unit;
      }

      const value = `<div class=tooltip-row style=text-align:left>
        ${icon}
        <div style="vertical-align:text-bottom;display:inline-block;max-width:calc(100% - 1.1em);"><span style=font-weight:400> 
        ${_labels[i]} 
        </span>  
        <span> :&nbsp;&nbsp; 
        ${formattedStr} 
        </span></div></div>`;
      values += value;
    }
  }
  return values;
}
