import * as d3 from 'd3';
import MRFormatter from '../../mr-formatter';
import MRParseColors from '../../mr-parsecolor';
import DropShadow from '../drop-shadow';
import getGraphCenter from './get-graph-center';

var pie = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    showBackground: true,
    showSliceLabel: true,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // for loading animation (will not be used when redraw)
  function tweenRadius(r, t) {
    var dampingFactor = 3;
    var dampingFactor2 = 2;
    if (t < 0.25) {
      return r - (r * t) / dampingFactor;
    } else if (t < 0.5) {
      return r + (r * (t - 0.5)) / dampingFactor;
    } else if (t < 0.75) {
      return r + (r * (t - 0.5) * (t - 0.5)) / dampingFactor2;
    } else {
      return r + (r * (t - 1) * (t - 1)) / dampingFactor2;
    }
  }

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;
    // calculate center position of the graph
    var { centerX, centerY } = getGraphCenter(config);

    selection.each(function(data) {
      if (!data) {
        data = [];
      }

      // shadow
      var dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      // add background slice
      if (config.showBackground) {
        // add background ring in case no data is available
        selection
          .selectAll('.bg-slice')
          .data([1])
          .enter()
          .append('circle')
          .attr('pointer-events', 'all')
          .attr('cursor', 'pointer')
          .attr('class', 'bg-slice');
        selection
          .selectAll('.bg-slice')
          .attr('fill', 'none')
          .attr('stroke', '#f9f7f7')
          .attr('stroke-width', config.radiusDefault - config.innerRadius)
          .attr('opacity', function() {
            let opacity = data.length > 0 && d3.max(data, d => parseFloat(d.value)) > 0 ? 0 : 1;
            if (config.graphType === 'pie') {
              opacity = 1;
            }
            return opacity;
          })
          .attr('fill', config.graphType === 'pie' ? '#f9f7f7' : '')
          .attr('transform', 'translate(' + centerX + ',' + centerY + ')')
          .attr('r', function() {
            var r = config.radiusDefault - (config.radiusDefault - config.innerRadius) / 2;
            if (config.graphType === 'pie') {
              r += config.fontSize;
            }
            return r >= 0 ? r : 0;
          });
      }

      const isRedraw = selection.selectAll('.slices')._groups[0].length > 0;

      // add slicesGrp
      var slicesGrp = selection.selectAll('.slices').data([1]);
      slicesGrp.exit().remove();
      slicesGrp
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slicesGrp)
        .attr('transform', 'translate(' + centerX + ',' + centerY + ')');

      var slices = selection
        .selectAll('.slices')
        .selectAll('path.slice')
        .data(config.pie(data));

      // Remove extra slices
      slices.exit().remove();
      // Added slices
      slices
        .enter()
        .append('path')
        .merge(slices)
        .style('fill', function(d, i) {
          return MRParseColors((d.data && d.data.labelColor) || config.graphColors[i % config.graphColors.length]);
        })

        .attr('class', 'slice')
        .attr('pointer-events', 'all')
        .attr('cursor', 'pointer');

      if (config.showSliceLabel) {
        var sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(config.pie(data));
        sliceLabel.exit().remove();
        sliceLabel
          .enter()
          .append('text')
          .attr('class', 'slice-label')
          .attr('opacity', '0')
          .attr('transform', function(d) {
            var pos = config.arc.centroid(d);
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('pointer-events', 'none');

        sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(config.pie(data))
          .attr('font-size', config.arcFontSize || config.radius / 10)
          .attr('text-anchor', 'middle')
          .attr('pointer-events', 'none')
          .attr('fill', 'white')
          .text(function(d) {
            if (Math.abs(d.endAngle - d.startAngle) < 0.5) {
              return '';
            }
            var percentage = (((d.endAngle - d.startAngle) / Math.PI / 2) * 100).toFixed(0) + '%';
            return percentage;
          })
          .attr('opacity', function() {
            return config.animate ? 0 : 1;
          })
          .transition()
          .duration(duration)
          .attr('transform', function(d) {
            var pos = config.arc.centroid(d);
            if (d.endAngle - d.startAngle > 6.283185) return 'translate(' + 0 + ',' + 0 + ')';
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('opacity', '1');
      }

      // add loading animation / redraw animation
      requestAnimationFrame(() => {
        var slices = selection.selectAll('.slices').selectAll('path.slice');
        let endAngle = 0;
        slices
          .transition()
          .ease(d3.easeCubicOut)
          .duration(function(d, i) {
            // loading animation duration
            let _duration = 0;
            if (!isRedraw) {
              // if (config.animate === true) {
              _duration = (duration + i * 0.095 * duration) * 1.8;
              // redraw animation duration
            } else {
              _duration = duration * 1.3;
            }
            return _duration;
          })
          // animation for slices

          .attrTween('d', function(d, i) {
            const defaultVal = {
              endAngle: endAngle,
              startAngle: endAngle,
            };
            const _current = this._current || defaultVal;
            // endAngle = d.endAngle;

            var interpolate = d3.interpolate(_current, d);

            this._current = interpolate(0);

            return function(t) {
              // loading animation
              if (!isRedraw) {
                // if (config.animate === true) {
                config.arc.outerRadius(function() {
                  return tweenRadius(config.radiusDefault, t);
                });
              }
              // redraw animation
              return config.arc(interpolate(t));
            };
          });
      });

      return selection;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    // set default config
    config.radius = config.radius ? config.radius : (Math.min(config.width, config.height) / 2) * 0.9;
    config.radiusDefault = config.radius;
    config.barWidth = config.barWidth || config.radius * 0.28;
    config.innerRadius =
      config.innerRadius != undefined && config.innerRadius != ''
        ? config.innerRadius
        : config.graphType === 'pie' && config.innerRadius != ''
        ? 0
        : config.radius - config.barWidth - 5;
    config.pie = d3
      .pie()
      .sort(null)
      .value(function(d) {
        return parseFloat(d.value);
      });
    config.arc = d3
      .arc()
      .outerRadius(config.radius)
      .innerRadius(config.innerRadius);

    return chart;
  };

  return chart;
};

export default pie;
