import React, { Component } from 'react';

class Box extends Component {
  constructor(props) {
    super(props);
    this.state = { redraw: false, showLoader: true };
    this.graphRef = React.createRef();
  }

  redraw = data => {
    this.graphRef.current._dataChanged(data, { update: true }, this.props.graphConfig);
  };

  componentDidMount() {
    try {
      if (!this.props.stopRerendering) {
        this.graphRef.current._dataChanged(this.props.data, {}, this.props.graphConfig);
      }
    } catch (error) {
      console.error(error);
    }
  }

  componentDidUpdate() {
    try {
      if (!this.props.stopRerendering) {
        this.graphRef.current._dataChanged(this.props.data, {}, this.props.graphConfig);
      }
    } catch (error) {
      console.error(error);
    }
  }

  inverseRedrawState() {
    this.setState(pervState => ({ redraw: !pervState.redraw }));
  }

  render() {
    const { props } = this;
    const DataVisualizationComponent = props.dataVisualizationComponent;
    const preferences = props.preferences || {};
    const styles = { position: 'relative', flexGrow: 1, overflow: 'hidden', height: '100%', width: '100%' };
    const getStyle = val => `${val}px`; // need to handle ems, rems etc
    if (preferences.width != null) styles.width = getStyle(preferences.width);
    if (preferences.height != null) styles.height = getStyle(preferences.height);

    if (props.clearDOM) return null;
    return (
      <section style={styles}>
        <section className="easein" style={{ width: '100%', height: '100%' }}>
          {!this.state.redraw ? (
            <DataVisualizationComponent
              preferences={this.props.preferences}
              ref={this.graphRef}
              data={this.props.data}
              getRerenderFunction={this.props.getRerenderFunction}
              graphRef={this.props.graphRef}
            />
          ) : null}
        </section>
      </section>
    );
  }
}

export default Box;
