/* eslint-disable no-param-reassign */
/* eslint-disable func-names */
import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';

const booleanMatrix = function() {
  let config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showValues: true,
  };

  // draw the graph here
  function chart(selection) {
    selection.each(data => {
      // shadow
      const dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      selection.selectAll('.row').remove();
      const rows = selection.selectAll('.row').data(data);
      rows.exit().remove();
      rows
        .enter()
        .append('g')
        .attr('class', 'row')
        .merge(rows);

      const circles = selection
        .selectAll('.row')
        .selectAll('.mrgraph-circle')
        .data((d, i) => {
          d.values.map(entry => {
            entry.labelIndex = i;
            entry.parrent = d.label;
            entry.parrentIndex = i;
          });
          return d.values;
        });

      circles.exit().remove();
      const r = Math.min(config.xScale.bandwidth() * 0.125, config.yScale.bandwidth() * 0.125, config.fontSize * 0.5);

      let positiveDataColor = config.graphColors[0];
      let negativeDataColor = config.graphColors[1];

      if (config.height > 0) {
        circles
          .enter()
          .append('circle')
          .attr('class', 'mrgraph-circle')
          .attr('r', 0)
          .attr('cx', d => config.x(d) + config.xScale.bandwidth() / 2 + r / 2)
          .attr('cy', d => config.y(d) + config.yScale.bandwidth() / 2)
          .merge(circles)
          .style('fill', d => {
            if (d.dataValue) return MRParseColors(config.positiveColor || positiveDataColor);
            return MRParseColors(config.negativeColor || negativeDataColor);
          })
          .style('cursor', 'pointer')
          .transition()
          .delay((d, i) => 0 + 25 * i)
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('r', r);
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default booleanMatrix;
