import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
var waterfall = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    viewRatio: 1,
    duration: 600,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showValues: true,
  };

  // Creating a responsive scale

  // draw the graph here
  function chart(selection) {
    selection.each(function(data, i) {
      var barGrp = selection.selectAll('.barGrp').data(data);
      barGrp.exit().remove();

      barGrp
        .enter()
        .append('g')
        .attr('class', 'barGrp')
        .merge(barGrp);

      var bar = selection
        .selectAll('.barGrp')
        .selectAll('.bar')
        .data(data[0]);

      bar.exit().remove();

      bar
        .enter()
        .append('rect')
        .attr('class', 'bar')
        .attr('height', 0)

        .attr('y', function(d, i) {
          return config.height;
        })
        .merge(bar)
        .attr('transform', function(d) {
          return 'translate(' + config.x(d.label) + ',0)';
        })

        .transition()

        .delay(function(d, i) {
          return 0 + 50 * i;
        })
        .ease(d3.easeQuad)
        .duration(500)
        .attr('y', function(d) {
          return config.y(Math.max(d.start, d.end));
        })
        .attr('height', function(d) {
          return Math.abs(config.y(d.start) - config.y(d.end));
        })
        .attr('width', config.xScale.bandwidth())

        .style('fill', function(d, i) {
          return MRParseColors(d.color || config.graphColors[i % config.graphColors.length]);
        });

      var valueGrp = selection.selectAll('.value-group').data([0]);
      valueGrp.exit().remove();
      valueGrp
        .enter()
        .append('g')
        .attr('class', 'value-group')
        .merge(valueGrp);

      var valueText = selection
        .selectAll('.value-group')
        .selectAll('.value-text')
        .data(data[0]);
      valueText.exit().remove();
      valueText
        .enter()
        .append('text')
        .attr('class', 'value-group')
        .attr('opacity', '0')
        .merge(valueText)
        .attr('text-anchor', 'middle')
        .attr('x', d => {
          return config.x(d.label) + config.xScale.bandwidth() / 2;
        })
        .attr('y', function(d) {
          return d.class === 'negative' ? config.y(d.end) + config.fontSize : config.y(d.end);
        })
        .attr('dy', function(d) {
          return d.class == 'negative' ? '-' : '';
        })

        .transition()
        .delay(function(d, i) {
          return 0 + 60 * i;
        })
        .ease(d3.easeQuad)
        .duration(600)
        .style('opacity', function(d) {
          return config.showValues ? 1 : 0;
        })
        .style('fill', function(d) {
          return config.valueColor ? config.valueColor : '#737b86';
        })

        .text(function(d) {
          if (d.label.toLowerCase() === 'delta') {
            return d.value.toString().includes('-') ? `(${d.value.toString().split('-')[1]})` : d.value;
          }
          return d.value;
        })
        .attr('font-size', function(d, i) {
          var valueFont = Math.min(config.xScale.bandwidth(), config.height, 12);
          var valueFontSize = config.valueFontSize || valueFont;
          return valueFontSize;
        });

      var lineGrp = selection.selectAll('.line-group-connector').data([0]);
      lineGrp.exit().remove();
      lineGrp
        .enter()
        .append('g')
        .attr('class', 'line-group-connector')
        .merge(lineGrp);

      var lineConnect = selection
        .selectAll('.line-group-connector')
        .selectAll('.line-connect')
        .data(data[0]);
      lineConnect.exit().remove();
      lineConnect
        .enter()
        .append('line')
        .attr('class', 'line-connect')
        .merge(lineConnect)
        .filter(function(d) {
          return d.class != 'total';
        })

        .attr('stroke', '#333')
        .attr('stroke-opacity', '0.4')

        .attr('x1', d => {
          return config.x(d.label) + config.xScale.bandwidth();
        })
        .attr('y1', function(d) {
          return config.y(d.end);
        })

        .attr('x2', d => {
          return config.x(d.label) + config.xScale.bandwidth() / 0.6;
        })
        .attr('y2', function(d) {
          return config.y(d.end);
        });
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);

    return chart;
  };

  return chart;
};

export default waterfall;
