import * as d3 from 'd3';
import { CatameraGraph, MRFormatter, Utils } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import ToolTip from '../../tooltip';
import withGraphComponent from '../../Graph-wrapper-HOC';
let toolTipClass;
class CMBarGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMBarGraph';
    toolTipClass = new ToolTip($ele);
  }

  setData(data) {
    const self = this;
    const normalize = this.preferences.normalize || false;
    if (normalize && data.data.length) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      data = this.normalizeData(data);
    }
    this.data = data.data ? data.data : data;
    this.summary = data.summary ? data.summary : '';
    this.labels = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (var i = 0; i < self.labels.length; i++) {
        if (!d[self.labels[i].value]) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });

    //
    var formattedData = [];

    var item = {};

    // process data object with multiple values
    for (var k = 0; k < this.labels.length; k++) {
      var items = [];
      for (var i = 0; i < this.filteredData.length; i++) {
        item = {
          label: this.filteredData[i].label,
          value: this.filteredData[i][this.labels[k].value],
          labelText: this.labels[k].label,
        };

        items.push(item);
      }
      formattedData.push(items);
    }
    this.dataType = this.labels && this.labels.length > 1 ? '2D' : '1D';
    if (data.labels && data.labels.length === 1) {
      const labelObj = data.labels[0];
      if (labelObj.label === 'label' && labelObj.value === 'value') {
        this.dataType = '1D';
      }
    }
    this.graphData = formattedData;
  }
  render(animate = true) {
    const self = this;
    super.render(animate);
    let config = getGraphConfigurations.call(this);

    //get yDomain
    var maxVal = 0;
    var minVal = 0;
    var maxLen = 0;
    var maxTotalVal = 0;

    if (this.graphData[0] && typeof this.graphData[0] == 'object') {
      for (var i = 0; i < this.graphData.length; i++) {
        var newMaxVal = d3.max(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.value)) ? parseFloat(d.value) : 0;
        });
        maxVal = newMaxVal > maxVal ? newMaxVal : maxVal;
        var newMinVal = d3.min(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.value)) ? parseFloat(d.value) : 0;
        });
        minVal = newMinVal < minVal ? newMinVal : minVal;
      }

      this.filteredData.map(function(d, k) {
        maxLen = d.label && d.label.length > maxLen ? d.label.length : maxLen;
        var i = 0;
        var totalVal = 0;
        for (var label in self.labels) {
          totalVal += parseFloat(d[self.labels[i].value]);
          self.graphData[i][k].accumulated = totalVal;
          i++;
        }
        // for comparing max-value
        maxTotalVal = maxTotalVal > totalVal ? maxTotalVal : totalVal;
      });
    }

    // calculating max  y value

    maxVal = Math.max(maxVal, maxTotalVal, this.xDomainMin[1]);
    minVal = Math.ceil(minVal * config.xAxisDomainFactor);
    maxVal = Math.ceil(maxVal * config.xAxisDomainFactor);
    const maxLabelLength = config.xAxisMaxLabelLength || 8;
    let maxTextSize = maxLen > maxLabelLength ? maxLabelLength : maxLen;

    maxVal = Math.ceil(maxVal * config.xAxisDomainFactor);

    // leave some space for y-axis
    var yAxisWidth = config.yAxisWidth || config.fontSize * (maxTextSize - 1 || 2);

    // Set Scales
    var xScale = d3
      .scaleLinear()
      .range([0, config.width - yAxisWidth])
      .domain([minVal, maxVal]);
    //  move scale to element default config

    if (config.percentageBased) {
      xScale = d3
        .scaleLinear()
        .range([0, config.width])
        .domain([100, this.xDomainMin[0]]);
    }

    var yScalePaddingouter = config.yScalePaddingOuter || 0;
    var yScale = d3
      .scaleBand()
      .range([0, config.height])
      .domain(
        this.filteredData.map(function(d, i) {
          return d.label;
        })
      );

    yScale.paddingInner(0.4).paddingOuter(yScalePaddingouter);

    // config for graphs
    config = {
      ...config,
      xScale: xScale,
      yScale: yScale,
      bandWidth: yScale.bandwidth(),
      y: (d, i) => {
        return yScale(d.label);
      },
      x: (d, i) => {
        return xScale(parseFloat(d.value));
      },
    };
    this.graphData = this.mapLabelsToColors(this.graphData);
    this.labels = this.mapLabelsToColors(this.labels);

    // bars
    // bars should use graphData instead of filteredData because of different data structure
    var bars = CatameraGraph.horizontalBars().config(config);
    this.$baseGrp.datum(this.graphData).call(bars);

    var legendGroup = CatameraGraph.legendGroup().config(config);
    if (this.labels.length > 1) {
      this.$baseGrp.datum(this.labels).call(legendGroup);
    } else {
      this.$baseGrp.datum(this.filteredData).call(legendGroup);
    }

    ////  Handling custom mouse events ////
    var $barFgs = this.$baseGrp.selectAll('.mrgraph-bar');

    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;
      let temp = {};
      if (d) {
        const { label, value, labelText } = d;
        temp.value = d.value;
        temp.label = d.label;

        if (`${d.labelText}`.toLowerCase() != 'label') {
          temp.value = d.value;
          temp.label = d.labelText;
          temp.subLabel = d.label;
        } else {
          temp.value = d.value;
          temp.label = d.label;
        }
      }

      return temp;
    };

    if ($barFgs) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: this.graphData,
        ...config,
      });
      $barFgs
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this);
    }
  }
}

export default withGraphComponent(CMBarGraph, { className: 'cm-bar' });
