import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';


var singleCircles = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 600,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    const self = this;
    selection.each(function(data, i) {
      var duration = config.animate ? config.duration : 0;

      var circleGrps = selection.selectAll('.circle-group').data(data);
      circleGrps.exit().remove();

      circleGrps
        .enter()
        .append('circle')
        .merge(circleGrps)
        .attr('cx', config.x)
        .attr('cy', config.height)
        // .attr("stroke-width",config.r*2)
        // .attr("stroke","transparent")
        .attr('r', function(d, i) {
          if (config.changeRadius) {
            return config.r * (d.radius ? d.radius : 1);
          }
          return config.r;
        })
        .style('fill', function(d, i) {
          return d.color ? MRParseColors(d.color) : MRParseColors(config.colors['default']);
        })
        .transition()
        .delay((d, i) => {
          return i * 10;
        })
        .ease(d3.easeQuadInOut)
        .duration(config.duration)
        .attr('cy', config.y);
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var scatterxAxis = function(mode) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    rotateAxis: false,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function chart(selection) {
    selection.each(function(data, i) {
      var numOfTicks = data[0].length;
      var xAxis = selection.selectAll('.x').data([1]);

      var xAxist = d3.axisBottom(config.xScale).tickFormat((d, i) => {
        if (typeof d == 'string' && d.length - 1 > 10) {
          return i % Math.ceil(numOfTicks / 6) == 0 ? d.slice(0, 7) + '..' : '';
        }
        if (numOfTicks >= 6) {
          return i % Math.ceil(numOfTicks / 6) == 0 ? d : '';
        } else {
          return d;
        }
      });

      var xAxisn = d3
        .axisBottom(config.xScaleN)
        .ticks(8)
        .tickSizeInner(0);

      xAxis
        .enter()
        .append('g')
        .attr('class', 'x axis')
        .merge(xAxis)
        .attr('transform', 'translate(' + config.graphPaddingLeft + ',' + (config.height + toPx(0.55)) + ')')
        .call(config.xAxisType == 'text' ? xAxist : xAxisn)
        .selectAll('text')
        .attr('y', 0)
        .attr('font-size', config.width / 40);

      var maxLength = 0;
      data.map(function(d) {
        if (d.label) {
          maxLength = maxLength > d.label.length ? maxLength : d.label.length;
        }
      });
    });

    return selection;
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export { singleCircles, scatterxAxis };

