/** @module NumberToHTML
 *   @author
 *   @description Tranform number to Formatted HTML template as per user-preferences
 */

import { camelToSnake, toObject } from './utils';

const _getStyleString = (styles = {}) => {
  let str = '';
  Object.keys(styles).forEach(s => {
    str += `${camelToSnake(s)}:${styles[s]}`;
  });
  return str;
};

const toHTML = (number, options = {}, _styles = {}) => {
  options.className = 'number';
  const formattedNumber = toObject(number, options);
  const { prefix, suffix, value, scaleSeparator, scale, prefixSeparator, suffixSeparator } = formattedNumber;

  const defaultStyles = {
    scale: {
      fontSize: '0.8em',
    },
    suffix: {
      fontSize: '0.8em',
    },
  };

  let keys = Object.keys(_styles);
  keys = Array.from(new Set(keys.concat(Object.keys(defaultStyles))));

  const styles = {};

  const className = options.className ? `${options.className} ${number}` : `number`;
  const classNames = {
    prefix: `${className}-prefix`,
    value: `${className}-value`,
    scale: `${className}-scale`,
    suffix: `${className}-suffix`,
  };

  keys.forEach(key => {
    styles[key] = Object.assign({}, _styles[key] || {}, defaultStyles[key] || {});
  });

  return `
<div>
  <span class="${classNames.prefix}" style="${_getStyleString(
    styles.prefix
  )}">${prefix}${prefixSeparator}</span><span class="${classNames.value}" style="${_getStyleString(
    styles.value
  )}">${value}</span><span  class="${classNames.scale}" style="${_getStyleString(
    styles.scale
  )}">${scaleSeparator}${scale}</span><span  class="${classNames.suffix}" style="${_getStyleString(
    styles.suffix
  )}">${suffixSeparator}${suffix}</span>
</div>
`;
};

export default toHTML;
