import * as d3 from 'd3';
import { CatameraGraph } from '../../GraphLibrary';
import ToolTip from '../../tooltip';
import getGraphConfigurations from '../../Graph-Configuration';
import withGraphComponent from '../../Graph-wrapper-HOC';
import './cm-clusterbubble.css';
let toolTipClass;
class CMClusterBubbleGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.$ele = $ele;
    this.graphCustomName = 'CMClusterBubbleGraph';
    toolTipClass = new ToolTip($ele);
  }

  setData(data) {
    this.data = data;
  }

  render(animate = true) {
    if (!this.data) return;

    let data = this.data;
    let config = getGraphConfigurations.call(this);

    config = { ...config, ...this.preferences };

    var clusterBubbleGraph = CatameraGraph.clusterBubble(this.$baseGrp).config(config);
    this.$baseGrp.datum({ data: data }).call(clusterBubbleGraph);

    // tooltip
    var $slices = d3.select(this.$ele).selectAll('.node');

    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;

      let temp = {};
      if (d.data) {
        const label = d.data.label;
        const value = d.value;
        temp = { label, value };
      }
      return temp;
    };

    if ($slices) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: data,
        ...config,
      });
      $slices
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this);
    }
  }
}

export default withGraphComponent(CMClusterBubbleGraph, { className: 'clusterBubble' });
