import * as d3 from 'd3';
import DropShadow from './drop-shadow';

const booleanMatrix = function() {
  let config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color4', 'color3', 'color1', 'color2', 'color5', 'color6'],
    showValues: true,
  };

  // draw the graph here
  function chart(selection) {
    const duration = config.animate ? config.duration || 600 : 0;
    selection.each((data, i) => {
      // shadow
      let dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      let rows = selection.selectAll('.row').data(data);
      rows.exit().remove();
      rows
        .enter()
        .append('g')
        .attr('class', 'row')
        .merge(rows);

      let boxes = selection
        .selectAll('.row')
        .selectAll('.mrgraph-box')
        .data((d, i) => {
          d.values.map(function(entry) {
            entry.labelIndex = i;
            entry.parrent = d.label;
            entry.parrentIndex = i;
          });
          return d.values;
        });

      boxes.exit().remove();

      if (config.height > 0) {
        boxes
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-box')
          .attr('r', 0)
          .attr('x', d => {
            return config.x(d);
          })
          .attr('y', d => {
            return config.y(d);
          })
          .merge(boxes)
          .style('fill', (d, i) => {
            return config.getBoxColour(parseInt(d.value));
          })

          .attr('width', 0)
          .attr('height', config.yScale.bandwidth())
          .style('stroke-width', 1)
          .style('stroke', '#fff')
          .style('opacity', 0.8)
          .style('cursor', 'pointer')
          .transition()
          .delay((d, i) => {
            return 0 + 25 * i;
          })
          .ease(d3.easeQuad)
          .duration(duration)
          .attr('width', config.xScale.bandwidth())
          .attr('height', config.yScale.bandwidth());
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default booleanMatrix;
