import * as d3 from 'd3';
import * as topojson from 'topojson';
import { CatameraGraph } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import world from './topojson/world';
import ToolTip from '../../tooltip';
import withGraphComponent from '../../Graph-wrapper-HOC';

const getValuesList = data => {
  var tempArray = [];
  const getValues = data => {
    if (data.value && typeof data.value === 'string') return tempArray.push(iparseInt(tem.value));
    if (Array.isArray(data)) {
      data.map(item => {
        if (item.values && Array.isArray(item.values)) return getValues(item.values);

        if (item.value && typeof item.value === 'string') tempArray.push(parseInt(item.value));
      });
    }
  };
  getValues(data);
  return tempArray.filter(a => a > 0);
};

let toolTipClass;

class CMChoroplethGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMChoroplethGraph';
    toolTipClass = new ToolTip($ele);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
  }

  setData(inData) {
    const data = JSON.parse(JSON.stringify(inData));
    this.data = data.data ? data.data : data;
    this.colors = data.colors || this.colors;
    this.summary = data.summary ? data.summary : '';
    this.labels = this.data && this.data.length > 0 ? this.data : [{ label: 'label', value: 'value' }];
    this.dataType = '2D';
    this.graphData = this.data;

    return this.formattedData;
  }

  render(animate = true) {
    super.render();
    if (!this.data) return;

    const { preferences } = this;
    const { legendType = 'value' } = preferences;

    const values = getValuesList(this.data);
    let maxValue = legendType == 'value' ? Math.max(...values) : 100;
    let minValue = legendType == 'value' ? Math.min(...values) : 0;
    let range = maxValue - minValue;
    let domain = [
      minValue,
      parseInt(minValue + range * 0.16),
      parseInt(minValue + range * 0.33),
      parseInt(minValue + range * 0.5),
      parseInt(minValue + range * 0.66),
      parseInt(minValue + range * 0.83),
      maxValue,
    ];

    //Legends value based or percentage based.
    const legends =
      legendType === 'value'
        ? [
            {
              label: `${domain[0]}-${domain[1]}`,
              value: `${domain[0]}-${domain[1]}`,
            },
            {
              label: `${domain[3]}-${domain[4]}`,
              value: `${domain[3]}-${domain[4]}`,
            },
            {
              label: `${domain[1]}-${domain[2]}`,
              value: `${domain[1]}-${domain[2]}`,
            },
            {
              label: `${domain[4]}-${domain[5]}`,
              value: `${domain[4]}-${domain[5]}`,
            },
            {
              label: `${domain[2]}-${domain[3]}`,
              value: `${domain[2]}-${domain[3]}`,
            },
            {
              label: `${domain[5]}-${domain[6]}`,
              value: `${domain[5]}-${domain[6]}`,
            },
          ]
        : [
            {
              label: `${domain[0]}-${domain[1]}%`,
              value: `${domain[0]}-${domain[1]}%`,
            },
            {
              label: `${domain[3]}-${domain[4]}%`,
              value: `${domain[3]}-${domain[4]}%`,
            },
            {
              label: `${domain[1]}-${domain[2]}%`,
              value: `${domain[1]}-${domain[2]}%`,
            },
            {
              label: `${domain[4]}-${domain[5]}%`,
              value: `${domain[4]}-${domain[5]}%`,
            },
            {
              label: `${domain[2]}-${domain[3]}%`,
              value: `${domain[2]}-${domain[3]}%`,
            },
            {
              label: `${domain[5]}-${domain[6]}%`,
              value: `${domain[5]}-${domain[6]}%`,
            },
          ];

    this.labels = legends;

    // Get the config.
    var config = getGraphConfigurations.call(this);
    this.$baseGrp.selectAll('*').remove();

    const { scale, graphColors } = config;

    // Indexing the data by the country Name for all countries.
    let indexedData = this.data.reduce((accumulator, d) => {
      accumulator[d.label] = d;
      return accumulator;
    }, {});

    let countries = topojson.feature(world, world.objects.countries).features;

    let getColor = config.getColor
      ? config.getColor
      : d3
          .scaleQuantize()
          .domain([minValue, maxValue])
          .range([...graphColors]);

    const projection = d3
      .geoEquirectangular()
      .translate([config.width / 2.1, config.height / 1.7])
      .scale(scale);

    const path = d3.geoPath().projection(projection);

    config = {
      ...config,
      height: 400,
      countries,
      getColor,
      projection,
      path,
      indexedData,
      mode: 'Choropleth',
    };

    var maps = CatameraGraph.chloroPleth().config(config);
    this.$baseGrp.datum(this.formattedData).call(maps);

    //legends
    var legendGroup = CatameraGraph.legendGroup().config(config);
    this.$baseGrp.datum(legends).call(legendGroup);

    var $mapGs = this.$baseGrp.selectAll('.country');
    const dataExtractionMethod = requiredParameters => {
      const { d, valuePrefix, valueSuffix } = requiredParameters;
      const data = indexedData[d.properties.name];
      let temp = {};
      if (data) {
        const { label, value, code } = data;
        temp = { label, value, valuePrefix, valueSuffix };
        console.log({ temp });
        return temp;
      }
      return temp;
    };

    if ($mapGs) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.labels,
        data: this.graphData,
        ...config,
      });
      $mapGs
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this)(config);
    }
  }
}
export default withGraphComponent(CMChoroplethGraph, { className: 'cm-choropleth' });
