import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';

var gaugeAutomation = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color2'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function calcAngle(percentage) {
    return ((percentage * 1.8 - 90) * Math.PI) / 170;
  }

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;

    selection.each(function(data, i) {
      var self = this;
      var arc1, arc2, arc, innerBarWidth;
      var tHeight = config.width / 62.6;
      var ty = config.width / 31;
      var fx = 3.8;
      this.easing = d3.easeQuadInOut;
      var barWidth = config.width / 9.7;
      data.value = parseInt(data.value);
      var safeValue = data.maxValue ? (data.value / data.maxValue) * 100 : data.value;
      if (safeValue > 100) {
        safeValue = 100;
      } else if (safeValue < 0) {
        safeValue = 0;
      }
      data.safeValue = safeValue;

      var innerBarWidth = config.width / 52;

      selection.select('.cm-value-grp').remove();
      selection.select('.cm-needle-grp').remove();
      selection.select('.cm-labels-grp').remove();

      // calculate center position of the graph config.width/15.5
      // var cX = (config.width - toPx(config.margin.left)/2)/2 + toPx(config.margin.left);
      var cX = (config.width - config.width / 15.5 / 2) / 2 + config.width / 15.5;
      var cY = config.height * 0.8 + toPx(config.margin.top);
      config.cX = cX;
      config.cY = cY;

      var slices = selection.selectAll('.slices').data([1]);
      slices.exit().remove();
      slices
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slices)
        .attr('transform', 'translate(' + cX + ',' + cY + ')');

      var arcEmpty = selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .data([1]);
      arcEmpty.exit().remove();
      arcEmpty
        .enter()
        .append('path')
        .attr('class', 'arc empty')
        .merge(arcEmpty);

      var arc1 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius - barWidth);
      var arc2 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius - barWidth);
      var arc3 = d3
        .arc()
        .outerRadius(innerBarWidth + config.innerRadius)
        .innerRadius(config.innerRadius);

      this.calcAngle = calcAngle;

      // Draw empty arc
      arc3.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc2.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc1.startAngle(calcAngle(0));
      selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .attr('d', arc2);
      selection
        .selectAll('.slices')
        .selectAll('.arc.inner')
        .attr('d', arc3);

      // set prevData for animation purpose
      // auto-detect if we need loading animation
      var isRedraw = selection.selectAll('.slices').selectAll('.arc.filled')._groups[0].length > 0;
      var prevData = isRedraw
        ? selection
            .selectAll('.slices')
            .selectAll('.arc.filled')
            .datum()
        : { value: 0, safeValue: 0 };

      // draw filled arc
      var arcFilled = selection
        .selectAll('.slices')
        .selectAll('.arc.filled')
        .data([data]);
      arcFilled.exit().remove();
      arcFilled
        .enter()
        .append('path')
        .attr('class', 'arc filled')
        .merge(arcFilled)
        .attr('fill', MRParseColors(config.graphColors[0]))
        .transition()
        // .delay(duration/2)
        .duration(duration * 2)
        .ease(this.easing)
        .attrTween('d', function(d, i) {
          var interpolate = d3.interpolate(prevData.safeValue, d.safeValue);
          return function(t) {
            var val = interpolate(t);
            arc1.endAngle(calcAngle(val));
            return arc1(interpolate(t));
          };
        });

      //subtext
      data.value = parseInt(data.value);
      var safeValue = data.maxValue ? (data.value / data.maxValue) * 100 : data.value;
      if (safeValue > 100) {
        safeValue = 100;
      } else if (safeValue < 0) {
        safeValue = 0;
      }

      var subtext = selection.append('g').attr('class', 'cm-value-grp');
      subtext
        .append('text')
        .attr('text-anchor', 'middle')
        .attr('transform', 'translate(' + cX + ',' + (cY + config.width / 9) + ')')
        .attr('font-weight', '500')
        .attr('fill', '#4f4f4f')
        .attr('y', function(d) {
          return data.subtext ? '-.8em' : '-.8em';
        })
        .attr('font-size', config.width / 10)
        .text(safeValue + '%');

      subtext
        .append('text')
        .attr('class', 'summarySubtext')
        .attr('text-anchor', 'middle')
        .attr('transform', 'translate(' + cX + ',' + (cY + config.width / 9) + ')')
        .attr('font-weight', '500')
        .attr('y', function(d) {
          return data.subtext ? '-.2em' : '';
        })
        .attr('fill', '#4f4f4f')
        .attr('font-size', config.width / 18)
        .text(data.subtext);

      var valueTextWidth = subtext
        .selectAll('.summarySubtext')
        .node()
        .getBBox().width;
      var radii = cY + config.width / 20;

      subtext
        .append('line')
        .merge(subtext.selectAll('line'))
        .attr('x1', cX - valueTextWidth / 2)
        .attr('y1', radii + config.width / 14)
        .attr('x2', cX + valueTextWidth / 1.9)
        .attr('y2', radii + config.width / 14)
        .attr('stroke-width', 2)
        .attr('stroke', function(i) {
          return MRParseColors(config.graphColors[0]);
        })
        .attr('opacity', function(i) {
          var values = [1, 0.95, 0.9];

          return values[i];
        });
    });
    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default gaugeAutomation;
