import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';

var arcElement = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color5'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function calcAngle(percentage) {
    return ((percentage * 1.8 - 90) * Math.PI) / 170;
  }
  function percToDeg(percentage) {
    return ((percentage * 1.8 + (90 * 170) / 180 - 90) * 180) / 170;
  }
  function xy(x, y) {
    return x - config.cX + ',' + (y - config.cY);
  }

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;

    selection.each(function(data, i) {
      var self = this;
      var arc1, arc2, arc3, arc, innerBarWidth;

      var fx = 3.8;
      this.easing = function(t) {
        return (1 - 1 / t) * 0.04 * fx * Math.sin((25 / fx) * t) + 1;
      };
      this.easing = d3.easeQuad;

      data.value = parseInt(data.value);
      var safeValue = data.maxValue ? (data.value / data.maxValue) * 100 : data.value;
      if (safeValue > 100) {
        safeValue = 100;
      } else if (safeValue < 0) {
        safeValue = 0;
      }
      data.safeValue = safeValue;

      innerBarWidth = toPx(0.3);

      // calculate center position of the graph
      var cX = (config.width - toPx(config.margin.left) / 2) / 2 + toPx(config.margin.left);
      var cY = config.height * 0.8 + toPx(config.margin.top);
      config.cX = cX;
      config.cY = cY;

      var slices = selection.selectAll('.slices').data([1]);
      slices.exit().remove();
      slices
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slices)
        .attr('transform', 'translate(' + cX + ',' + cY + ')');

      // var arcInner = selection.selectAll('.slices').selectAll('.arc.inner').data([1]);
      // arcInner.exit().remove();
      // arcInner.enter()
      // .append('path').attr('class', 'arc inner').merge(arcInner)
      // .attr('fill', '#e3e3e3')

      var arcEmpty = selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .data([1]);
      arcEmpty.exit().remove();
      arcEmpty
        .enter()
        .append('path')
        .attr('class', 'arc empty')
        .merge(arcEmpty)
        .attr('fill', '#e3e3e3');

      arc1 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius - config.barWidth);
      arc2 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius - config.barWidth);
      arc3 = d3
        .arc()
        .outerRadius(innerBarWidth + config.innerRadius)
        .innerRadius(config.innerRadius);

      this.calcAngle = calcAngle;

      // Draw empty arc
      arc3.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc2.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc1.startAngle(calcAngle(0));
      selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .attr('d', arc2);
      selection
        .selectAll('.slices')
        .selectAll('.arc.inner')
        .attr('d', arc3);

      // set prevData for animation purpose
      // auto-detect if we need loading animation
      var isRedraw = selection.selectAll('.slices').selectAll('.arc.filled')._groups[0].length > 0;
      var prevData = isRedraw
        ? selection
            .selectAll('.slices')
            .selectAll('.arc.filled')
            .datum()
        : { value: 0, safeValue: 0 };
      var isResize = prevData == data;

      if (!isRedraw) {
        arc1.endAngle(calcAngle(0));
      } else if (!isResize) {
        config.animate = true;
      }

      // draw filled arc
      var arcFilled = selection
        .selectAll('.slices')
        .selectAll('.arc.filled')
        .data([data]);
      arcFilled.exit().remove();
      arcFilled
        .enter()
        .append('path')
        .attr('class', 'arc filled')
        .merge(arcFilled)
        .attr('pointer-events', 'all')
        .attr('cursor', 'pointer')
        .attr('fill', MRParseColors(config.graphColors[0]))
        .transition()
        .duration(config.duration)
        .ease(this.easing)
        .attrTween('d', function(d, i) {
          var interpolate = d3.interpolate(prevData.safeValue, d.safeValue);
          return function(t) {
            var val = interpolate(t);
            arc1.endAngle(calcAngle(val));
            return arc1(interpolate(t));
          };
        });

      return selection;
    });
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default arcElement;
