import * as d3 from 'd3';

let xAxis = function(mode, config) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    viewRatio: 1,
  };

  let showLabel = (i, objectLength) => {
    let a = parseInt(objectLength / 5);
    if (i % a === 0) {
      return true;
    }
  };

  let showValue = d => {
    const { xAxisMaxLabelLength = null } = config;
    if (xAxisMaxLabelLength) {
      if (d.length < config.xAxisMaxLabelLength) {
        return d;
      } else {
        return d.slice(0, config.xAxisMaxLabelLength) + '..';
      } 
    } else {
      return d;
    }
  };

  const getXScaleWidth = (xScale, configWidth) => {
    const range = (xScale && xScale.range()) || [];

    // If xScale is defined, then return width based on the scale's range as it is more accurate
    if (range.length === 2) return range[1] - range[0];

    // Else return config.width
    return configWidth;
  };

  function chart(selection) {
    selection.each(function(data, i) {
      let objectLength = Object.keys(data[0]).length - 1;
      let xAxis = selection.selectAll('.x').data([1]);
      let fontSize = config.fontSize || Math.max(config.width / 40, 8) * config.viewRatio;
      const xScaleWidth = getXScaleWidth(config.xScale, config.width);
      const gridLine = {};

      xAxis
        .selectAll('.title')
        .data([config.xAxisTitle])
        .join(enter =>
          enter
            .append('text')
            .attr('class', 'xaxis title')
            .text(d => d)
        )
        .attr('transform', `translate(${config.width / 2}, ${fontSize * 2.3})`);

      xAxis
        .enter()
        .append('g')
        .attr('class', 'x axis')
        .merge(xAxis)
        .attr('transform', 'translate(' + 0 + ',' + config.height + ')')
        .style('display', d => {
          return config.showAxisX ? '' : 'none';
        })
        .call(
          d3
            .axisBottom(config.xScale)
            .ticks(2)
            .tickSize(
              (() => {
                return -config.height + config.range.top;
              })()
            )
            .tickFormat((d, i) => {
              let text = `${d}`;
              if (config.xAxisMaxLabelLength && config.xAxisMaxLabelLength < text.length) {
                text = text.slice(0, +config.xAxisMaxLabelLength) + '...';
              }
              if (mode == 'simple') {
                return i % 2 == 1 ? '' : text;
              } else if (mode === 'heatmap') {
                if (objectLength <= 6 || showLabel(i, objectLength)) {
                  return showValue(text);
                } else return '';
              } else if (mode === 'column') {
                if (text.length < 4) {
                  return text.toUpperCase();
                } else {
                  return text;
                }
              } else {
                let arrLen = data.length;
                if (data && data.constructor === Array && data[0] && data[0].constructor === Array) {
                  arrLen = data[0].length;
                }

                /**
                 * Following code is to limit the text-length & number of labels, displayed in xAxis to prevent their overlap
                 * 1: Calculate default 'averageLabelLength' based on xScaleWidth and fontSize
                 * 2: Adjust averageLabelLength, based on user preference or min/max length
                 * 3: Calculate maxTicks, based on averageLabelLength
                 * 4: Hide/Show/Trim based on maxTicks and averageLabelLength
                 */

                // Assumptions
                const charWidth = fontSize * 0.6; // Average width per character

                // 1: Calculate averageLabelLength
                const barWidth = xScaleWidth / (arrLen || 1);
                let averageLabelLength = Math.floor(barWidth / charWidth) - 1;

                // 2: Adjust averageLabelLength
                const minLength = +config.xAxisMinLabelLength || 4;
                const maxLength = +config.xAxisMaxLabelLength || 15;
                if (averageLabelLength < minLength) averageLabelLength = minLength;
                else if (averageLabelLength > maxLength) averageLabelLength = maxLength;

                // 3: Calculate maxTicks
                const _labelWidth = (averageLabelLength + 1) * charWidth; // Approximate length of the label + padding.
                const _maxTicks = Math.floor(xScaleWidth / _labelWidth); // Max number of labels that can be displayed for current graph width.
                const _i = Math.ceil(arrLen / _maxTicks) || 1;

                // 4: Hide/Show/Trim based on maxTicks and averageLabelLength
                if (i % _i || averageLabelLength <= 0) {
                  gridLine[i] = false;
                  return ''; // Hide '_i' th label
                }

                gridLine[i] = true;

                if (d.toString().length > averageLabelLength) {
                  if (averageLabelLength > 1)
                    return (
                      d
                        .toString()
                        .slice(0, averageLabelLength)
                        .trim() + '..'
                    );
                  else return d.toString().slice(0, averageLabelLength) + '..';
                }
                return d;
              }
            })
        )
        .selectAll('text')
        .attr('y', function() {
          return fontSize;
        })
        .attr('fill', config.xAxisColor || 'currentColor')
        .attr('font-size', fontSize);

      selection
        .selectAll('.x')
        .selectAll('line')
        .attr('y2', -config.graphHeight * 0.99 || 0)
        .attr('transform', `translate(${config.xScale.step() / 2 - 1}, ${config.svgHeight - config.height})`);

      if (config.axisXLines) {
        selection
          .selectAll('.x')
          .selectAll('line')
          .filter(function name(d, i) {
            return `${config.axisXLines}`.toLowerCase() === 'all' || gridLine[i];
          })
          .attr('y2', -config.height)
          .attr('y1', -config.range.bottom)
          .style('display', 'block')
          .attr('transform', ``);
      }
      if (config.rotateAxis) {
        selection
          .selectAll('.x')
          .selectAll('text')
          .attr('text-anchor', 'end')
          .attr('style', 'transform: rotate(-25deg)');

        selection.selectAll('.x').attr('transform', 'translate(0, ' + (config.height + config.width / 70) + ')');
      }
    });

    return selection;
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    config.fontSize = config.xAxisFontSize || config.fontSize;
    return chart;
  };

  return chart;
};
export default xAxis;
