import * as d3 from 'd3';
import { MRParseColors } from '../../GraphLibrary';
import Utils from '../../GraphLibrary/graph-elements/utils';

const stackedBar = function() {
  var oldpath = '';
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showValues: true,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;
    selection.each(function(data, i) {
      var lastPos = 0;

      const defs = selection.selectAll('defs').data([1]);

      const uniqueId = `StBar${Utils.makeid()}`;
      defs
        .enter()
        .append('defs')
        .append('clipPath')
        .append('rect')
        .merge(defs)
        .select('clipPath')
        .attr('id', function(d, i) {
          return `s${i}${uniqueId}`;
        });
      defs.exit().remove();

      const clipPath0 = selection.select(`#s0${uniqueId}`);

      // selection.selectAll('g').remove();

      var rects = selection.selectAll('.single-bar').data([1]);
      rects.exit().remove();
      rects
        .enter()
        .append('g')
        .attr('class', 'single-bar');
      rects = selection.selectAll('.single-bar');
      let posForPath = 0;
      let posX = 0;
      let d3line = d3
        .line()
        .x(d => {
          var x = posForPath;
          posForPath += config.xScale(d);
          return x;
        })
        .y(config.height * 0.8);
      let max = Math.max.apply(
        Math,
        data.map(function(o) {
          return o.value;
        })
      );
      let gapBetweenBar = (1 / 100) * max;

      let accumulated = 0;
      data.forEach((d, i) => {
        d.accumulated = accumulated;
        d.barHeight = i === 0 ? config.barHeight * 3 : config.barHeight * 1.2;

        accumulated += parseFloat(d.value);
        let valueArray = [];
        valueArray.push(parseFloat(d.value), gapBetweenBar);
        d.valueArray = valueArray;
      });
      var paths = rects.selectAll('.path').data(data);

      clipPath0
        .select('rect')
        .attr('x', 0)
        .attr('rx', 5)
        .attr('width', config.width)
        .attr('height', config.barHeight * 1.2)
        .attr('y', config.barHeight * 3 - config.barHeight * 0.6);

      paths.exit().remove();
      paths
        .enter()
        .append('rect')
        .attr('class', (d, i) => {
          if (i == 0) {
            return 'path corner-left';
          } else if (i == data.length - 1) {
            return 'path corner-right';
          } else {
            return 'path corner-center';
          }
        })
        .attr('x', function(d, i) {
          return 0;
        })
        .attr('width', function(d, i) {
          return 0;
        })
        .merge(paths)
        .attr('clip-path', function(d, i) {
          return i === data.length - 1 ? `url(#s0${uniqueId})` : '';
        })
        .attr('y', function(d, i) {
          return config.barHeight * 3 - d.barHeight / 2;
        })
        .attr('height', function(d, i) {
          return d.barHeight;
        })
        .attr('fill', d => MRParseColors(d.color))
        .on('mouseover', function(d, i) {
          if (i === data.length - 1) {
            clipPath0
              .select('rect')
              .transition()
              .duration((config.duration || 300) / 2)
              .attr('height', config.barHeight * 3)
              .attr('y', config.barHeight * 1.5);
          }

          d3.select(this)
            .transition()
            .duration((config.duration || 300) / 2)
            .attr('y', function(d, i) {
              return config.barHeight * 1.5;
            })
            .attr('height', function(d, i) {
              return config.barHeight * 3;
            });
        })
        .on('mouseout', function(d, i) {
          if (i === data.length - 1) {
            clipPath0
              .select('rect')
              .transition()
              .duration((config.duration || 300) / 2)
              .attr('height', d.barHeight)
              .attr('y', config.barHeight * 3 - d.barHeight / 2);
          }
          d3.select(this)
            .transition()
            .duration((config.duration || 300) / 2)
            .attr('height', function(d, i) {
              return d.barHeight;
            })
            .attr('y', function(d, i) {
              return config.barHeight * 3 - d.barHeight / 2;
            });
        })
        .transition()
        .duration(config.duration)
        .attr('x', function(d, i) {
          return config.x0(d);
        })
        .attr('width', function(d, i) {
          return config.x(d);
        });

      selection.select('.single-bar').attr('transform', d => {
        return `translate(${config.width * 0.05}, ${config.height - config.barHeight * 2})    scale(0.9,1)`;
      });
    });
    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(value) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign({}, config, value);
    return chart;
  };

  return chart;
};
export default stackedBar;
