/** @module cm-severity.js
 *   @author
 *   @description React Component wrapper for cm-severity graph
 */

import React, { Component } from 'react';
import '../../GraphLibrary/graph.css';
import './cm-stacked-bar-graph.css';
import CMSingleBarGraphElement from './cm-stacked-bar.graph';

class CMSingleBarElement extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];

    this.state = {
      colors: props.colors || this.colors,
      data: props.data || {},
    };
    this.graphRef = React.createRef();
  }

  render() {
    return (
      <div className={`cm-stacked-bar-graph cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp graph" ref={this.graphRef} id="graph">
          <div id="box" />
        </div>
      </div>
    );
  }

  _dataChanged(d) {
    if (d) {
      this.graph = this.graph || new CMSingleBarGraphElement(this.graphRef.current);

      this.graph.colors = this.colors;

      d.data.forEach((d, i) => {
        d.color = this.colors[i % this.colors.length];
      });
      this.graph.setData(d);
      requestAnimationFrame(() => {
        this.graph.render();
      });
    }
  }

  draw(animate) {
    this.graph.render(animate);
  }

  resize() {
    if (!this.graph) return;
    requestAnimationFrame(() => {
      this.graph.render(false);
    });
  }

  componentDidMount() {
    this._dataChanged(this.state.data);
  }

  componentDidUpdate() {
    this._dataChanged(this.state.data);
  }

  componentWillUnmount() {
    if (this.graph) this.graph.unmount();
  }
}

export default CMSingleBarElement;
