import * as d3 from 'd3';
import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';

// Reusable components in d3.
// Tutorial https://bost.ocks.org/mike/chart/

var refLine = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 500,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function getReferenceY(d) {
    if (!d._ref) {
      return 0;
    }
    var formattedVal = MRFormatter.format(d._ref);
    return config.yScale(formattedVal.value);
  }

  function chart(selection) {
    selection.each(function(data) {
      var line = d3
        .line()
        .x(config.x)
        //.y(config.y);
        .y(getReferenceY.bind(this));

      let refLineGroup = selection.select('.mrgraph-refline-grp');
      if (refLineGroup.empty()) refLineGroup = selection.append('g').attr('class', 'mrgraph-refline-grp');

      var refLine = refLineGroup.selectAll('.mrgraph-refline').data(data);

      refLine.exit().remove();

      refLine
        .enter()
        .append('path')
        .attr('class', 'mrgraph-refline')
        .merge(refLine)
        .attr('fill', 'none')
        .attr('stroke', config.colors['refline'])
        .attr('stroke-linejoin', 'round')
        .attr('stroke-linecap', 'round')
        .attr('stroke-width', toPx(0.05))
        .style('stroke-dasharray', '4,4')
        .attr('d', line)
        .attr('opacity', 0)
        .transition()
        .delay(200)
        .ease(d3.easeQuadInOut)
        .duration(600)
        .attr('opacity', 1);
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var line = function() {
  // One messy configurations
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    fontSize: 15,
    animate: true,
    duration: 500,
  };

  // draw the graph here
  function chart(selection) {
    selection.each(function(data) {
      var duration = config.animate ? config.duration : 0;

      // use d3 line
      var d3line = d3
        .line()
        .x(config.x)
        .y(config.y);

      if (config.curve) {
        d3line.curve(d3[config.curve]);
      }

      var lineGrp = selection.selectAll('.line-group').data([1]);
      lineGrp.exit().remove();

      lineGrp
        .enter()
        .append('g')
        .attr('class', 'line-group')
        .merge(lineGrp);

      // Draw Lines
      var allLines = selection
        .selectAll('.line-group')
        .selectAll('.linegraph-path')
        .data(data);
      allLines.exit().remove();

      // On Enter
      if (config.showLine) {
        allLines
          .enter()
          .append('path')
          .attr('class', 'linegraph-path')
          .attr('fill', 'none')
          .attr('stroke', function(d, i) {
            if (config.graphColors) {
              return (
                config.lineColor || MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length])
              );
            } else {
              return MRParseColors((d[0] && d[0].labelColor) || config.lineColor || 'color1');
            }
          })
          // .attr('stroke-width', (config.width / 120) * config.viewRatio)
          .attr('stroke-width', function(d, i) {
            return d.format === 'median' || d.format === 'average' ? 1.5 : 2;
          })
          .attr('stroke-linejoin', 'round')
          .attr('stroke-linecap', 'round')
          .attr('d', d3line)
          .attr('stroke-dasharray', function(d, i) {
            return d.format === 'median' || d.format === 'average'
              ? '3 6'
              : `${this.getTotalLength()} ${this.getTotalLength()}`;
          })
          .attr('stroke-dashoffset', function(d, i) {
            return this.getTotalLength();
          })
          .transition()
          .duration(function(d, i) {
            const interval = Math.min(this.getTotalLength() / 1000, 1);
            return duration * 1.5 + interval * duration;
          })
          .ease(d3.easeQuadOut)
          .delay(function(d, i) {
            const delay = 100;
            return i * delay;
          })
          .attr('stroke-dashoffset', 0);

        // On Update
        allLines
          .merge(allLines)
          .attr('stroke-dasharray', 0)
          .attr('stroke-dashoffset', 0)
          .transition()
          .delay(0)
          .duration(duration * 3)
          .ease(d3.easeElastic.amplitude(0.1).period(0.8))
          .attr('d', d3line);
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var circles = function() {
  // One messy configurations
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    fontSize: 15,
    animate: true,
    duration: 500,
  };

  // draw the graph here
  function chart(selection) {
    selection.each(function(data) {
      var duration = config.animate ? config.duration : 0;

      var circleGrps = selection.selectAll('.circle-group').data(data);
      circleGrps.exit().remove();
      if (config.showCircleOnLines) {
        circleGrps
          .enter()
          .append('g')
          .attr('class', 'circle-group')
          .merge(circleGrps);

        var circles = selection
          .selectAll('.circle-group')
          .selectAll('.linegraph-circle')
          .data(function(d, i) {
            d.groupIndex = i;
            return d;
          });

        circles.exit().remove();

        circles.join(
          enter =>
            enter
              .append('circle')
              .attr('class', 'linegraph-circle')
              .style('fill', '#ffffff')
              .style('stroke-width', '2')
              .style('stroke', function(d) {
                const x = config.graphColors
                  ? MRParseColors(config.graphColors[d.groupIndex % 6])
                  : MRParseColors(config.colors['default']);

                return config.circleColour ? config.circleColour : x;
              })
              .attr('opacity', 0)
              .attr('r', 0)
              .attr('cx', config.x)
              .attr('cy', function(d) {
                return config.yScale(parseFloat(d.value));
              })
              .call(enter =>
                enter
                  .transition()
                  .duration((d, i) => {
                    var interval = data[0] ? (duration * 3) / data[0].length : 0;
                    return (2 * i * interval) / 10 + duration;
                  })
                  .delay((d, i) => {
                    var interval = data[0] ? (duration * 2) / data[0].length : 0;
                    return (2 * i * interval) / data[0].length;
                  })
                  .attr('r', config.circleGroupRadius || config.toPx(0.3))
                  .attr('opacity', 1)
              ),
          update =>
            update
              .style('stroke', function(d) {
                const x = config.graphColors
                  ? MRParseColors(config.graphColors[d.groupIndex % 6])
                  : MRParseColors(config.colors['default']);
                return config.circleColour ? config.circleColour : x;
              })
              .attr('opacity', 1)
              .call(update =>
                update
                  .transition()
                  .ease(d3.easeElastic.amplitude(0.1).period(0.8))
                  .duration((d, i) => {
                    var interval = data[0] ? (duration * 2) / data[0].length : 0;
                    const j = i < data[0].length / 2 ? i : data[0].length - i - 1;
                    return (3 * j * interval) / 10 + duration * 3;
                  })
                  .delay(10)
                  .attr('r', config.circleGroupRadius || config.toPx(0.3))
                  .attr('opacity', 1)
                  .attr('cx', config.x)
                  .attr('cy', function(d) {
                    return config.yScale(parseFloat(d.value));
                  })
              ),
          exit => exit.attr('opacity', '1').call(exit => exit.attr('opacity', 0).remove())
        );
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var singleCircles = function() {
  // One messy configurations
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,

    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 500,
  };

  // draw the graph here
  function chart(selection) {
    selection.each(function(data) {
      var duration = config.animate ? config.duration : 0;

      var circleGrps = selection.selectAll('.circle-group').data(data);
      circleGrps.exit().remove();

      circleGrps
        .enter()
        .append('circle')
        .merge(circleGrps)
        .attr('cx', config.x)
        .attr('cy', config.height)
        .attr('r', config.r)
        .style('fill', function(d) {
          return d.color ? MRParseColors(d.color) : MRParseColors(config.colors['default']);
        })
        .transition()
        .delay(i => {
          return i * 10;
        })
        .ease(d3.easeQuadInOut)
        .duration(duration)
        .attr('cy', config.y);
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export { refLine, line, circles, singleCircles };
