import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';

const lollipop = function() {
  let config = {};
  function chart(selection) {
    selection.each((data, i) => {
      var svg = selection.selectAll('g');
      var duration = config.duration || 400;
      var minRadius = config.fontSize * 1.2;

      let columns = data.length;
      var graphHeight = config.height - config.fontSize * 3;
      let columnWidth = config.width / columns;
      var preparedData = [];
      data.forEach((d, i) => {
        let obj = {};
        obj.percentage = 'XX%';
        obj.label = d.label;
        obj.value = d.value;
        obj.color = config.graphColors[i];
        preparedData.push(obj);
      });

      const arr = (preparedData || []).map(function(d) {
        return parseInt(d.value) || 0;
      });
      var maxRadius = d3.max(arr);
      var radiusScale = d3
        .scaleLinear()
        .domain([0, maxRadius])
        .range([minRadius, (columns > 1 ? 1 : 0.6) * Math.min(columnWidth * 0.45, graphHeight * 0.45)]);

      var g = svg.data(preparedData);
      g.exit().remove();

      var gmerge = g
        .enter()
        .append('g')
        .call(function(parent) {
          // called only once when appended
          parent.append('line').attr('class', 'popline');

          parent
            .append('circle')
            .attr('class', 'circle-bg')
            .attr('cursor', 'pointer')
            .attr('cy', graphHeight)
            .attr('r', 0);

          parent.append('rect').attr('class', 'base'); //  the base rectangle

          parent.append('line').attr('class', 'side-lines-right'); //  the bars on the right of the rectange

          parent.append('line').attr('class', 'side-lines-left'); //  the bars on the left of the rectange

          // Center Text on the Circle
          parent
            .append('text')
            .attr('class', 'center-text')
            .attr('text-anchor', 'middle')
            .attr('opacity', 0);

          parent
            .append('text')
            .attr('text-anchor', 'middle')
            .attr('class', 'label-wrp')
            .attr('fill', '#fff')
            .attr('pointer-events', 'none')
            .attr('opacity', 0);

          parent
            .select('.label-wrp')
            .append('tspan')
            .attr('class', 'tspan1')
            .attr('font-weight', 400);

          parent
            .select('.label-wrp')
            .append('tspan')
            .attr('class', 'tspan2');
        })
        .merge(g);

      // Lolipop Line
      gmerge
        .select('.popline')
        .attr('x1', function(d, i) {
          return columnWidth * i + columnWidth / 2;
        })
        .attr('x2', function(d, i) {
          return columnWidth * i + columnWidth / 2;
        })
        .attr('y2', graphHeight)
        .attr('y1', graphHeight)
        .attr('stroke', config.lollipopLineColor || '#999')
        .attr('stroke-width', config.lollipopLineWidth || 1)
        .transition()
        .delay(0)
        .duration(duration)
        .attr('y2', function(d, i) {
          let ypos = radiusScale(maxRadius) - radiusScale(d.value);

          return (
            (graphHeight - (config.lollipopBaseHeight || config.fontSize)) * (1 - (config.lineHeightFactor || 0.5)) +
            ypos
          );
        });
      //Lolipop Cirlce
      gmerge
        .select('.circle-bg')
        .attr('cx', function(d, i) {
          return columnWidth * i + columnWidth / 2;
        })
        .attr('fill', function(d, i) {
          return MRParseColors(config.graphColors[i % config.graphColors.length]);
        })
        .transition()
        .delay(0)
        .duration(duration)

        .attr('cy', function(d, i) {
          let ypos = radiusScale(maxRadius) - radiusScale(d.value);
          return (
            (graphHeight - (config.lollipopBaseHeight || config.fontSize)) * (1 - (config.lineHeightFactor || 0.5)) +
            ypos
          );
        })
        .attr('r', function(d) {
          return radiusScale(d.value);
        });
      gmerge
        .select('.base')
        .attr('y', graphHeight)
        .attr('x', function(d, i) {
          return columnWidth * i;
        })
        .attr('fill', function(d, i) {
          return MRParseColors(config.graphColors[i % config.graphColors.length]);
        })
        .attr('height', config.lollipopBaseHeight || config.fontSize)
        .attr('width', columnWidth);

      gmerge
        .select('.side-lines-left')
        .attr('x1', function(d, i) {
          return columnWidth * i;
        })
        .attr('x2', function(d, i) {
          return columnWidth * i;
        })
        .attr('y1', graphHeight - (config.sideLinesHeight || 20))
        .attr('y2', function(d, i) {
          return graphHeight + (config.lollipopBaseHeight || config.fontSize);
        })
        .attr('stroke', config.sideLinesColor || '#aaa')
        .attr('stroke-width', config.sideLinesWidth || 1);

      gmerge
        .select('.side-lines-right')
        .attr('x1', function(d, i) {
          return columnWidth * i + columnWidth;
        })
        .attr('x2', function(d, i) {
          return columnWidth * i + columnWidth;
        })
        .attr('y1', graphHeight - (config.sideLinesHeight || 20))
        .attr('y2', function(d, i) {
          return graphHeight + (config.lollipopBaseHeight || config.fontSize);
        })
        .attr('stroke', config.sideLinesColor || '#aaa')
        .attr('stroke-width', config.sideLinesWidth || 1);

      gmerge
        .select('.center-text')
        .attr('transform', function(d, i) {
          return (
            'translate(' +
            (parseFloat(columnWidth * i) + parseFloat(columnWidth / 2)) +
            ',' +
            (graphHeight + config.fontSize * 2.4) +
            ')'
          );
        })
        .attr('font-size', config.fontSize)
        .text(function(d) {
          return d.label;
        })
        .attr('fill', config.labelColor || 'black')
        .transition()
        .delay(0)
        .duration(duration)
        .attr('opacity', 1);

      //for long  bar

      var textGroup = gmerge.select('.label-wrp').attr('x', function(d, i) {
        return columnWidth * i + columnWidth / 2;
      });

      textGroup
        .transition()
        .duration(duration)
        .attr('y', function(d, i) {
          let ypos = radiusScale(maxRadius) - radiusScale(d.value);
          return (
            (graphHeight - (config.lollipopBaseHeight || config.fontSize)) * (1 - (config.lineHeightFactor || 0.5)) +
            ypos +
            config.fontSize * 0.5
          );
        })
        .transition()
        .duration(config.animate ? 100 : 0)
        .attr('opacity', 1);

      textGroup
        .select('.tspan1')
        .text(function(d) {
          if ((radiusScale(d.value) * 2) / (config.fontSize * 1.5) > (d.value && ('' + d.value).length)) {
            return d.value;
          }
          return '';
        })
        .attr('font-size', function(d) {
          return config.fontSize * 1.5;
        });

      textGroup
        .select('.tspan2')
        .text(d => {
          let subText = config.subText || '';
          return radiusScale(d.value) > columnWidth / 3 ? subText : '';
        })
        .attr('x', function(d, i) {
          return columnWidth * i + columnWidth / 2;
        })
        .attr('dy', config.fontSize);
    });
    return selection;
  }

  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };
  return chart;
};

export default lollipop;
