/** @module graph-component-hoc
 *   @author Hemanth KS
 *   @description React Component wrapper for graphs
 */

import React from 'react';
import '../GraphLibrary/graph.css';
import Box from './widget-wrapper';
import dataReformator from './invertData';
import { debounce } from 'lodash';

let hocSelfRef = undefined;

function withGraphComponent(graph, graphSpecifications) {
  class GraphWrapper extends React.PureComponent {
    constructor(props) {
      super(props);
      hocSelfRef = this;
      this.graphRef = React.createRef();
      if (props.getRerenderFunction) {
        props.getRerenderFunction(this.reRender);
      }
      if (props.graphRef) {
        props.graphRef.current = {
          debouncedRedraw: debounce(this.reRender, 500),
          redraw: () => {
            this.reRender();
          },
        };
      }
    }

    render() {
      return (
        <div className={`cm-graph-container ${this.props.preferences.className} ${graphSpecifications.className}`}>
          <div className="cm-graph-wrp" ref={this.graphRef}>
            <div className={graphSpecifications.innerclassName}>
              <div id={`${graphSpecifications.id}`}></div>
            </div>
          </div>
        </div>
      );
    }

    reRender() {
      if (hocSelfRef.graph) hocSelfRef.graph.unmount(true);
      hocSelfRef._dataChanged(hocSelfRef.props.data);
    }

    _dataChanged(data, forceAnimate) {
      if (data) {
        const invertData = (this.props && this.props.preferences && this.props.preferences.invertData) || false;
        if (invertData) {
          data = dataReformator(data);
        }
        var animate = forceAnimate || (this.graph ? false : true);
        // initializing new graph from passed function
        this.graph = this.graph || new graph(this.graphRef.current);
        //  set Preferences and config for the graph
        this.graph.setPreferences(this.props.preferences || {});
        this.graph.setData(data);
        requestAnimationFrame(() => {
          this.graph.render(animate);
        });
      }
    }

    componentDidMount() {
      this._dataChanged(this.props.data);
    }
    componentWillUnmount() {
      if (this.graph) this.graph.unmount();
    }
  }

  return props => (
    <Box
      dataVisualizationComponent={GraphWrapper}
      data={props.data}
      preferences={props.preferences}
      getRerenderFunction={props.getRerenderFunction}
      graphRef={props.graphRef}
    />
  );
}

export default withGraphComponent;
