/*eslint-disable */
import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';

var multiplebars = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showValues: true,
  };

  // Creating a responsive scale

  // draw the graph here
  function chart(selection) {
    selection.each(function(data, i) {
      var maxLabelLength = d3.max(
        data[0].map(function(d) {
          return d.label.length;
        })
      );
      if (maxLabelLength > 10) {
        maxLabelLength = 11;
      }
      var fontSize;
      // Draw labels first. Calculate chart area , (transformX) based on its width.

      var labelGrp = selection.selectAll('.label-group').data(data.slice(0, 1));
      labelGrp.exit().remove();
      labelGrp
        .enter()
        .append('g')
        .attr('class', 'label-group')
        .merge(labelGrp)
        .attr('transform', 'translate(0,2)')
        .attr('font-size', config.labelFontSize || config.yAxisFontSize || config.fontSize); //applying custom label font

      var labels = selection
        .selectAll('.label-group')
        .selectAll('.mrgraph-bar-label')
        .data(function(d, i) {
          return d;
        });

      labels.exit().remove();

      if (config.width > 0) {
        // add labels
        labels
          .enter()
          .append('text')
          .attr('class', 'mrgraph-bar-label')
          .merge(labels)
          .text(function(d, i) {
            const maxLabelLength = config.maxLabelLength;
            if (config.showLabelGroup || config.showLabelGroup === undefined) {
              return d.label.length > maxLabelLength ? d.label.slice(0, maxLabelLength - 1) + '..' : d.label;
            } else {
              return '';
            }
          })
          .attr('font-size', function(d, i) {
            return config.labelFontSize || config.yAxisFontSize || config.fontSize;
          });
      }

      try {
        var transformX =
          selection
            .select('.label-group')
            .node()
            .getBBox().width +
          config.width / 30;
      } catch (e) {
        var transformX =
          selection
            .select('.label-group')
            .node()
            .getBoundingClientRect().width +
          config.width / 30;
      }
      // shadow

      var barGrp = selection.selectAll('.bar-group').data(data);
      barGrp.exit().remove();
      barGrp
        .enter()
        .append('g')
        .attr('class', 'bar-group')
        .merge(barGrp)
        .attr('transform', 'translate(' + transformX + ',' + 0 + ')');

      var valueGrp = selection.selectAll('.value-group').data(data);
      valueGrp.exit().remove();
      valueGrp
        .enter()
        .append('g')
        .attr('class', 'value-group')
        .merge(valueGrp)
        .attr('transform', 'translate(' + transformX + ',-1)')
        .attr('font-size', config.width / 25);

      var bars = selection
        .selectAll('.bar-group')
        .selectAll('.mrgraph-bar')
        .data(function(d, i) {
          d.map(function(entry) {
            entry.labelIndex = i;
          });
          return d;
        });

      var values = selection
        .selectAll('.value-group')
        .selectAll('.mrgraph-bar-value')
        .data(function(d, i) {
          return d;
        });

      values.exit().remove();

      var barheight = (config.yScale.bandwidth() / data.length) * 1;
      if (config.width > 0) {
        bars
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-bar')
          .attr('pointer-events', 'all')
          .attr('x', function(d, i) {
            if (d.value < 0) {
              return config.xScale(Math.min(0, d.value)) + Math.abs(config.xScale(d.value) - config.xScale(0));
            } else {
              return config.xScale(Math.min(0, d.value));
            }
          })
          .attr('width', 0)
          .merge(bars)

          .style('fill', function(d, i) {
            if (data.length > 1 || config.coloredBars) {
              return MRParseColors(d.labelColor || config.graphColors[d.labelIndex % config.graphColors.length]);
            } else {
              return MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length]);
            }
          })

          .attr('y', function(d, i) {
            let value = config.yScale(d.label) + d.labelIndex * barheight;

            return value;
          })
          .attr('height', barheight)

          .transition()

          .delay(function(d, i) {
            return ((config.duration * i) / data && data[0] && data[0].length) || 1;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)

          .attr('x', function(d, i) {
            return config.xScale(Math.min(0, d.value));
          })
          .attr('width', function(d, i) {
            return Math.abs(config.xScale(d.value) - config.xScale(0));
          })

          .attr('cursor', 'pointer');

        // add labels

        labels.exit().remove();

        selection
          .selectAll('.label-group')
          .selectAll('.mrgraph-bar-label')
          .attr('y', function(d, i) {
            return (
              config.yScale(d.label) +
              (config.bandWidth + (config.labelFontSize || config.yAxisFontSize || config.fontSize)) / 2.2
            );
          })
          .attr('x', function(d) {
            return 0;
          })
          .attr('opacity', 0)
          .transition()
          .delay(function(d, i) {
            return ((config.duration * i) / data && data[0] && data[0].length) || 1;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('opacity', 1)
          .attr('fill', '#777');

        // add text value
        values
          .enter()
          .append('text')

          .attr('class', 'mrgraph-bar-value')
          .merge(values)
          .text(function(d, i) {
            let barWidth = Math.abs(config.xScale(d.value) - config.xScale(0));

            if (config.showValues && barheight >= 10 && barWidth >= 22) {
              return d.value;
            } else if (barheight < 10 || !config.showValues || barWidth < 22) {
              return '';
            }
          })
          .attr('font-size', function(d, i) {
            fontSize = barheight > 10 ? Math.min(config.bandWidth * 0.7, config.fontSize) : 0;
            return config.valueFontSize || fontSize;
          })
          .attr('y', (d, i) => {
            let barheightcalc = config.barHeight ? config.barHeight : 0;

            let value = config.yScale(d.label) + d.labelIndex * barheight;

            return value + i * barheightcalc * data[0].length + barheight / 2 + config.fontSize / 2.2;
          })
          .attr('fill', function(d, i) {
            return '#fff';
          })
          .transition()
          .delay(function(d, i) {
            return ((config.duration * i) / data && data[0] && data[0].length) || 1;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('x', function(d, i) {
            let valueGroupPosition = config.valueGroupPosition ? config.valueGroupPosition : 0;

            return config.xScale(d.value) - 1.5 * fontSize * (d.value > 0 ? 1 : -1) + valueGroupPosition;
          });
      }
    });
    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default multiplebars;
