/** @module cm-bubble.js
 *   @author
 *   @description React Component wrapper for cm-bubble graph
 */

import React, { Component } from 'react';
import '../../GraphLibrary/graph.css';
import './cm-bubble.css';
import CMBubbleGraph from './cm-bubble.graph';

class CMBubble extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];

    this.state = {
      colors: props.colors || this.colors,
      data: props.data || {},
    };
    this.graphRef = React.createRef();
  }

  componentDidMount() {
    if (this.state.data) {
      this._dataChanged(this.state.data);
    }
  }

  componentDidUpdate() {
    if (this.props.redraw) {
      if (this.state.data) {
        this._dataChanged(this.state.data);
      }
    }
  }

  componentWillUnmount() {
    if (this.graph) this.graph.unmount();
  }

  static getDerivedStateFromProps(props, currentState) {
    const newState = currentState;
    if (newState.data !== props.data) {
      newState.data = props.data;
      return newState;
    }
    return null;
  }

  _dataChanged = data => {
    let animateIn;

    if (data) {
      animateIn = this.graph ? false : true;
      this.graph = this.graph || new CMBubbleGraph(this.graphRef.current);

      this.graph.setPreferences(this.props.preferences || {});

      this.graph.setData(data);
      this.graph.graphColors = this.state.colors;
      this.graph.widgetPreview = this.props.preferences.widgetPreview;
      requestAnimationFrame(() => {
        this.graph.render(animateIn);
      });
    }
  };

  draw(animate) {
    if (this.graph) {
      this.graph.render(animate);
    }
  }

  resize() {
    if (this.graph) {
      this.graph.render(false);
    }
  }

  render() {
    return (
      <div className={`cm-bubble cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" ref={this.graphRef} />
      </div>
    );
  }
}

export default CMBubble;
