import getGraphSpecificConfig from './Graphs';

function getGraphConfigurations() {
  const { preferences } = this || { preferences: {} };
  if (preferences.showLegends !== undefined) {
    this.overRideLegendDisplay(preferences.showLegends);
  }
  const specificConfig = getGraphSpecificConfig.call(this);

  const configProperties = {
    animate: true,
    duration: preferences.duration || 600,
    tooltipsEnabled: true,
    viewRatio: 1,
  };

  const config = {
    toPx: this.toPx,
    animate: preferences.animate || false,
    fontSize: preferences.fontSize || this.getFontSize(),
    valueGroupPosition: preferences.valueGroupPosition,
    barHeight: preferences.barHeight,
    gapBetweenGroups: preferences.gapBetweenGroups,
    showLabelGroup: preferences.showLabelGroup,
    image: preferences.image,
    invertData: preferences.invertData,
    negativeColor: preferences.negativeColor,
    paddingBetweenbars: preferences.paddingBetweenbars,
    packingCircle: preferences.packingCircle,
    labelFontSize: preferences.labelFontSize,
    labelColor: preferences.labelColor,
    valueFontSize: preferences.valueFontSize,
    valueColor: preferences.valueColor,
    labelColor: preferences.labelColor,
    yScalePaddingOuter: preferences.yScalePaddingOuter,
    themeColor: preferences.themeColor,
    colorRange: preferences.colorRange || 5,
    showLabelGroup: preferences.showLabelGroup,
    showValues: preferences.showValues,
    centerValueFontSize: preferences.centerValueFontSize,
    subtextFontSize: preferences.subtextFontSize,
    graphColors: preferences.graphColors || ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    centerValueMaxLength: preferences.centerValueMaxLength || 5,
    subTextMaxLength: preferences.subTextMaxLength || 10,
    legendsMaxLength: preferences.legendsMaxLength || 10,
    labelsMaxLength: preferences.labelsMaxLength || 10,
    legendBulletRadius: preferences.legendBulletRadius,
    legendFontSize: preferences.legendFontSize,
    cols: preferences.numberOfCols || this.findNumberOfCols(),
    opacity: preferences.opacity !== undefined ? preferences.opacity : 1,
    margin: {
      ...{
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
      },
      ...preferences.margin,
    },
    padding: {
      ...{
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
      },
      ...preferences.padding,
    },
    range: {},
    showLegends: this.displayLegends,
    // showLegends: preferences.showLegends !== undefined ? preferences.showLegends : true,
    offsetCenterX: preferences.offsetCenterX,
    offsetCenterY: preferences.offsetCenterY,

    // Number formatter library options
    notation: 'compact',
    valuePrefix: preferences.valuePrefix || '',
    valueSuffix: preferences.valueSuffix || '',
    prefix: preferences.prefix,
    prefixSeparator: preferences.prefixSeparator,
    suffixSeparator: preferences.suffixSeparator,
    suffix: preferences.suffix,
    xAxisDomainFactor: preferences.xAxisDomainFactor || 1,
    onClick: preferences.onClick,
    onLoad: preferences.onLoad,
    maxValue: preferences.maxValue,
    tooltipValueType: 'discrete', // ['discrete', 'continuous'],
    ToolTipVisualization: preferences.ToolTipVisualization,
    removeTooltipDefaultStyling: preferences.removeTooltipDefaultStyling,
    onMouseEnter: preferences.onMouseEnter,
    onMouseLeave: preferences.onMouseLeave,
    onMouseMove: preferences.onMouseMove,
    hideOdometer: preferences.hideOdometer,
    ...configProperties,
  };

  const result = { ...config, ...specificConfig };
  /*
  this.preferences are config parameters passed down from the react component.
  These are meant to override the graph config.
  Eg. if a graph instance always needs to hideAxisY it can be done using this.
  */

  this.padding = result.padding;
  this.margin = result.margin;

  /*
  Width and height are dependant on padding & margin.
  Graph dimensions need to be updated whenever padding/margin are changed.
  */
  this.setDimensions();

  const dimensions = {
    width: this.width,
    height: this.getGraphHeight(specificConfig),
    svgWidth: this.width,
    svgHeight: this.height,
    clientHeight: this.clientHeight,
    clientWidth: this.clientWidth,
  };
  return { ...result, ...dimensions };
}
export default getGraphConfigurations;
