import * as d3 from 'd3';
import MRParseColors from '../../mr-parsecolor';

import DropShadow from '../drop-shadow';
import Utils from '../utils';

const getGraphCenter = (config, i = 0, length) => {
  var centerX = config.width / 2;
  var centerY = config.height / 2;
  if (config.viewType === 'split-legends') centerX = config.width / 4;
  if (config.viewType === 'split') centerX = (config.width * (2 * i + 1)) / (2 * length);

  centerY = config.arcCenterY;
  return { centerX, centerY };
};

var splitDonutPie = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 500,
    labelCount: 3,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  function tweenRadius(r, t) {
    var dampingFactor = 3;
    var dampingFactor2 = 3;
    if (t < 0.25) {
      return r - (r * t) / dampingFactor;
    } else if (t < 0.5) {
      return r + (r * (t - 0.5)) / dampingFactor;
    } else if (t < 0.75) {
      return r + (r * (t - 0.5) * (t - 0.5)) / dampingFactor2;
    } else {
      return r + (r * (t - 1) * (t - 1)) / dampingFactor2;
    }
  }

  function chart(selection) {
    var duration = config.animate ? config.duration : 0;
    // calculate center position of the graph

    var { centerX, centerY } = getGraphCenter(config);

    selection.each(function(data) {
      if (!data) {
        data = [];
      }
      const length = data.length;
      data.forEach((element, i) => {
        var { centerX, centerY } = getGraphCenter(config, i, length);
        let data = [element];
        const defs = selection.selectAll('defs').data([1]);

        const uniqueId = `donutPie${Utils.makeid()}`;
        defs
          .enter()
          .append('defs')
          .append('clipPath')
          .attr('id', uniqueId)
          .append('path');
        defs.exit().remove();

        // shadow
        var dropShadow = DropShadow().config({});
        selection.datum([1]).call(dropShadow);

        // add background slice

        // add slicesGrp
        var slicesGrp = selection.selectAll('.slices').data([1]);
        slicesGrp.exit().remove();
        slicesGrp
          .enter()
          .append('g')
          .attr('class', 'slices' + i)
          .merge(slicesGrp)
          .attr('transform', 'translate(' + centerX + ',' + centerY + ')');

        // append all slices to slicesGrp
        var slices = selection
          .selectAll('.slices' + i)
          .selectAll('path.slice')
          .data(config.pie(data));

        // selection.selectAll('.slices' + i).style('clip-path', `url(#${uniqueId})`);

        // Remove extra slices
        slices.exit().remove();
        // Added slices
        slices
          .enter()
          .append('path')
          .merge(slices)
          .style('fill', function(d) {
            if (data.length > 1 || config.coloredBars) {
              return MRParseColors(d.data.labelColor || config.graphColors[i % config.graphColors.length]);
            } else {
              return MRParseColors(d.data.labelColor || config.graphColors[i % config.graphColors.length]);
            }
            // return MRParseColors(config.graphColors[i % 8]);
          })

          .attr('class', 'slice');

        // add loading animation / redraw animation
        requestAnimationFrame(() => {
          var slices = selection.selectAll('.slices' + i).selectAll('path.slice');
          slices
            .transition()
            .duration(function(d, i) {
              // loading animation duration
              let _duration = 0;
              if (config.animate === true) {
                _duration = (duration + i * 0.095 * duration) * 1.8;
                // redraw animation duration
              } else {
                _duration = duration * 1;
              }
              // animation for slices
              return _duration;
            })
            .attrTween('d', function(d, i) {
              const _current = this._current || d;
              // this._current = this._current || d;
              var interpolate = d3.interpolate(_current, d);
              this._current = interpolate(0);

              return function(t) {
                // loading animation
                if (config.animate === true) {
                  config.arc.outerRadius(function() {
                    return tweenRadius(config.radiusDefault, t);
                  });
                }
                // redraw animation
                return config.arc(interpolate(t));
              };
            });
        });
      });

      return selection;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);

    var pi = Math.PI;
    config.radius = config.radius ? config.radius : (Math.min(config.width, config.height) / 2) * 0.9;
    config.radiusDefault = config.radius;
    config.barWidth = config.radius * 0.28;
    config.innerRadius = config.radius - config.barWidth;
    config.arcAngle === 0 ? 45 : config.arcAngle;

    function deg2Rad(deg) {
      return (deg * Math.PI) / 180;
    }

    // Degrees to radians, (rotate by -90 deg)
    function calcAngle(degrees) {
      const totalAngle = 180 + config.arcAngle * 2;
      return (deg2Rad(degrees) * totalAngle) / 180 - (deg2Rad(90) * totalAngle) / 180;
    }

    config.pie = d3
      .pie()
      .sort(null)
      .startAngle(calcAngle(0))
      .endAngle(calcAngle(180))
      .value(function(d) {
        return parseFloat(d.value);
      });
    config.bgArc = d3
      .arc()
      .cornerRadius(Utils.cornerRadius(config.barWidth, config.roundedCorner))
      .startAngle(calcAngle(0))
      .endAngle(calcAngle(180))
      .outerRadius(config.radius)
      .innerRadius(config.innerRadius);
    config.arc = d3
      .arc()
      .outerRadius(config.radius)
      .innerRadius(config.innerRadius);

    return chart;
  };

  return chart;
};

export default splitDonutPie;
