/*
    CanvasSummary
    @responsibility :
      - Canvas Widget Summary Item
*/
import React from 'react';
import { MRParseColors } from '../../../GraphLibrary';
import { getFormattedNumber } from '../../../GraphLibrary/formatter';
import { toObject } from '../../../GraphLibrary/Number';

import './index.css';

const CanvasSummary = props => {
  const { data = {}, color = '#555', style = {} } = props;
  const _getColor = _color => MRParseColors(_color);

  const barStyle = { backgroundColor: _getColor(color), display: props.multiline ? 'none' : 'block' };
  // const _formattedVal = getFormattedNumber(parseFloat(data.value));
  // _formattedVal.value =
  //   _formattedVal.value % 1 === 0
  //     ? _formattedVal.value
  //     : _formattedVal.value
  //     ? _formattedVal.value.toString().split('.')[0]
  //     : _formattedVal.value;

  const num = {
    value: data.value,
    prefix: props.prefix,
    suffix: props.suffix,
  };

  const _formattedVal = toObject(num, props.options || {});
  const valueMultiline = { fontSize: props.multiline ? '1.5em' : '2em' };
  const subtextMultiline = { fontSize: props.multiline ? '0.8em' : '1em' };
  return (
    <div className="canvas-summary" style={style.root}>
      <div className="value-wrp" style={valueMultiline}>
        {_formattedVal.prefix ? <div className="prefix">{_formattedVal.prefix}</div> : ''}
        <div className="value">{_formattedVal.value == undefined ? 0 : _formattedVal.value}</div>
        {_formattedVal.suffix ? <div className="suffix">{_formattedVal.suffix}</div> : ''}
      </div>
      <div className="subtext" style={subtextMultiline}>
        {data.subtext}
      </div>
      <div className="line" style={barStyle} />
    </div>
  );
};

export default CanvasSummary;
