import React from 'react';
import ReactDom from 'react-dom';
import * as d3 from 'd3';

import DefaultTooltip from './defaultTooltip';

export default class ToolTip {
  constructor($ele) {
    this.draw($ele);
  }

  draw($wrp) {
    let $tooltip = document.getElementsByTagName('mr-tooltip')[0];

    if (!$tooltip) {
      $tooltip = document.createElement('mr-tooltip');
    }
    $tooltip.innerHTML = `
      <div class="inner">
        <div class="anim">
          <div class="content">
          </div>
        </div>
      </div>`;
    this.$tooltip = $tooltip;
    $tooltip.className = 'piktorsense-default-tooltip mr-tooltip';
    document.body.appendChild($tooltip);
  }

  setContent(data = {}, tooltipConfig = {}, counter = 1) {
    const ToolTipVissualization = tooltipConfig.ToolTipVisualization || DefaultTooltip;
    if (tooltipConfig.removeTooltipDefaultStyling) {
      this.$tooltip.className = 'mr-tooltip';
    }
    if (this.$tooltip.getElementsByClassName('content')[0]) {
      ReactDom.render(
        <ToolTipVissualization data={data} config={tooltipConfig} />,
        this.$tooltip.getElementsByClassName('content')[0]
      );
    } else {
      this.draw();
      setTimeout(() => {
        this.setContent(data);
        // eslint-disable-next-line no-plusplus
      }, 1000 * 0.5 * counter++);
    }
  }
  position(x, y) {
    if (this.$tooltip.show === false) {
      this.$tooltip.show = true;
    }
    this.$tooltip.classList.add('active');

    if (typeof this.$tooltip.position === 'function') {
      this.$tooltip.position(x, y);
    }
    this.$tooltip.style.transform = `translate(${x}px, ${y}px)`;
  }

  hide() {
    this.$tooltip.classList.remove('active');
  }
  getEventHandlers(tooltipConfig) {
    const mouseOver = d => {
      const config = tooltipConfig;
      if (!this.$tooltip) {
        return;
      }
      const data = config.dataExtractionMethod({ d, ...config });
      this.setContent(data, tooltipConfig);
      this._noContentForTooltip = false;
    };
    const onMouseMove = (d = {}) => {
      if (this._noContentForTooltip) return;
      if (this.$tooltip) {
        this.position(
          (d.event && d.event.clientX) || d3.event.clientX,
          (d.event && d.event.clientY) || d3.event.clientY
        );
      }
    };

    const onMouseOut = (d, i) => {
      if (this.$tooltip) this.hide();
    };
    return { mouseOver, onMouseMove, onMouseOut };
  }
}
