import * as d3 from 'd3';

const TEMPLATE = {
  slim: {
    sm: 'NA',
    md: 'NA',
    lg: 'NA',
  },
  square: {
    sm: 'no-legends',
  },
  rectangle: {
    sm: 'NA',
  },
  bar: {
    sm: 'NA',
    md: 'split-legends',
    lg: 'split-legends',
  },
  'long-bar': {
    sm: 'NA',
    md: 'NA',
    lg: 'split',
  },
};
const getViewType = (boxtype, boxsize) => {
  if (TEMPLATE[boxtype]) {
    if (TEMPLATE[boxtype][boxsize]) return TEMPLATE[boxtype][boxsize];
  }
  return 'default';
};

function TREEMAPGRAPH(config) {
  const { boxsize, boxtype } = config || {};

  // Import Graph Specific Config
  const xScale = d3
    .scaleLinear()
    .domain([0, this.width])
    .range([0, this.width]);
  const yScale = d3
    .scaleLinear()
    .domain([0, this.height])
    .range([0, this.height]);

  const fontSize = Math.max(Math.min(this.height, this.width, 500), 300) * 0.04;

  const tempConfig = {
    viewType: getViewType(boxtype, boxsize),
    xScale,
    yScale,
    duration: 300,
    percentage: true,
    fontSize,
  };

  if (this.graphCustomName === 'CMHeatMapGraph') {
    tempConfig.padding = {
      top: 2,
      bottom: 1,
      left: 2,
      right: 2.5,
    };
    tempConfig.margin = {
      left: 0,
      right: 0,
    };
  }
  return { ...tempConfig };
}

export default TREEMAPGRAPH;
