const showExtraNegativeTick = (yScale, config) => {
  /***
   * In case the domain minimun is too small then try to show additional -ve axis tick
   */
  const _domain = yScale.domain();
  yScale.nice();

  let reverse = false;
  let low = _domain[0];
  let high = _domain[1];

  if (_domain[0] > _domain[1]) {
    low = _domain[1];
    high = _domain[0];
    reverse = true;
  }
  const numOfTicks = Math.round((config.height / (config.fontSize * 3)) * (config.tickDensity || 0.5));
  const ticks = yScale.ticks();

  if (low < 0) {
    // const negativeTicks = [];
    // for (let i = 0; i < ticks.length; i += 1) {
    //   if (ticks[i] <= 0) negativeTicks.push(ticks[i]);
    // }

    // const positiveTicks = [];
    // for (let i = ticks.length - 1; i >= 0; i -= 1) {
    //   if (ticks[i] >= 0) positiveTicks.push(ticks[i]);
    // }

    // const skipFactor = Math.floor(ticks.length / numOfTicks);
    // const lowRef = Math.round(low / config.yAxisDomainFactor);
    // low = Math.min(low, negativeTicks[negativeTicks.length - 1], -(positiveTicks[skipFactor] || 0));

    const lastTick = ticks[ticks.length - 1] / Math.floor(ticks.length / numOfTicks);
    const minNegativeValue = -(high - low) * 0.2;

    if (low < lastTick) {
      for (let i = ticks.length - 1; i >= 0; i -= 1) {
        if (Math.abs(ticks[i]) > Math.abs(low)) {
          low = -Math.abs(ticks[i]);
          //  * Math.floor(ticks.length / numOfTicks);
          // high = ticks[0];
          break;
        }
      }
    }

    if (low > minNegativeValue) low += minNegativeValue;
    // else low = low * 1.1;

    const range = reverse ? [high, low] : [low, high];
    yScale.domain(range).nice();
  }

  return yScale;
};

export { showExtraNegativeTick };
