function camelToSnake(string) {
  return string
    .replace(/[\w]([A-Z])/g, function(m) {
      return m[0] + '-' + m[1];
    })
    .toLowerCase();
}

const format = (number, options = {}) => {
  // return new Intl.NumberFormat('en-IN', { ...options, compactDisplay: 'long' }).format(number);
  // const _options = { ...options };
  const locale = options.locale || 'en-IN';
  let scale = '';
  let scaleSeparator = '';
  const splitScale = options.notation === 'compact' && locale.startsWith('en-');

  let _options = Object.assign({}, options);
  if (splitScale) _options = Object.assign({}, _options, { compactDisplay: 'long' });

  const numberStr = new Intl.NumberFormat(locale, _options).format(number);

  const scaleAbrviation = {
    thousand: 'K',
    million: 'M',
    billion: 'B',
    trillion: 'T',
  };

  if (splitScale) {
    let parts = numberStr.split(' ');
    if (parts.length > 1) {
      scale = parts[parts.length - 1];
      parts = parts.slice(0, parts.length - 1);
    }
    const value = parts.join(' ');

    return {
      value: value,
      scaleSeparator: options.compactDisplay !== 'long' ? '' : ' ',
      scale: options.compactDisplay !== 'long' ? scaleAbrviation[scale] || scale : scale,
    };
  }

  return { value: numberStr, scaleSeparator, scale };
};

const toObject = (number = {}, options = {}) => {
  if (typeof number !== 'object') number = { value: number };

  const { prefix = '', prefixSeparator = '', suffix = '', suffixSeparator = '', value = '' } = number;
  const numberObj = format(value, options);

  return {
    value: numberObj.value,
    scale: numberObj.scale,
    scaleSeparator: numberObj.scaleSeparator,
    prefix,
    suffix,
    prefixSeparator: prefix ? prefixSeparator : '',
    suffixSeparator: suffix ? suffixSeparator : '',
  };
};

const toString = (number = {}, options = {}) => {
  const formattedNumber = toObject(number, options);

  const { prefix, suffix, value, scale, scaleSeparator, prefixSeparator, suffixSeparator } = formattedNumber;

  return `${prefix}${prefixSeparator}${value}${scaleSeparator}${scale}${suffixSeparator}${suffix}`;
};

export { camelToSnake, format, toObject, toString };
