import * as d3 from 'd3';
import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import { stackOffsetNone } from 'd3';

const roundedRectanglePath =
  'M100,100 h200 a20,20 0 0 1 20,20 v200 a20,20 0 0 1 -20,20 h-200 a20,20 0 0 1 -20,-20 v-200 a20,20 0 0 1 20,-20 z';

const getGraphCenter = config => {
  let widthFactor = 0;
  let heightFactor = 0;

  if (config.packingCircle) {
    widthFactor = 0.02;
    heightFactor = 0.02;
  }
  let centerX;
  let centerY;
  if (config.viewType === 'split-legends') {
    centerX = -config.width / 2;
    centerY = 10;
    // return { centerX, centerY };
  } else {
    centerX = config.width * widthFactor;
    centerY = config.height * heightFactor;
  }
  return { centerX, centerY, widthFactor, heightFactor };
};

var circlesPack = function() {
  var config = {
    width: 0,
    height: 0,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    animate: true,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return Math.max(config.width / 35, 15) * em;
  };

  // draw the graph here
  function chart(selection) {
    config.toPx = toPx;
    const { centerX, centerY, widthFactor, heightFactor } = getGraphCenter(config);
    var displayText = [];
    var duration = config.duration ? config.duration : 400;

    try {
      selection.each(function(data, i) {
        data.forEach((item, i) => (item.index = i));
        var dropShadow = DropShadow().config({});

        let alignmentRef = selection.select('.alignment-ref');

        if (alignmentRef.empty() || config.packingCircle) {
          alignmentRef = selection.append('g').attr('class', 'alignment-ref');
          alignmentRef.append('circle').attr('fill', '#efefef');
        }

        var nodeWrp = selection.selectAll('.node-wrp').data([1]);
        nodeWrp.exit().remove();
        nodeWrp
          .enter()
          .append('g')
          .attr('class', 'node-wrp')
          .attr('transform', `translate( ${centerX / 2},${centerY})`);

        nodeWrp.merge(nodeWrp);

        var nodeWrp = selection.selectAll('.node-wrp');

        selection.datum([1]).call(dropShadow);

        if (config.packingCircle) {
          const r = Math.min(config.width * 0.97, config.height * 0.97) / 2;
          alignmentRef
            .select('rect')
            .attr('height', config.height)
            .attr('width', config.width);
          alignmentRef
            .select('circle')
            .attr('r', r)
            .attr('cx', config.width / 2)
            .attr('cy', config.height / 2);
        }

        var pack = d3
          .pack({ children: data })
          .size([config.width * (1 - 2 * widthFactor), config.height * (1 - 2 * heightFactor)]);

        var t = d3
          .transition()
          .duration(duration * 2.5)
          .ease(d3.easeSinInOut);

        // hierarchy
        var h = d3.hierarchy({ children: data }).sum(function(d) {
          if (!d.children) return Math.floor(d.value);
        });

        const topArr = [];
        const btmArr = [];

        const leaves = pack(h)
          .leaves()
          .filter(d => !!d.parent);

        leaves.forEach(n => {
          topArr.push(n.y - n.r);
          btmArr.push(n.y + n.r);
        });

        const topBound = d3.min(topArr);
        const btmBound = d3.max(btmArr);

        const topOffset = topBound - (topBound + (config.height - btmBound)) / 2;

        //JOIN
        var nodes = nodeWrp.selectAll('.bubble-node').data(leaves);

        var circle = nodeWrp.selectAll('circle').data(leaves);

        //EXIT
        nodes.exit().remove();
        circle
          .exit()
          .style('fill', 'none')
          .attr('r', 1e-6)
          .remove();

        //ENTER
        var node = nodes
          .enter()
          .filter(function(d) {
            return !d.children;
          })
          .append('g')
          .attr('class', function(d) {
            return 'bubble-node';
          })
          .attr('transform', function(d) {
            return 'translate(' + d.x + ',' + d.y + ')';
          });

        node
          .append('circle')
          .attr('r', 1e-6)
          .style('fill', function(d, i) {
            return 'none';
          })
          .attr('pointer-events', 'all')
          .attr('cursor', 'pointer')
          .transition(t)
          .duration(duration / 2)
          .ease(circle.empty() ? d3.easeBackOut : d3.easeBackOut)
          .attrTween('d', function(d) {
            return interpolateSVGSegment(d.x, d.y, d.r, 0, 360);
          })
          .style('fill', function(d, i) {
            return MRParseColors(config.graphColors[i % config.graphColors.length]);
          })
          .attr('r', function(d) {
            return d.r * (config.radiusFactor || 0.92);
          });

        var text = node.append('g').attr('class', 'group-circle-text');

        text
          .append('text')
          .attr('dy', '-0.4em')
          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style(
            'font-size',
            config.labelFont ||
              function(d, i) {
                displayText[i] = getFontSize(d.data.label, 2 * d.r);
                return displayText[i];
              }
          )
          .text(function(dd, i) {
            var labelStr = '';
            if ('enableTextTrim' in config) {
              labelStr = config.enableTextTrim ? getTrimmedLabel(dd.data.label, dd.r * 2) : dd.data.label;
            } else {
              labelStr = getTrimmedLabel(dd.data.label, dd.r * 2);
            }
            return labelStr;
          })
          .transition()
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        //Translating the whole the circle-text-group only when deviation value is available.
        node.selectAll('.group-circle-text').attr('transform', (d, i) => {
          if (d.data && d.data.subLabel && d.r >= 55) {
            return `translate(${0},${-displayText[i] * 1})`;
          }
        });

        text
          .append('text')
          .attr('dy', '1.0em')
          .text(function(d, i) {
            let text = `${MRFormatter.format(d.value, 'toNumber').value}`;
            return (config.valuePrefix || '') + text + (config.valueSuffix || '') + (config.overrideUnit || '');
          })
          .style(
            'font-size',
            config.valueFont ||
              function(d, i) {
                return displayText[i];
              }
          )
          .style('background', 'white')
          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style('font-weight', '400')
          .transition(t)
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        //Code for GSEM Presentation design
        if (config.subLabel) {
          var subLevelText = text
            .append('rect')
            .attr('class', 'scenario-box')
            .attr('dy', '2em')
            .attr('width', function(d, i) {
              return displayText[i] * 5;
            })
            .attr('height', function(d, i) {
              return displayText[i] * 1.15;
            })
            .attr('rx', (d, i) => displayText[i] * 0.6) // set the x corner curve radius
            .attr('ry', (d, i) => displayText[i] * 0.8)
            .attr('fill', 'white')
            .attr('transform', function(d, i) {
              return `translate( ${displayText[i] * -2.5},${displayText[i] * 1.5})`;
            })
            .attr('display', function(d, i) {
              if (d.r < 55) {
                return 'none';
              }
            });

          text
            .append('path')
            .attr('class', 'arrow-head')
            .attr('d', 'M0,-5L10,0L0,5')
            .attr('transform', function(d, i) {
              if (d.data && d.data.sign) {
                if (d.data.sign === '+') {
                  return `translate( ${displayText[i] * -1.6},${displayText[i] * 2.45}) rotate (-90)`;
                } else if (d.data.sign === '-') {
                  return `translate( ${displayText[i] * -1.7},${displayText[i] * 1.75}) rotate (90)`;
                }
              }
            })
            .attr('fill', function(d, i) {
              if (d.data && d.data.sign) {
                if (d.data.sign === '+') {
                  return '#009900';
                } else if (d.data.sign === '-') {
                  return '#FF6F6F';
                }
              }
            })
            .attr('display', function(d, i) {
              if (d.r < 55) {
                return 'none';
              }
            });

          text
            .append('text')
            .attr('dy', '3em')
            .attr('dx', (d, i) => displayText[i] * 0.5)
            .attr('text-anchor', 'middle')
            .text(function(d, i) {
              if (d.data && d.data.subLabel) {
                let text = `${MRFormatter.format(d.data.subLabel, 'toNumber').value}`;
                return (config.valuePrefix || '') + text + (config.valueSuffix || '') + (config.overrideUnit || '');
              }
              return '';
            })
            .style(
              'font-size',
              config.valueFont ||
                function(d, i) {
                  return displayText[i] * 0.8;
                }
            )
            .style('fill', (d, i) => {
              if (d.data && d.data.sign) {
                if (d.data.sign === '+') {
                  return '#009900';
                } else if (d.data.sign === '-') {
                  return '#FF6F6F';
                }
              }
            })
            .style('font-weight', '500')
            .attr('display', function(d, i) {
              if (d.r < 55) {
                return 'none';
              }
            })
            .transition(t)
            .duration((duration * 2) / 3)
            .ease(d3.easeSin)
            .attr('opacity', '1');
        }

        function animate() {
          var labeltext = selection.selectAll('.bubble-node');
          labeltext.each(function(d) {
            if (d.r <= config.toPx(1.5)) {
              d3.select(this)
                .select('.group-circle-text')
                .style('opacity', '0');
            } else {
              if (d.r > config.toPx(1.5)) {
                d3.select(this)
                  .select('.group-circle-text')
                  .style('opacity', '1');
              }
            }
          });
        }

        setTimeout(() => {
          animate();
        }, 10);
      });
    } catch (e) {}

    return selection;
  }

  function numberWithCommas(x) {
    var parts = x.toString().split('.');
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    return parts.join('.');
  }

  function generateSVGSegment(x, y, r, startAngle, endAngle) {
    startAngle *= Math.PI / 180;
    endAngle *= Math.PI / 180;

    var largeArc = endAngle - startAngle <= Math.PI ? 0 : 1;
    var sweepFlag = 1;
    return [
      'M',
      x,
      y,
      'L',
      x + Math.sin(startAngle) * r,
      y - Math.cos(startAngle) * r,
      'A',
      r,
      r,
      0,
      largeArc,
      sweepFlag,
      x + Math.sin(endAngle) * r,
      y - Math.cos(endAngle) * r,
      'Z',
    ].join(' ');
  }

  function getTrimmedLabel(labelStr, maxWidth) {
    const fontSize = getFontSize(labelStr, maxWidth);
    let maxLen = config.maxFontSize || Number.POSITIVE_INFINITY;
    if (fontSize * labelStr.length > maxWidth) {
      maxLen = Math.floor(maxWidth / fontSize);
    }

    return maxLen < labelStr.length ? labelStr.substring(0, maxLen) + '..' : labelStr;
  }

  function getFontSize(text, maxWidth) {
    const scaledFontSize = maxWidth / 10;
    const MAX_FONTSIZE = config.maxFontSize || config.fontSize;
    const MIN_FONTSIZE = config.minFontSize || config.fontSize * 0.85;

    return Math.min(Math.max(scaledFontSize, MIN_FONTSIZE), MAX_FONTSIZE);
  }

  function interpolateSVGSegment(x, y, r, startAngle, endAngle) {
    return function(t) {
      return generateSVGSegment(x, y, r, startAngle, startAngle + (endAngle - startAngle) * t);
    };
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};
export default circlesPack;
