import * as d3 from 'd3';
// import arrow from './arrow.svg';

const setBrush = function setBrush(d3brush, brushGroup, coords) {
  d3brush.move(brushGroup, coords);
};

const getAnimationDuration = (e, config) => {
  let duration = config.brushSelection == null ? 0 : config.duration * 0.0005;

  if (e && e.type === 'mousemove') {
    duration = 0;
  }

  /*
  brushSelection = config.brushSelection || config.defaultSelection;

  let duration = 0;

  if (!e) {
    // No animation on load
    duration = 500;
  } else {
    const { sourceEvent } = e;
    if (e.type === 'end' || e.type === 'click') {
      // For triggered event
      duration = 600;
    }

    if (sourceEvent && sourceEvent.type === 'click') {
      // For click event
      duration = 600;
    }
  }
  */

  return duration;
};

const brush = function brush() {
  // Default Config
  let config = {
    id: 'brush-mask',
    snapToGrid: true,
  };

  // draw the graph here
  function chart(selection) {
    let brushSelection = [];
    // eslint-disable-next-line no-unused-vars
    selection.each((data, i) => {
      selection.selectAll('.brush-group').remove(); // Need to remove this, as event handlers do not seem to update

      brushSelection = config.brushSelection || config.defaultSelection;

      config.arrowW = config.fontSize * 1.1;
      config.arrowH = config.fontSize * 0.8;

      const brushGroup = selection
        .selectAll('.brush-group')
        .data([1])
        .enter()
        .append('g')
        .attr('class', 'brush-group');

      const d3brush = d3
        .brushX()
        .extent([[config.x1, config.y1], [config.x2, config.y2]])
        .handleSize(3);

      const setBrushCoords = (left, right, e) => {
        const areaMask = brushGroup.select(`#${config.id}`);
        const duration = getAnimationDuration(e, config);
        const { prevCoords = [config.x1, config.x2] } = config;

        if (areaMask.empty()) {
          brushGroup
            .append('clipPath')
            .attr('id', config.id)
            .append('rect')
            .attr('x', prevCoords[0])
            .attr('width', prevCoords[1] - prevCoords[0])
            .attr('y', config.y1)
            .attr('height', config.y2);
        }

        brushGroup
          .select('clipPath')
          .select('rect')
          .attr('y', config.y1)
          .attr('height', config.y2)
          .attr('x', function fn() {
            return d3.select(this).attr('x') === null ? left || config.x1 : d3.select(this).attr('x');
          })
          .transition()
          .duration(duration)
          .ease(d3.easeCubicInOut)
          // .duration(0)
          .attr('x', left || config.x1)
          .attr('width', right - left);

        const dataPoints = {
          update: false,
        };

        const range = config.xScale.range();
        const rangePoints = d3.range(range[0], range[1] + config.xScale.step() / 2, config.xScale.step());

        const reachedEnd = rangePoints[rangePoints.length - 1] === right;

        if (e && (e.type === 'end' || e.type === 'click' || e.type === 'mouseup' || reachedEnd)) {
          dataPoints.update = true;
          const ids = [rangePoints.indexOf(left), rangePoints.indexOf(right)];
          dataPoints.ids = ids;
          dataPoints.start = data[0][ids[0]].label;
          dataPoints.end = data[0][ids[1]].label;
        }

        if (config.onUpdate) config.onUpdate(dataPoints.ids || brushSelection, dataPoints);
      };

      const brushHandle = (g, coords) => {
        const { prevCoords = [] } = config;
        const duration = getAnimationDuration(coords && coords[2], config);
        g.selectAll('.handle--w')
          .data([{ type: 'w' }])
          .attr('display', coords === null ? 'none' : null)
          .attr('fill', '#455eee12')
          .attr('x', (d, j) => {
            const x1 = prevCoords[0] == null ? coords[0] : prevCoords[0];
            return x1;
          })
          .attr('width', (d, j) => {
            return coords[1] - coords[0];
          })
          .transition()
          .duration(duration)
          .ease(d3.easeCubicInOut)
          .attr('x', coords[0]);

        g.selectAll('.handle--custom')
          .data([{ type: 'w' }, { type: 'e' }])
          .attr('display', coords === null ? 'none' : null)
          // .attr('transform', (d, j) => {
          //   const x1 = prevCoords[j] == null ? coords[j] : prevCoords[j];
          //   return `translate(${x1},${config.y2})`;
          // })
          .attr('x', (d, j) => {
            const x1 = prevCoords[j] == null ? coords[j] : prevCoords[j];
            return x1 - config.arrowW;
          })
          .transition()
          .duration(duration)
          .ease(d3.easeCubicInOut)
          // .attr('transform', (d, j) => (coords === null ? null : `translate(${coords[j]},${config.y2})`));
          .attr('x', (d, j) => (coords === null ? null : coords[j] - config.arrowW));

        selection
          .select(`.${config.calloutLineClass}`)
          .attr('transform', () => {
            const x1 = prevCoords[1] == null ? coords[1] : prevCoords[1];
            return `translate(${x1}, 0)`;
          })
          .transition()
          .ease(d3.easeCubicInOut)
          .duration(duration)
          .attr('transform', () => `translate(${coords[1]}, 0)`);
      };

      const onBrushEnd = function onBrushEnd() {
        if (config.snapToGrid && d3.event.selection != null) {
          const x1 = d3.event.selection[0];
          const x2 = d3.event.selection[1];

          let _x1;
          let _x2;

          const range = config.xScale.range();
          const rangePoints = d3.range(range[0], range[1] + config.xScale.step() / 2, config.xScale.step());

          const halfStep = config.xScale.step() / 2; // Half of distance between 2 points on xAxis
          const _selectedIds = {};

          // can be optimized
          for (let j = 0; j < rangePoints.length; j += 1) {
            const _xPos = rangePoints[j] + halfStep;

            // Snap Selection to xAxis values
            if (_x1 === undefined && x1 < _xPos) _x1 = rangePoints[j];
            if (_x2 === undefined && x2 < _xPos) _x2 = rangePoints[j];
          }

          // Set default Selection to entire graph area
          if (_x1 === undefined) {
            _x1 = rangePoints[0];
          }
          if (_x2 === undefined) {
            _x2 = rangePoints[rangePoints.length - 1];
          }

          // Set minimum selection to atleast 1 'BandWidth' wide
          if (_x1 === _x2) {
            const minRange = (config.minRange != null ? config.minRange : 1) * halfStep * 2;
            if (_x2 !== rangePoints[rangePoints.length - 1]) {
              _x2 += minRange;
            } else {
              _x1 -= minRange;
            }
          }

          // Redraw brush in case calcualted values do not match initial values
          if (x1 !== _x1 || x2 !== _x2) {
            _selectedIds.left = rangePoints.indexOf(_x1);
            _selectedIds.right = rangePoints.indexOf(_x2);

            if (_selectedIds.left !== -1 && _selectedIds.right !== -1) {
              brushSelection[0] = _selectedIds.left;
              brushSelection[1] = _selectedIds.right;
            }

            const { sourceEvent } = d3.event;
            const coords = [_x1, _x2];

            // // Set active class on brushGroup container for any css animation
            // brushGroup.attr('class', 'brush-group active');

            // // Animate / move clipPath
            setBrushCoords(coords[0], coords[1], sourceEvent);
            // d3.select(this).call(brushHandle, [...coords, sourceEvent]);

            // // Update coords in parent component to persist state
            // config.prevCoords = coords;
            // if (config.onUpdateCoords) config.onUpdateCoords(coords);

            // setTimeout(() => {
            //   setBrush(d3brush, brushGroup, [_x1, _x2]);
            // }, 0);
          } else {
            brushGroup.attr('class', 'brush-group ');
          }
        }
      };

      const onBrush = function onBrush() {
        const { selection: coords, sourceEvent } = d3.event;
        // For hover
        if (coords != null) {
          brushGroup.attr('class', 'brush-group active');
          setBrushCoords(coords[0], coords[1], sourceEvent);
          d3.select(this).call(brushHandle, [...coords, sourceEvent]);

          if (sourceEvent && sourceEvent.type === 'mousemove') {
            config.prevCoords = coords;
          }

          if (config.onUpdateCoords) config.onUpdateCoords(coords);
        } else {
          brushGroup.attr('class', 'brush-group ');
        }
      };

      const setBrushStyles = function setBrushStyles(ele) {
        if (ele) {
          const w = config.arrowW;
          const h = config.arrowH;

          ele.select('.overlay').style('pointer-events', 'none');
          ele
            .select('.selection')
            .style('pointer-events', 'none')
            .attr('stroke', 'transparent')
            .attr('fill', 'transparent')
            .style('height', h * 0.4)
            // .style('y', config.y2 - h * 0.4);
            .style('y', config.y2);

          // const path = `M 0,${-h * 0} ${w * 0.5},${h} ${w * -0.5},${h} z`;

          ele
            .selectAll('.handle--custom')
            .data([{ type: 'w' }, { type: 'e' }])
            .enter()
            // .append('path')
            // .attr('stroke', '#fff')
            // .attr('fill', '#21264e')
            // .attr('stroke-linejoin', 'round')
            // .attr('stroke-linecap', 'round')
            // .attr('cursor', 'ew-resize')
            // .attr('stroke-width', h * 0.2)
            // // .style('opacity', 0)
            // .attr('transform', d => `translate(${d.type === 'w' ? config.x1 : config.x2},${config.y2})`)
            // .attr('d', path);
            .append('image')
            .attr('class', d => `handle--custom handle--custom--${d.type}`)
            .attr('x', d => (d.type === 'w' ? config.x1 : config.x2) - w)
            .attr('y', config.y2)
            .attr('width', w * 2)
            .attr('height', h * 2);
          // .attr('xlink:href', arrow);
          //
          // <image x="10" y="20" width="80" height="80" xlink:href="recursion.svg" />
        }
      };

      brushGroup.call(d3brush);
      setBrushStyles(brushGroup);

      d3brush.on('brush', onBrush).on('end', onBrushEnd);

      if (brushSelection && brushSelection.length) {
        const range = config.xScale.range();
        const rangePoints = d3.range(range[0], range[1] + config.xScale.step() / 2, config.xScale.step());

        if (config.calloutPosition) {
          setBrush(d3brush, brushGroup, config.calloutPosition);
        } else {
          setBrush(d3brush, brushGroup, [rangePoints[brushSelection[0]], rangePoints[brushSelection[1]]]);
        }
      }

      return true;
    });

    return selection;
  }

  // Getters and Setters for config
  chart.config = function conf(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default brush;
