import * as d3 from 'd3';
import { TooltipText, Value } from './tooltipText';

// (function(){

class MRSimpleTooltip {
  constructor($wrp) {
    return;
    this.draw($wrp);
  }

  draw($wrp) {
    return;
    // requestAnimationFrame(() => {
    let $tooltip = document.getElementsByTagName('mr-tooltip')[0];

    if (!$tooltip) {
      $tooltip = document.createElement('mr-tooltip');
    }
    this.$tooltip = $tooltip;
    $tooltip.className = 'mr-tooltip';
    ($wrp || document.body).appendChild($tooltip);
    // });
  }

  setContent(title, body) {
    return;
    if (title === undefined) {
      this.$tooltip.setAttribute('hide-hdr', '');
    } else {
      this.$tooltip.removeAttribute('hide-hdr', '');
    }

    this.$tooltip.innerHTML = `
        <div class="inner">
          <div class="anim">
            <div class="hdr">${title}</div>
            <div class="content">${body}</div>
          </div>
        </div>
        `;
  }

  position(x, y) {
    return;
    if (this.$tooltip.show === false) {
      this.$tooltip.show = true;
    }
    this.$tooltip.classList.add('active');

    if (typeof this.$tooltip.position === 'function') {
      this.$tooltip.position(x, y);
    }
    this.$tooltip.style.transform = `translate(${x}px, ${y}px)`;
  }

  hide() {
    return;
    this.$tooltip.classList.remove('active');
  }
}

// Mixin
const GraphTooltip = superClass =>
  class extends superClass {
    // GraphTooltip{

    constructor($ele) {
      super($ele);
      return;
      this.tooltipsEnabled = true;
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;

      let root = document.getElementById('root');
      if (root == null) {
        root = document.body;
      }

      let tooltipWrp = document.getElementsByTagName('mr-tooltip-wrp')[0];
      if (tooltipWrp == null) {
        tooltipWrp = document.createElement('mr-tooltip-wrp');
      }
      this.$tooltipWrapper = tooltipWrp;
      tooltipWrp.inherit = $ele.host || $ele;
      requestAnimationFrame(() => {
        root.appendChild(tooltipWrp);
      });

      this.labels = [];
    }

    setLabels(labels, graphColors) {
      this.labels = labels;
      this.graphColors =
        graphColors.length > 0 ? graphColors : ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
    }

    onMouseOver(d) {
      return;
      const graphColors = this.graphColors || ['color4'];
      if (!this.tooltip) {
        this.tooltip = new MRSimpleTooltip(this.$tooltipWrapper);
      }
      this._noContentForTooltip = false;
      if (d._tooltip) {
        this.tooltip.setContent(d._tooltip.title || `${d.label} : ${d.value}`, d._tooltip.message);
      } else if (d.value || d.labelText) {
        const tooltipText = TooltipText.bind(this)(d);
        this.tooltip.setContent(undefined, tooltipText);
      } else if (this.labels.length > 0) {
        const values = Value(d, this.labels, graphColors);

        this.tooltip.setContent(undefined, values);
      } else {
        this._noContentForTooltip = true;
        return;
      }
      const { clientX } = d3.event;
      this.tooltip.position(clientX, d3.event.clientY);
    }

    onMouseMove(d) {
      return;
      if (this._noContentForTooltip) return;
      if (this.tooltip) {
        this.tooltip.position(d3.event.clientX, d3.event.clientY);
      }
    }

    onMouseOut(d, i) {
      return;
      if (this.tooltip) this.tooltip.hide();
    }

    unmount() {
      return;
      this.onMouseOut();
    }
  };

export default GraphTooltip;

export { MRSimpleTooltip };
