import { yBreakPoint } from '../utils';

const TEMPLATE_HORIZONTAL_BAR = {
  CMButterflyGraph: {
    slim: {
      xs: 'NA',
      sm: 'A',
      md: 'A',
      lg: 'A',
    },
    'tall-rectangle': {
      xs: 'NA',
      sm: 'A',
    },
    square: {
      sm: 'A',
    },
    rectangle: {
      xs: 'NA',
      sm: 'A',
      md: 'A',
      lg: 'A',
      fs: 'A',
    },
    bar: {
      sm: 'A',
      md: 'A',
      lg: 'A',
      fs: 'A',
    },
    'long-bar': {
      xs: 'A',
      sm: 'A',
      md: 'A',
      fs: 'A',
    },
  },
  CMBarGraph: {
    slim: {
      xs: 'NA',
      sm: 'NA',
      md: 'NA',
      lg: 'NA',
    },
    'tall-rectangle': {
      xs: 'NA',
      sm: 'NA',
      md: 'no-labels',
    },
    square: {
      xs: 'NA',
      sm: 'NA',
    },
    rectangle: {
      xs: 'NA',
      sm: 'NA',
      md: 'no-labels',
    },
    bar: {
      xs: 'NA',
      sm: 'no-labels',
      md: 'no-labels',
    },
    'long-bar': {
      xs: 'no-legends',
      md: 'no-legends',
      lg: 'no-labels',
      fs: 'no-labels',
    },
  },
  CMStackedBarGraph: {
    slim: {
      xs: 'NA',
      sm: 'NA',
      md: 'NA',
      lg: 'NA',
    },
    'tall-rectangle': {
      xs: 'NA',
      sm: 'NA',
      md: 'no-labels',
    },
    square: {
      xs: 'NA',
      sm: 'NA',
    },
    rectangle: {
      xs: 'NA',
      sm: 'NA',
      md: 'no-labels',
    },
    bar: {
      xs: 'NA',
      sm: 'no-labels',
      md: 'no-labels',
    },
    'long-bar': {
      xs: 'no-legends',
      md: 'no-legends',
      lg: 'no-labels',
      fs: 'no-labels',
    },
  },
};

const getViewType = (boxtype, boxsize, graphName) => {
  if (TEMPLATE_HORIZONTAL_BAR[graphName] && TEMPLATE_HORIZONTAL_BAR[graphName][boxtype]) {
    if (TEMPLATE_HORIZONTAL_BAR[graphName][boxtype][boxsize])
      return TEMPLATE_HORIZONTAL_BAR[graphName][boxtype][boxsize];
  }
  return 'default';
};

function HORIZONTALBARGRAPHS(config) {
  //get graph preferences
  const preferences = this.preferences || {};

  // Import Graph Specific Config

  let showLabels = true;

  const { boxsize, boxtype, boxHeight } = config || {};

  const graphName = this.graphCustomName;
  const graphHeight = this.getGraphHeight();

  let tempConfig = {
    viewType: getViewType(boxtype, boxsize, graphName),
    graphHeight,
    tooltipsEnabled: true,
    yAxisType: 'text',
    isClipPath: true,
    showValueGroup: preferences.showValueGroup !== undefined ? preferences.showValueGroup : true,
    showlabelGroup: preferences.showlabelGroup !== undefined ? preferences.showlabelGroup : true,
    showLabels: preferences.showLabels !== undefined ? preferences.showLabels : true,
  };

  // Setting conditons for graph responsiveness for single stacked bar.
  if (yBreakPoint[boxHeight] <= yBreakPoint.xs) {
    showLabels = false;
  }

  // Setting the config based on graph , single stacked bar has margin already taken care.
  if (graphName !== 'CMSingleBarElement') {
    const margin = {
      margin: {
        top: this.getFontSize() * 0.09,
        right: this.getFontSize() * 0.2,
        bottom: this.getFontSize() * 0.1,
        left: this.getFontSize() * 0.2,
      },
    };

    tempConfig = { ...tempConfig, ...margin };
  } else {
    const subConfig = {
      displayLegends: preferences.showLegends !== undefined ? preferences.showLegends : true,
      labelFontSize: preferences.labelFontSize,
      mainBarMouseEnter: true,
      wrapText: true,
      tooltipsEnabled: true,
      svgHeight: this.height,
      viewRatio: 0.9,
      hideLegendValue: preferences.hideLegendValue !== undefined ? preferences.hideLegendValue : true,
    };

    const baseBarMouseEvents = {
      baseBarMouseEnter: false,
      baseBaronMouseMove: false,
      baseBaronMouseLeave: false,
    };

    tempConfig = { ...tempConfig, ...subConfig, ...baseBarMouseEvents };
  }
  return tempConfig;
}

export default HORIZONTALBARGRAPHS;
