import * as d3 from 'd3';
import MRFormatter from '../../mr-formatter';
import MRParseColors from '../../mr-parsecolor';
import getGraphCenter from './get-graph-center';
import { setText } from '../../Number';

var splitGraphValue = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    showTrend: true,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    config.centerValueFontSize = config.centerValueFontSize || Math.min(config.width / 10, config.height / 10);
    config.subtextFontSize = config.subtextFontSize || Math.min(config.width / 20, config.height / 15);

    selection.each(function(data) {
      if (!data || !data.data || !data.data.length) {
        data.data = [];
      }

      const length = data.data.length;
      if (config.viewType !== 'split') {
        data.data = [
          {
            label: data.summary.subtext,
            value: data.summary.value,
          },
        ];
      }

      data.data.forEach((element, i) => {
        var { centerX, centerY } = getGraphCenter(config, i, length);
        var summary = { ...data.summary, value: element.value, subtext: element.label };
        let graphValFontsize_1;
        graphValFontsize_1 = (2 * config.radius) / 6;

        const fontsPadding = Math.min(config.radius / 20, 5);
        // add graph value group
        var graphValGrp = selection.selectAll('.graph-val-grp' + i).data([1]);
        graphValGrp.exit().remove();
        if (!config.hideCenterValue) {
          graphValGrp
            .enter()
            .append('g')
            .attr('class', 'graph-val-grp' + i)
            .merge(graphValGrp)
            .attr(
              'transform',
              `translate(${centerX + (config.moveCenterXByFactor || 0) * config.width},${centerY +
                (config.moveCenterYByFactor || 0) * config.height})`
            );

          // add graph value
          var graphVal = selection
            .selectAll('.graph-val-grp' + i)
            .selectAll('.graph-val')
            .data([0]);
          graphVal.exit().remove();
          let graphValNode = graphVal
            .enter()
            .append('text')
            .attr('text-anchor', 'middle')
            .attr('class', 'graph-val')
            .merge(graphVal)
            .attr('transform', function() {
              var transY = summary.subtext ? -1 * Math.min(10, config.centerValueFontSize / 2) : 0;
              return 'translate(0,' + transY + ')';
            })
            .attr('font-size', config.centerValueFontSize)
            .attr('fill', '#424146')
            .attr('font-weight', '400')
            .attr('opacity', 1);

          const num = {
            value: summary.value,
            prefix: summary.prefix || config.valuePrefix,
            prefixSeparator: summary.prefixSeparator || config.prefixSeparator,
            suffixSeparator: summary.suffixSeparator || config.suffixSeparator,
            suffix: summary.numberUnit || summary.unit || config.valueSuffix,
          };

          const options = {
            ...config,
            maximumFractionDigits: config.maximumFractionDigits != null ? config.maximumFractionDigits : 1,
          };
          setText(graphValNode, num, options);

          if (summary.subtext && summary.accentColor) {
            graphVal
              .enter()
              .append('line')
              .attr('class', 'graph-val-accent')
              .attr('stroke', MRParseColors(summary.accentColor))
              .attr('stroke-width', 2)
              .attr('x1', -config.fontSize * 0.8)
              .attr('x2', config.fontSize * 0.8)
              .attr('y1', fontsPadding + 0.75 * graphValFontsize_1)
              .attr('y2', fontsPadding + 0.75 * graphValFontsize_1);
          }

          // add graph label
          var graphLabel = selection
            .selectAll('.graph-val-grp' + i)
            .selectAll('.graph-label')
            .data([0]);
          graphLabel.exit().remove();
          graphLabel
            .enter()
            .append('text')
            .attr('text-anchor', 'middle')
            .attr('class', 'graph-val')
            .merge(graphLabel)
            .attr('transform', `translate(0,${config.subtextFontSize})`)
            .text(function(d) {
              const maxLen = config.subTextMaxLength;
              return (
                (summary.subtext &&
                  summary.subtext.substring(0, maxLen) + (summary.subtext.length > maxLen ? '..' : '')) ||
                ''
              );
            })
            .attr('font-size', config.subtextFontSize)
            .attr('opacity', 1)
            .attr('font-weight', 300);
        } else {
          graphValGrp.remove();
        }
      });
      return selection;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    config.baseFontSize = Math.max((config.width / 40) * config.viewRatio, 9);
    config.graphValMaxWidth = config.graphValMaxWidth ? config.graphValMaxWidth : config.baseFontSize * 6;
    return chart;
  };

  return chart;
};

export default splitGraphValue;
