/** @module cm-line.js
 *   @author
 *   @description React Component wrapper for cm-line graph
 */

import React, { Component } from 'react';

import './index.css';

class CMNumbers extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
  }

  render() {
    let { summary, title, data } = this.props.data;
    const graphData = data && data.length > 0 ? data[data.length - 1] : {};
    return (
      <div className={`cm-line cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" style={{ display: 'flex' }}>
          <div className="text-box-wrapper ">
            <div className="text-box-content">
              <label className="graph-value">{graphData.value}</label>
              {/* <span className={`graph-trend ${summary.trend === 'up' ? 'trend-up' : 'trend-down'}`} /> */}
              <div className="graph-subText">{graphData.label}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CMNumbers;
