/* eslint-disable no-var */
import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';

function butterflyElements() {
  var config = {
    x1: undefined,
    x2: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showLabels: true,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;
    selection.each(data => {
      // var maxLabelLength = d3.max(data[0].map(d => d.label.length));
      var baseBarGrp = selection.selectAll('.base-bar-group').data([data[0]]);
      var barGrpRight = selection.selectAll('.bar-group-right').data([data[1]]);
      var barGrpLeft = selection.selectAll('.bar-group-left').data([data[0]]);
      var dropShadow = DropShadow().config({});
      var transformX = 0;

      // shadow
      selection.datum([1]).call(dropShadow);

      baseBarGrp.exit().remove();
      baseBarGrp
        .enter()
        .append('g')
        .attr('class', 'base-bar-group')
        .merge(baseBarGrp)
        .attr('transform', 'translate(' + transformX + ',0)');

      barGrpLeft.exit().remove();
      barGrpLeft
        .enter()
        .append('g')
        .attr('class', 'bar-group-left butterfly-group')
        .merge(barGrpLeft)
        .attr('transform', 'translate(' + transformX + ',0)');

      barGrpRight.exit().remove();
      barGrpRight
        .enter()
        .append('g')
        .attr('class', 'bar-group-right butterfly-group')
        .merge(barGrpRight)
        .attr('transform', 'translate(' + config.width / 2 + ',0)');

      var baseBars = selection
        .selectAll('.base-bar-group')
        .selectAll('.mrgraph-base-bar')
        .data(function(d, i) {
          d.map(function(entry) {
            entry.labelIndex = i;
          });
          return d;
        });

      baseBars.exit().remove();
      if (config.width > 0) {
        var barWidthArray = [];
        baseBars
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-base-bar')
          .merge(baseBars)
          .attr('pointer-events', 'all')
          .style('fill', '#f5f5f5')
          .attr('cursor', 'pointer')
          .attr('x', 0)
          .attr('y', config.y)
          .attr('height', config.bandWidth)
          .attr('width', config.width);

        var barsLeft = selection
          .selectAll('.bar-group-left')
          .selectAll('.mrgraph-bar')
          .data(function(d, i) {
            d.map(function(entry) {
              entry.labelIndex = i;
            });
            return d;
          });
        try {
          barsLeft.exit().remove();
        } catch (e) {
          // bars.exit().remove();
        }
        var barsRight = selection
          .selectAll('.bar-group-right')
          .selectAll('.mrgraph-bar')
          .data(function(d, i) {
            d.map(function(entry) {
              entry.labelIndex = i;
            });
            return d;
          });
        try {
          barsRight.exit().remove();
        } catch (e) {
          // bars.exit().remove();
        }
      }

      var delay = 500 / data[0].length;

      selection.selectAll('.label-group').remove();

      if (config.width > 0) {
        var barWidthArray = [];
        if (config.showLabels) {
          var labelGrp = selection.selectAll('.label-group').data(data.slice(0, 1));
          labelGrp.exit().remove();
          labelGrp
            .enter()
            .append('g')
            .attr('class', 'label-group')
            .merge(labelGrp)
            .attr('font-size', config.width / 25);

          var labels = selection
            .selectAll('.label-group')
            .selectAll('.mrgraph-bar-label')
            .data(function(d, i) {
              return d;
            });

          labels.exit().remove();

          labels
            .enter()
            .append('text')
            .attr('class', 'mrgraph-bar-label')
            .merge(labels)
            .text(function(d, i) {
              const fontSize = config.labelFontSize || config.fontSize;
              if (config.bandWidth + fontSize * 0.9 > fontSize * 0.7) {
                let barWidthLeft = [];
                let barWidthRight = [];

                barWidthLeft = config.width - config.xScale1(parseFloat(data[0][i].value)) - config.width / 2;
                barWidthRight = config.width - config.xScale2(parseFloat(data[1][i].value));

                const minLabels = Math.min(barWidthLeft, barWidthRight) - fontSize;
                const spliceFactor = (minLabels / fontSize) * 3;
                return d.label.length > spliceFactor ? d.label.slice(0, spliceFactor) + '..' : d.label;
              }
              return '';
            })
            .attr('font-size', function(d, i) {
              if (config.labelFontSize) {
                return config.labelFontSize;
              } else {
                let { fontSize, bandWidth } = config;
                fontSize = parseFloat(fontSize);
                if (fontSize < 0.9 * bandWidth) return fontSize * 1.1;
                return 0;
              }
            });
        }

        barsLeft
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-bar')
          .attr('pointer-events', 'all')
          .attr('x', function(d) {
            return config.width / 2;
          })
          .attr('width', 0)
          .merge(barsLeft)
          .style('fill', function(d, i) {
            if (data.length > 1) {
              return MRParseColors(config.graphColors[0]);
            } else {
              return MRParseColors(config.graphColors[0]);
            }
          })
          .attr('y', config.y)
          .attr('height', config.bandWidth)
          .transition()
          .delay(function(d, i) {
            return 50 * i;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('x', function(d, i) {
            var barWidth = config.xScale1(parseFloat(d.value));
            barWidthArray.push(barWidth);
            return barWidth;
          })
          .attr('width', function(d, i) {
            var barWidth = config.width - config.xScale1(parseFloat(d.value)) - config.width / 2;
            barWidthArray.push(barWidth);
            return barWidth;
          })
          .attr('cursor', 'pointer');

        barsRight
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-bar')
          .attr('pointer-events', 'all')
          .attr('x', function(d) {
            return 0;
          })
          .attr('width', 0)
          .merge(barsRight)
          .style('fill', function(d, i) {
            if (data.length > 1) {
              return MRParseColors(config.graphColors[1]);
            } else {
              return MRParseColors(config.graphColors[1]);
            }
          })
          .attr('y', config.y)
          .attr('height', config.bandWidth)
          .transition()
          .delay(function(d, i) {
            return 50 * i;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('width', function(d, i) {
            var barWidth = config.width - config.xScale2(parseFloat(d.value));
            barWidthArray.push(barWidth);
            return barWidth;
          })
          .attr('cursor', 'pointer');

        if (config.showLabels) {
          labels
            .enter()
            .selectAll('text')
            .attr('y', function(d, i) {
              var transform =
                (config.bandWidth +
                  d3
                    .select(this)
                    .node()
                    .getBBox().height) /
                2;
              return config.yScale(d.label) + transform - toPx(0.15);
            })
            .attr('x', function(d, i) {
              var transform =
                (config.width -
                  d3
                    .select(this)
                    .node()
                    .getBBox().width) /
                2;

              return transform;
            })
            .attr('opacity', 0)
            .style('fill', '#fff')
            .style('font-weight', '400')
            .transition()
            .delay(function(d, i) {
              return 50 * i;
            })
            .ease(d3.easeQuad)
            .duration(config.duration)
            .attr('opacity', 1);
        }

        var isStacked = selection.selectAll('.bar-group-left')._groups[0].length > 1;

        var maxWidth = config.width / 7;
      }
    });
    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
}

export default butterflyElements;
