/** @module NumberToSVG
 *   @author
 *   @description Tranform number to SVG Text as per user-preferences
 */

import { toObject } from './utils';

const setText = (numberNode, number = {}, options = {}) => {
  const formattedNumber = toObject(number, options);

  const { prefix, suffix, value, scale, scaleSeparator, prefixSeparator, suffixSeparator } = formattedNumber;

  // const { className = 'number' } = options;

  const className = options.className ? `${options.className} ${number}` : `number`;

  const classNames = {
    prefix: `${className}-prefix`,
    value: `${className}-value`,
    scale: `${className}-scale`,
    suffix: `${className}-suffix`,
  };

  const $nodes = {};

  Object.keys(classNames).forEach(item => {
    let $node = numberNode.select(`.${classNames[item]}`);
    if ($node.empty()) $node = numberNode.append('tspan').attr('class', classNames[item]);

    $nodes[item] = $node;
  });

  $nodes.prefix.text(`${prefix}${prefixSeparator}`);
  $nodes.value.text(`${value}`);
  $nodes.scale.attr('font-size', '0.8em').text(`${scaleSeparator}${scale}`);
  $nodes.suffix.attr('font-size', '0.8em').text(`${suffixSeparator}${suffix}`);

  return numberNode;
};

export default setText;
