import * as d3 from 'd3';
import DropShadow from './drop-shadow';

const chloroPleth = function() {
  let config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color4', 'color3', 'color1', 'color2', 'color5', 'color6'],
    showLabels: false,
  };

  // draw the graph
  function chart(selection) {
    const duration = config.animate ? config.duration : 0;
    const { countries, path, projection, getColor, indexedData, valueFontSize, labelColor } = config;

    selection.each((data, i) => {
      if (selection.select('.mapGroup').empty()) {
        selection.append('g').attr('class', 'mapGroup');
      }
      if (selection.select('.label-group').empty()) {
        selection.append('g').attr('class', 'label-group');
      }

      const paths = selection
        .select('.mapGroup')
        .selectAll('.countries')
        .data(countries);
      paths.exit().remove();

      const labels = selection
        .select('.label-group')
        .selectAll('text')
        .data(countries);
      labels.exit().remove();

      paths
        .enter()
        .append('path')
        .attr('class', 'country')
        .attr('d', path)
        .style('stroke', '#fff')
        .style('stroke-width', 1)
        .style('fill', function(d) {
          const { name } = d.properties;
          const data = indexedData[name];
          if (data) {
            const color = getColor(data.value);
            console.log(color);
            return color;
          }
          return '#E5E5E5';
        })
        .style('opacity', 0.8);

      if (config.showLabels) {
        labels
          .enter()
          .append('text')
          .style('font-size', valueFontSize || '0.5em')
          .style('font-weight', '500')
          .style('fill', labelColor)
          .style('stroke', 'none')
          .attr('x', function(d) {
            return path.centroid(d)[0];
          })
          .attr('y', function(d) {
            return path.centroid(d)[1];
          })
          .attr('text-anchor', 'middle')
          // .attr('dy', '0.35em')
          .text(function(d) {
            let stateArea = path.area(d);
            const { name } = d.properties;
            const data = indexedData[name];
            if (data && data.code) {
              const { code, label } = data;
              if (stateArea <= 106) {
                return '';
              } else {
                return code;
              }
            }
          });
      }
    });

    return selection;
  }

  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default chloroPleth;
