/** @module cm-canvas-summary.js
 *   @author
 *   @description React Component wrapper for cm-canvas-summary graph
 */

import React, { Component } from 'react';
import '../../GraphLibrary/graph.css';
import CanvasSummary from './CanvasSummary';

import './index.css';

class CMCanvasSummary extends Component {
  constructor(props) {
    super(props);

    this.colors = ['color1', 'color3', 'color2', 'color4', 'color5', 'color6'];

    this.state = {
      colors: props.colors || this.colors,
      data: props.data || {},
    };
    this.graphRef = React.createRef();
  }

  render() {
    let { data = [] } = this.props.data;
    let { summary } = this.props.data;
    let colorArray = [];
    data = data.slice(0, 3);
    this.state.data.summary.widgetName === 'Priority Incidents'
      ? (colorArray = ['color4', 'color2', 'color1'])
      : (colorArray = ['color4', 'color2', 'color3']);

    const colors = this.props.colors || colorArray;
    const maxWidth = data ? `${(100 / data.length) * 1.1}%` : '';

    const _getColor = (_colors, index) => {
      if (_colors && _colors.length) {
        return _colors[index];
      }
      return 'color4';
    };

    data.forEach(element => {
      element.subtext = element.label;
    });

    let unit = undefined;
    if (this.props.data.hasOwnProperty('summary') && this.props.data.summary.hasOwnProperty('unit')) {
      unit = this.props.data.summary.unit;
    }

    const { preferences = {} } = this.props;
    const number = {
      prefix: summary.valuePrefix || preferences.valuePrefix,
      suffix: summary.unit || summary.valueSuffix || preferences.valueSuffix,
    };
    const options = { ...preferences };
    const style = {};
    if (preferences.fontSize) {
      style.fontSize = preferences.fontSize;
      if (!((preferences.fontSize || '').includes('px') || (preferences.fontSize || '').includes('em'))) {
        style.fontSize += 'px';
      }
    }

    return (
      <div style={style} className={`cm-canvas-summary cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" ref={this.graphRef}>
          {data.map((item, index) => (
            // eslint-disable-next-line react/no-array-index-key
            <CanvasSummary
              prefix={number.prefix}
              suffix={number.suffix}
              options={options}
              view="l1"
              key={`${index}-item.label`}
              style={{ maxWidth }}
              data={item}
              color={_getColor(colors, index)}
            />
          ))}
        </div>
      </div>
    );
  }

  static getDerivedStateFromProps(props, current_state) {
    if (current_state.data !== props.data) {
      current_state.data = props.data;
      return current_state;
    }
    return null;
  }
}

export default CMCanvasSummary;
