import * as d3 from 'd3';
import { CatameraGraph, MRParseColors } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import ToolTip from '../../tooltip';
import withGraphComponent from '../../Graph-wrapper-HOC';
import './cm-area.css';
let toolTipClass;
class CMAreaGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    toolTipClass = new ToolTip($ele);
  }

  setData(data) {
    this.graphCustomName = 'CMAreaGraph';
    const self = this;

    this.data = data.data ? data.data : data;
    const normalize = this.preferences.normalize || false;
    if (normalize && this.data.length) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      this.data = this.normalizeData(this.data);
    }
    this.summary = data.summary ? data.summary : '';

    this.labels = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (var i = 0; i < self.labels.length; i++) {
        if (!d[self.labels[i].value]) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });

    var formattedData = [];
    var item = {};

    // process data object with multiple values
    for (var k = 0; k < this.labels.length; k++) {
      var items = [];
      for (var i = 0; i < this.filteredData.length; i++) {
        item = {
          label: this.filteredData[i].label,
          value: this.filteredData[i][this.labels[k].value],
          labelText: this.labels[k].label,
          _formatted: this.filteredData[i][this.labels[k].value],
        };
        items.push(item);
      }
      if (this.summary && this.summary.median) {
        items.median = this.summary.median;
      }
      if (this.summary && this.summary.unit) {
        items.unit = this.summary.unit;
      }
      formattedData.push(items);
    }

    this.dataType = data.labels && data.labels.length > 1 ? '2D' : '1D';
    if (data.labels && data.labels.length === 1) {
      const labelObj = data.labels[0];
      if (labelObj.label === 'label' && labelObj.value === 'value') {
        this.dataType = '1D';
      }
    }
    this.graphData = formattedData;
    return formattedData;
  }

  overRideLegendDisplay = (showLegends = false) => {
    if (this.labels.length > 1) {
      //stacked area - multiseries data -showLegends:true/false
      this.displayLegends = this.preferences.showLegends;
    } else {
      //area -series data -showLegends:false
      this.displayLegends = false;
    }
  };

  render(animate = true) {
    const mode = '';
    const self = this;

    super.render();
    if (!this.filteredData || !this.width || !this.height) return;

    let config = getGraphConfigurations.call(this);

    // View Not Supported
    if (config.viewType === 'NA') {
      this.$baseGrp.selectAll('*').remove();
      return;
    }

    //get yDomain
    var maxVal = 0;
    var minVal = 0;
    var sumPositive = {};
    var sumNegative = {};
    var inverseSum = {};
    var inverseSumNegative = {};
    this.graphData.map(series => {
      series.map(function(d, k) {
        if (d.value >= 0) {
          // positive scale calculation
          var formattedVal = {};
          formattedVal.value = parseFloat(d.value);
          inverseSum[k] = inverseSum[k] || 0;
          d._formatted = formattedVal;
          d._prevValue = inverseSum[k];
          inverseSum[k] += formattedVal.value;
        } else {
          // negative scale calculation
          var formattedVal = {};
          formattedVal.value = parseFloat(d.value);
          inverseSumNegative[k] = inverseSumNegative[k] || 0;
          d._formatted = formattedVal;
          d._prevValue = inverseSumNegative[k];
          inverseSumNegative[k] += formattedVal.value;
        }
      });
    });
    if (config.isStacked) {
      this.graphData.map((d, i) => {
        this.graphData[i].map(function(d, k) {
          if (d.value >= 0) {
            // positive scale calculation
            var formattedVal = {};
            formattedVal.value = parseFloat(d.value);
            sumPositive[k] = sumPositive[k] || 0;
            d._formatted = formattedVal;
            d._prevValue = sumPositive[k];
            inverseSum[k] -= formattedVal.value;
            d._prevInverse = inverseSum[k];
            sumPositive[k] += formattedVal.value;
            return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value;
          } else {
            // negative scale calculation
            var formattedVal = {};
            formattedVal.value = parseFloat(d.value);
            sumNegative[k] = sumNegative[k] || 0;
            d._formatted = formattedVal;
            d._prevValue = sumNegative[k];
            inverseSumNegative[k] -= formattedVal.value;
            d._prevInverse = inverseSumNegative[k];
            sumNegative[k] += formattedVal.value;
            return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value;
          }
        });
      });

      minVal = d3.min(Object.values(sumNegative)) || 0;
      maxVal = d3.max(Object.values(sumPositive));
    } else {
      maxVal = d3.max(this.graphData, (d, i) => {
        return d3.max(this.graphData[i], function(d) {
          var formattedVal = {};
          formattedVal.value = parseFloat(d.value);
          d._formatted = formattedVal;
          d._prevValue = 0;
          return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value;
        });
      });
      minVal = d3.min(this.graphData, (d, i) => {
        return d3.min(this.graphData[i], function(d) {
          var formattedVal = {};
          formattedVal.value = parseFloat(d.value);
          d._formatted = formattedVal;
          d._prevValue = 0;
          return formattedVal.minVal ? formattedVal.minVal : formattedVal.value;
        });
      });
    }

    minVal = Math.min(Math.ceil(minVal * config.yAxisDomainFactor), this.yDomainMin[1]);
    maxVal = Math.max(Math.ceil(maxVal * config.yAxisDomainFactor), this.yDomainMin[1]);

    if (config.medianArea && config.medianValue) {
      maxVal = Math.max(maxVal, parseFloat(config.medianValue));
      minVal = Math.min(minVal, parseFloat(config.medianValue));
    }

    self.colorMap = d3.map();

    if (config.graphColors) {
      this.graphColors = config.graphColors;
    }

    const graphColors = config.medianArea ? this.graphColors.filter((d, i) => i < 3) : this.graphColors;
    const highlightColors = config.medianArea ? this.graphColors.filter((d, i) => i >= 3) : this.graphColors;

    for (let i = 0; i < self.graphData.length; i++) {
      this.graphData[i].color = graphColors[i % graphColors.length];
      this.graphData[i].highlightColor = highlightColors[i % highlightColors.length];
      try {
        if (this.graphData[i][0].labelText !== undefined)
          self.colorMap.set(self.graphColors[i], this.graphData[i][0].labelText);
      } catch (err) {
        console.error(err);
      }
    }

    const { range } = config;
    const yAxisWidth = this.getYAxisWidth(maxVal, config);
    const graphLeftSpacing = parseFloat(range.left) + parseFloat(yAxisWidth);

    // SetScale
    var xScale = d3
      .scalePoint()
      .range([graphLeftSpacing, config.width - range.right])
      .domain(
        this.graphData.map(function(data, i) {
          return data.map(function(d, i) {
            return d.label;
          });
        })[0]
      );

    // Configuration to adjust the range of max value
    maxVal *= config.maxRangeMultiplier;
    minVal *= config.minRangeMultiplier;
    var yScale = d3
      .scaleLinear()
      .range([config.height, range.top])
      .domain([minVal, maxVal]);

    config = {
      ...config,
      //axis configuration
      xScale: xScale,
      yScale: yScale,
      range,
      yAxisWidth,

      //area curve configuration
      x: (d, i) => {
        try {
          return xScale(d.label);
        } catch (error) {
          return console.error(error);
        }
        //
      },
      y: (d, i) => {
        return yScale(d._formatted.value);
      },
      y0: (d, i) => {
        if (!config.isStacked) return yScale(0);
        return yScale(d._prevInverse);
      },
      y1: (d, i) => {
        if (!config.isStacked) return yScale(d._formatted.value);
        return yScale(d._prevInverse + d._formatted.value);
      },
    };

    // x-axis
    var xAxis = CatameraGraph.xAxis(mode).config(config);
    this.$baseGrp.datum(this.filteredData).call(xAxis);

    // y-axis
    var yAxis = CatameraGraph.yAxis(mode).config(config);
    this.$baseGrp.datum(this.filteredData).call(yAxis);

    //areas
    var area = CatameraGraph.area().config(config);
    this.$baseGrp.datum(this.graphData).call(area);

    this.$baseGrp.select('.legend-group').remove();

    // custom legends group

    var legendGroup = CatameraGraph.legendGroup().config(config);
    if (this.labels.length > 1) {
      const legends = this.labels;
      this.$baseGrp.datum(legends).call(legendGroup);
    } else {
      this.$baseGrp.datum([]).call(legendGroup);
    }

    if (config.showTooltip) {
      setTimeout(() => {
        // mouseEvent();
      }, 100);

      function mouseEvent() {
        self.$baseGrp
          .select('.area-group')
          .select('.mouse-over-effects')
          .remove();
        var mouseG = self.$baseGrp
          .select('.area-group')
          .append('g')
          .attr('class', 'mouse-over-effects');

        //Line code on hover
        mouseG
          .append('path')
          .attr('class', 'mouse-line')
          .style('stroke', 'rgb(0, 0, 0)')
          .style('opacity', '1');

        var mousePerLine = mouseG
          .selectAll('.mouse-per-line')
          .data(self.graphData)
          .enter()
          .append('g')
          .attr('class', 'mouse-per-line');

        // line Circles
        mousePerLine
          .append('circle')
          .attr('r', config.tooltipCircleRadii || Math.round(1.5 * (config.highlightedStrokeWidthSize || 2 / 2)))
          .style('stroke', 'gray')
          .style('fill', function(d, i) {
            return MRParseColors(d.color || '#fff');
          })
          .style('stroke-width', config.tooltipCircleRadiiStrokeWidth || '0.4')
          .style('opacity', '0')
          .attr('filter', '#url(drop-shadow)');

        mousePerLine.append('text').style('opacity', '0');

        const series1 = self.graphData[0];
        var leftBound = xScale(series1[0].label) + xScale.bandwidth() / 2;
        var rightBound = xScale(series1[series1.length - 1].label) + xScale.bandwidth() / 2;

        var rect = mouseG
          .append('rect')
          .attr('x', leftBound)
          .attr('width', rightBound - leftBound)
          .attr('height', config.height)
          .attr('fill', 'none')
          .attr('cursor', 'pointer')
          .attr('pointer-events', 'all');

        ////  Handling custom mouse events ////

        const dataExtractionMethod = requiredParameters => {
          const { d } = requiredParameters;
          let temp = {};

          if (requiredParameters.data && requiredParameters.data.length > 1) {
            const value = [];
            if (d && d.labelText) {
              if (requiredParameters.labels) {
                requiredParameters.labels.forEach(label => {
                  if (d[label.value]) {
                    const temp2 = label.label;
                    value.push({ label: temp2, value: d[label.value] });
                  }
                });
              }

              temp = { label: d.labelText, value };
            }
          } else {
            if (d) {
              const { label, value, labelText } = d;
              temp = { label: labelText, value };
              if (label) {
                temp.subLabel = label;
              }
            }
          }

          return temp;
        };
        const eventHandlers = toolTipClass.getEventHandlers({
          dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
          labels: self.labels,
          data: self.graphData,
          ...config,
        });

        rect
          .on('click', config.onClick && config.onClick.bind(self))
          .on('mouseenter', config.onMouseEnter && config.onMouseEnter.bind(self))
          .on(
            'mousemove',
            (config.onMouseMove && config.onMouseMove.bind(self)) ||
              function(d, i) {
                var mouse = d3.mouse(this);

                if (mouse[0] < leftBound || mouse[0] > rightBound) {
                  self.$baseGrp.select('.mouse-line').style('opacity', '1');
                  self.$baseGrp.selectAll('.mouse-per-line circle').style('opacity', '1');
                  return;
                }

                self.$baseGrp.select('.mouse-line').style('opacity', '1');

                if (config.showTooltipCircleOnLines) {
                  self.$baseGrp.selectAll('.mouse-per-line circle').style('opacity', '1');
                }
                var arr = {};

                self.$baseGrp.select('.mouse-line').attr('d', function() {
                  var d = 'M' + mouse[0] + ',' + config.height;
                  d += ' ' + mouse[0] + ',' + 0;

                  return d;
                });
                var lineNodes = self.$baseGrp.selectAll('.linegraph-path').nodes();

                self.$baseGrp.selectAll('.mouse-per-line').attr('transform', function(d, i) {
                  var end = 0;
                  var beginning = 0;
                  if (lineNodes && lineNodes[i]) {
                    end = lineNodes[i].getTotalLength();
                  } else {
                    return;
                  }
                  var target = null;
                  var pos;
                  while (true) {
                    target = Math.floor((beginning + end) / 2);
                    pos = lineNodes[i].getPointAtLength(target);
                    if ((target === end || target === beginning) && pos.x !== mouse[0]) {
                      break;
                    }
                    if (pos.x > mouse[0]) end = target;
                    else if (pos.x < mouse[0]) beginning = target;
                    else break; //position found
                  }

                  var domain = xScale.domain();
                  var range = [leftBound, rightBound];
                  var rangePoints = d3.range(range[0], range[1] + xScale.step(), xScale.step());
                  var xLabel = domain[d3.bisect(rangePoints, pos.x + xScale.step() / 2) - 1];
                  arr.labelText = xLabel;

                  d3.select(this)
                    .select('text')
                    .text(yScale.invert(pos.y).toFixed(1));

                  let labelValue = '';

                  self.labels.forEach(l => {
                    if (l.label === d[0].labelText) {
                      labelValue = l.value;
                    }
                  });

                  if (config.tooltipValueType === 'discrete') {
                    const entry = (self.data || []).find(_d => _d.label === xLabel);
                    arr[labelValue] = entry[labelValue];
                  } else {
                    arr[labelValue] = yScale.invert(pos.y).toFixed(2);
                  }
                  arr.data = {};
                  return 'translate(' + mouse[0] + ',' + pos.y + ')';
                });

                eventHandlers.mouseOver(arr);
                eventHandlers.onMouseMove();
                return;
              }
          )
          .on(
            'mouseleave',
            (config.onMouseLeave && config.onMouseLeave.bind(self)) ||
              function(d, i) {
                self.$baseGrp.select('.mouse-line').style('opacity', '0');
                self.$baseGrp.selectAll('.mouse-per-line circle').style('opacity', '0');
                eventHandlers.onMouseOut();
                return;
              }
          );

        //onLoaded
        config.onLoad && config.onLoad.bind(self)(config);
      }
    }
  }
}

export default withGraphComponent(CMAreaGraph, { className: 'cm-area' });
