import * as d3 from 'd3';
import { CatameraGraph } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import './cm-pyramid.css';
import withGraphComponent from '../../Graph-wrapper-HOC';

import ToolTip from '../../tooltip';

let toolTipClass;
class CMPyramidGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMPyramidGraph';
    this.colors = {};
    toolTipClass = new ToolTip($ele);
  }

  setData(origData) {
    this.origData = Object.assign({}, origData);
    this.summaryData = Object.assign({}, origData);
    this.processData();
  }

  processData() {
    const data = [...(this.origData.data ? this.origData.data : this.origData)];
    // Assigning Labels
    const labels =
      this.origData.labels && this.origData.labels.length > 0
        ? this.origData.labels // For Multiseris Data
        : [{ label: 'label', value: 'label' }];

    let formatedData = data.filter(d => {
      const _d = d;
      // filter out data object with one or more value undefined
      for (let i = 0; i < labels.length; i += 1) {
        if (!d[labels[i].value]) {
          _d.pass = false;
        }
      }
      return _d.pass !== false;
    });

    // handle data with multiple columns
    // Graph Specfic not applicable for 2D graphs
    if (labels.length > 1) {
      const averageData = [];
      labels.map(d =>
        averageData.push({
          label: d.label,
          value: d3.mean(formatedData.map(k => k[d.value])).toFixed(2),
        })
      );
      formatedData = averageData;
      this.dataType = '1D';
    }

    this.data = formatedData;
    this.formatedData_backup = JSON.parse(JSON.stringify(formatedData));
  }

  // call render function in d3basegraph to update dimensions and responsive scales
  render(animate = true) {
    const { preferences } = this || { preferences: {} };
    let dataToBeRendered = null;
    const normalize = preferences.normalize || false;
    super.render();

    if (!this.width || !this.height) return; // this should be added after super.render() because we need to calculate width and height in super.render()
    dataToBeRendered = this.formatedData_backup;
    if (normalize) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      dataToBeRendered = this.normalizeData(this.formatedData_backup);
    }

    //Updating this.data to the normalized data for baseGraph methods
    this.data = dataToBeRendered;
    this.summaryData.data = dataToBeRendered;

    //Getting Configurations from the Graph Configurtions
    let config = getGraphConfigurations.call(this);
    dataToBeRendered = this.mapLabelsToColors(dataToBeRendered);

    const pieSlices = CatameraGraph.Pyramid().config(config);
    this.$baseGrp.datum(dataToBeRendered).call(pieSlices);

    let legendGroup = CatameraGraph.legendGroup().config(config);
    this.$baseGrp.datum(dataToBeRendered).call(legendGroup);

    const $pyramidSlices = this.$baseGrp.selectAll('.pyramid-group');

    const dataExtractionMethod = requiredParameters => {
      const { d, valuePrefix, valueSuffix } = requiredParameters;
      let temp = {};
      if (d) {
        const { label, value } = d;
        temp = { label, value, valuePrefix, valueSuffix };
      }
      return temp;
    };

    if ($pyramidSlices) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: dataToBeRendered,
        ...config,
      });
      $pyramidSlices
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this)(config);
    }
  }
}

export default withGraphComponent(CMPyramidGraph, { className: 'cm-pyramid' });
