import * as d3 from 'd3';
// import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
// import digitaliseData from "../../Graphs/DigitaliseData"

const getGraphCenter = (config, i = 0, length = 1) => {
  var toPx = function(em) {
    return config.fontSize * em;
  };
  var centerX = config.width / 2 + toPx(config.margin.left);
  var centerY = config.height / 2 + toPx(config.margin.top);
  if (config.viewType === 'split-legends') centerX = config.width / 4 + toPx(config.margin.left);
  if (config.viewType === 'split') centerX = (config.width * (2 * i + 1)) / (2 * length);
  return { centerX, centerY };
};

var aster = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    showSliceLabel: true,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // for loading animation (will not be used when redraw)
  function tweenRadius(r, t) {
    var dampingFactor = 3;
    var dampingFactor2 = 2;
    if (t < 0.25) {
      return r - (r * t) / dampingFactor;
    } else if (t < 0.5) {
      return r + (r * (t - 0.5)) / dampingFactor;
    } else if (t < 0.75) {
      return r + (r * (t - 0.5) * (t - 0.5)) / dampingFactor2;
    } else {
      return r + (r * (t - 1) * (t - 1)) / dampingFactor2;
    }
  }

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration || 300 : 0; //temp fix for timeline animation

    // calculate center position of the graph
    var { centerX, centerY } = getGraphCenter(config);

    selection.each(function(data) {
      if (!data) {
        data = [];
      }

      // shadow
      var dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      const isRedraw = selection.selectAll('.slices')._groups[0].length > 0;

      // add slicesGrp
      var slicesGrp = selection.selectAll('.slices').data([1]);
      slicesGrp.exit().remove();
      slicesGrp
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slicesGrp)
        .attr('transform', 'translate(' + centerX + ',' + centerY + ')');

      var slices = selection
        .selectAll('.slices')
        .selectAll('path.slice')
        .data(config.pie(data));

      // Remove extra slices
      slices.exit().remove();
      // Added slices
      slices
        .enter()
        .append('path')
        .style('fill', '#efefef')
        .attr('stroke', 'none')
        .merge(slices)
        .attr('class', 'outer-slice')
        .attr('cursor', 'pointer');
      slices
        .enter()
        .append('path')
        .style('fill', function(d, i) {
          return MRParseColors(config.graphColors[i % config.graphColors.length]);
        })
        .merge(slices)
        .attr('class', 'slice')
        .attr('pointer-events', 'all')
        .attr('cursor', 'pointer');

      if (config.showSliceLabel) {
        var sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(config.pie(data));
        sliceLabel.exit().remove();
        sliceLabel
          .enter()
          .append('text')
          .attr('class', 'slice-label')
          .attr('opacity', '0')
          .attr('transform', function(d) {
            var pos = config.arc.centroid(d);
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('pointer-events', 'none');

        sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(config.pie(data))
          .attr('font-size', 0 || config.radius / 10)
          .attr('text-anchor', 'middle')
          .attr('pointer-events', 'none')
          .attr('fill', 'white')
          .text(function(d) {
            if (Math.abs(d.endAngle - d.startAngle) < 0.5) {
              return '';
            }
            var percentage = (((d.endAngle - d.startAngle) / Math.PI / 2) * 100).toFixed(0) + '%';
            return percentage;
          })
          .attr('opacity', function() {
            return config.animate ? 0 : 1;
          })
          .transition()
          .duration(duration)
          .attr('transform', function(d) {
            var pos = config.arc.centroid(d);
            if (d.endAngle - d.startAngle > 6.283185) return 'translate(' + 0 + ',' + 0 + ')';
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('opacity', '1');
      }

      // add loading animation / redraw animation
      requestAnimationFrame(() => {
        var slices = selection.selectAll('.slices').selectAll('path.slice');
        var outerSlices = selection.selectAll('.slices').selectAll('path.outer-slice');
        let endAngle = 0;
        slices
          .merge(slices)
          .transition()
          .ease(d3.easeCubicOut)
          .duration(function(d, i) {
            // loading animation duration
            let _duration = 0;
            if (!isRedraw) {
              // if (config.animate === true) {
              _duration = (duration + i * 0.095 * duration) * 1.8;
              // redraw animation duration
            } else {
              _duration = duration + i * duration * 0.04;
            }
            return _duration;
            // animation for slices
          })
          .attr('d', config.arc);
        if (config.outerArcRadius) {
          outerSlices.attr('d', config.outerArc);
        }
      });

      return selection;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    // set default config

    config.pie = d3
      .pie()
      .sort(null)
      .value(function(d) {
        return parseFloat(d.value);
      });
    config.arc = d3
      .arc()
      .innerRadius(+config.innerRadius)
      .outerRadius(function(d) {
        return (config.radius - +config.innerRadius) * (config.yScale(d.value) / config.radius) + config.innerRadius;
      });

    config.outerArc = d3
      .arc()
      .innerRadius(+config.innerRadius)
      .outerRadius(config.outerArcRadius || config.radius);

    return chart;
  };

  return chart;
};

export default aster;
