import { CatameraGraph } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import ToolTip from '../../tooltip';
import withGraphComponent from '../../Graph-wrapper-HOC';

let toolTipClass;

class CMGaugeGraph extends CatameraGraph.GraphTooltip(CatameraGraph.d3baseGraph) {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMGaugeGraph';
    toolTipClass = new ToolTip($ele);
  }
  setData(data) {
    data = this.normalizeData(data, { type: 'gauge' });

    const { summary } = data;
    if (summary && summary.value) {
      var safeValue = summary.maxValue ? (summary.value / summary.maxValue) * 100 : summary.value;
      var value = summary.maxValue ? summary.maxValue : summary.value;
      const temp = { ...summary, value: value, intitalValue: summary.value, safeValueData: safeValue };
      this.graphData = { ...data, summary: temp };
    }
  }

  render(animate = true) {
    super.render();

    // this should be added after super.render() because we need to calculate width and height in super.render()
    if (!this.graphData || !this.width || !this.height) return;

    // config for graphs.

    var config = getGraphConfigurations.call(this);
    config.viewType = '';
    this.$baseGrp.selectAll('*').remove();

    var gaugeMeter;
    if (config.gaugeMeterTheme == 2) {
      config.offsetCenterY = 0.2;
      if (this.graphData.summary.target) {
        this.graphData.summary.color =
          this.graphData.summary.value >= this.graphData.summary.target ? 'colorpositive' : 'colornegative';
      }
      gaugeMeter = CatameraGraph.gaugeMeterTheme2().config(config);
    } else {
      config.offsetCenterY = 0.05;
      gaugeMeter = CatameraGraph.gaugeMeter().config(config);
    }
    this.$baseGrp.datum({ ...this.graphData.summary }).call(gaugeMeter);

    // graph value
    var graphValue = CatameraGraph.graphValue().config(config);
    this.$baseGrp.datum(this.graphData).call(graphValue);

    ////  Handling custom mouse events ////

    // tooltip
    const $slices = this.$baseGrp.selectAll('.filled');
    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;
      let temp = {};
      if (d) {
        const { subtext, safeValue } = d;

        temp = { label: subtext || '', value: safeValue };
      }
      return temp;
    };
    if (config.tooltipsEnabled) {
      config = {
        ...config,
        valueSuffix: this.graphData.summary.maxValue ? '%' : config.valueSuffix,
      };
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: this.graphData,
        ...config,
      });
      $slices
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);
    }
    // Click
    $slices.on('click', function(d) {
      if (config.onClick) config.onClick.bind(this).call(d);
    });

    //onLoaded
    config.onLoad && config.onLoad.bind(this)(config);
  }
}

export default withGraphComponent(CMGaugeGraph, { className: 'cm-gauge' });
