import * as d3 from 'd3';

var dropShadow = function() {
  var config = {
    id: 'drop-shadow',
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    selection.each(function(data, i) {
      // filters go in defs element
      // var defs = selection.append('defs');
      var svg = document.getElementById('hiddenSvg');

      if (!svg) {
        var root = document.getElementsByTagName('BODY')[0];
        var svg = document.createElement('SVG');
        svg.setAttribute('id', 'hiddenSvg');

        root.appendChild(svg);
      }

      var defs = d3.select(svg).select('defs');

      if (!defs.empty()) {
        return;
      }

      defs = d3.select(svg).append('defs');

      // create filter with id #drop-shadow
      // height=130% so that the shadow is not clipped
      var filter = defs
        .append('filter')
        .attr('id', config.id)
        .attr('height', '300%')
        .attr('width', '300%')
        .attr('x', '-100%')
        .attr('y', '-100%');

      filter
        .append('feGaussianBlur')
        .attr('in', 'SourceAlpha')
        .attr('stdDeviation', 1.5);

      filter
        .append('feOffset')
        //.attr('in', 'blur')
        .attr('dx', 0.5)
        .attr('dy', 0.5)
        .attr('result', 'offsetBlur');

      var feComponentTransfer = filter.append('feComponentTransfer');
      feComponentTransfer
        .append('feFuncA')
        .attr('type', 'linear')
        .attr('slope', 0.3);

      // overlay original SourceGraphic over translated blurred opacity by using
      // feMerge filter. Order of specifying inputs is important!
      var feMerge = filter.append('feMerge');

      feMerge.append('feMergeNode');
      // .attr("in", "offsetBlur")
      feMerge.append('feMergeNode').attr('in', 'SourceGraphic');

      return;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default dropShadow;
