/** @module cm-clustered-bubble.js
 *   @author
 *   @description React Component wrapper for cm-clustered-bubble graph
 */

import React, { Component } from 'react';
import CMClusteredGraph from './cm-clusteredbubblegraph';
import './cm-clusterbubble.css';
import '../../GraphLibrary/graph.css';

class CMClustered extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
    this.state = {
      colors: props.colors || this.colors,
      data: props.data || {},
      renderState: true,
    };
    this.graphRef = React.createRef();
  }

  render() {
    return (
      <div className={`cm-clustered-bubble cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" ref={this.graphRef} />
      </div>
    );
  }

  _dataChanged(data) {
    if (data) {
      this.graph = this.graph || new CMClusteredGraph(this.graphRef.current);
      this.graph.setPreferences(this.props.preferences || {});
      // this.graph.setData(data);
      this.graph.setData(data);
      // if (this.props.renderVariable) {
      //   this.graph.render(true);
      // }

      requestAnimationFrame(() => {
        this.graph.render(true);
        this.graph.redraw(data);
        // this.graph.draw(data);
        // this.graph.setData(data);
        //   setTimeout(() => {
        //     this.graph.redraw(newCsv);
        //   }, 6000);
      });

      //todo: add redraw/ resize/ setData animation changes
    }
  }

  draw(animate) {
    this.graph.render(animate);
  }

  resize() {
    this.graph.render(false);
  }

  static getDerivedStateFromProps(props, current_state) {
    if (current_state.data !== props.data) {
      current_state.data = props.data;
      return current_state;
    }
    return null;
  }

  componentDidMount() {
    this._dataChanged(this.state.data);
  }

  componentDidUpdate() {
    // this._dataChanged(this.state.data, false);
  }

  componentWillUnmount() {}
}

export default CMClustered;
