import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import Utils from './utils';

const dataTreeBars = function() {
  let config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    showValues: false,
  };

  // draw the graph here
  function chart(selection) {
    selection.each((data, i) => {
      let barWidth = config.barWidth * (config.barWidthFactor || 0.8);
      // shadow
      let dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      let barGrp = selection.selectAll('.bar-group').data(data);
      barGrp.exit().remove();
      barGrp
        .enter()
        .append('g')
        .attr('class', 'bar-group')
        .merge(barGrp);

      var valueGrp = selection.selectAll('.value-group').data(data);
      valueGrp.exit().remove();

      valueGrp
        .enter()
        .append('g')
        .attr('class', 'value-group')
        .merge(valueGrp);

      let bars = selection
        .selectAll('.bar-group')
        .selectAll('.mrgraph-bar')
        .data((d, i) => {
          d.values.map(function(entry) {
            entry.labelIndex = i;
            entry.parrent = d.label;
            entry.parrentIndex = i;
          });
          return d.values;
        });

      bars.exit().remove();

      let barShadow = selection
        .selectAll('.bar-group')
        .selectAll('.mrgraph-bar-shadow')
        .data((d, i) => {
          d.values.map(function(entry) {
            entry.labelIndex = i;
            entry.parrent = d.label;
            entry.parrentIndex = i;
          });
          return d.values;
        });

      barShadow.exit().remove();
      var fontSize = '10';
      var values = selection
        .selectAll('.value-group')
        .selectAll('.mrgraph-bar-value')
        .data(function(d, i) {
          d.values.map(function(entry) {
            entry.labelIndex = i;
            entry.parrent = d.label;
            entry.parrentIndex = i;
          });
          return d.values;
        });
      values.exit().remove();

      if (config.height > 0) {
        if (config.showValues) {
          values
            .enter()
            .append('text')
            .attr('class', 'mrgraph-bar-value')
            .merge(values);

          selection
            .selectAll('.value-group')
            .selectAll('.mrgraph-bar-value')
            .attr('y', config.height)
            .text(function(d, i) {
              return config.valuePrefix + d.value + config.valueSuffix;
            })
            .attr('font-size', function(d, i) {
              const maxWidth = config.barWidth * 0.8;
              const textLength = `${config.valuePrefix + d.value + config.valueSuffix}`.length;
              const valueFontSize = config.barWidth / textLength;
              var textWidth = textLength * valueFontSize;
              var reducedFontSize = valueFontSize;
              if (textWidth && textWidth > maxWidth) {
                reducedFontSize = (valueFontSize * maxWidth) / textWidth;
              }
              reducedFontSize = Math.min(reducedFontSize, 18);
              // hide text if its too small
              return reducedFontSize > 6 ? reducedFontSize : 0;
            })
            .attr('fill', function(d, i) {
              return config.valueColor || '#FFFFFF';
            })
            .attr('opacity', 0)
            .style('text-anchor', 'middle')
            .attr('x', function(d, i) {
              return (
                config.xscaleobj[d.parrentIndex] +
                i * config.barWidth +
                barWidth / 2 +
                (config.barWidthFactor ? (1 - config.barWidthFactor) * config.barWidth * 0.5 : 0)
              );
            })
            .transition()
            .delay(function(d, i) {
              return 0 + 50 * i;
            })
            .ease(d3.easeQuad)
            .duration(config.duration)
            .attr('y', function(d, i) {
              const maxWidth = config.barWidth * 0.8;
              const textLength = `${config.valuePrefix + d.value + config.valueSuffix}`.length;
              const valueFontSize = config.barWidth / textLength;
              var textWidth = textLength * valueFontSize;
              var reducedFontSize = valueFontSize;
              if (textWidth && textWidth > maxWidth) {
                reducedFontSize = (valueFontSize * maxWidth) / textWidth;
              }
              reducedFontSize = Math.min(reducedFontSize, 18);
              return config.yScale(d.value) + 1.5 * reducedFontSize * (d.value > 0 ? 1 : -1);
            })
            .attr('opacity', 1);
        }

        if (config.barShadow) {
          barShadow
            .enter()
            .append('rect')
            .attr('class', 'mrgraph-bar-shadow')
            .attr('height', 0)
            .attr('y', (d, i) => {
              return config.height;
            })
            .merge(barShadow)
            .style('fill', '#f2f2f2')
            .attr('x', (d, i) => {
              return (
                config.xscaleobj[d.parrentIndex] +
                i * config.barWidth +
                (config.barWidthFactor ? (1 - config.barWidthFactor) * config.barWidth * 0.5 : 0)
              );
            })
            .attr('width', barWidth)
            .transition()
            .attr('y', 0)
            .attr('height', config.height);
        }
        bars
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-bar')
          .attr('height', 0)
          .attr('y', (d, i) => {
            if (config.negativeAxis) {
              return config.yScale(0);
            }
            return config.height;
          })
          .merge(bars)
          .style('fill', (d, i) => {
            if (data.length > 1 && config.coloredBars) {
              return MRParseColors(d.labelColor || config.graphColors[d.labelIndex % config.graphColors.length]);
            } else {
              return MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length]);
            }
          })
          .style('opacity', (d, i) => 1 - i * (config.xScaleOpacityGradient[d.parrentIndex] || 0.08))
          .style('cursor', 'pointer')
          .attr('x', (d, i) => {
            return (
              config.xscaleobj[d.parrentIndex] +
              i * config.barWidth +
              (config.barWidthFactor ? (1 - config.barWidthFactor) * config.barWidth * 0.5 : 0)
            );
          })
          .attr('width', barWidth)
          .transition()
          .delay((d, i) => {
            return 0 + 50 * i;
          })
          .ease(d3.easeQuad)
          .duration(config.duration)
          .attr('y', (d, i) => {
            if (config.negativeAxis) {
              return d.value > 0 ? config.yScale(parseFloat(d.value)) : config.yScale(0);
            }
            return config.yScale(parseFloat(d.value));
          })

          .attr('height', (d, i) => {
            if (config.percentageBased) {
              var percentage = parseFloat(d.value) / 100;
              var yValue = (percentage * 100).toFixed(2);
              var barHeight = config.height - config.yScale(yValue);
            } else {
              if (config.negativeAxis) {
                return Math.abs(config.yScale(0) - config.yScale(parseFloat(d.value)));
              }
              var barHeight = config.height - config.yScale(parseFloat(d.value));
            }
            // add bar height to an array to store it, we'll need to use it later to render bar values
            return barHeight;
          });
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

const xAxis = function(mode) {
  let config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    viewRatio: 1,
    rotateAxis: false,
  };

  function chart(selection) {
    selection.each((data, i) => {
      let xAxisGrp = selection.selectAll('.axis-group').data([0]);
      xAxisGrp.exit().remove();

      if (config.showAxisX) {
        xAxisGrp
          .enter()
          .append('g')
          .attr('class', 'axis-group')
          .merge(xAxisGrp)
          .attr('transform', (d, i) => {
            return 'translate(' + 0 + ',' + config.height + ')';
          });

        let text = selection
          .selectAll('.axis-group')
          .selectAll('text')
          .data(data);
        text.exit().remove();
        text
          .enter()
          .append('text')
          .merge(text)

          .text((d, i) => {
            let maxWidth = 0;
            let text = d.label + '';
            if (config.xscaleobj[i + 1]) {
              maxWidth = config.xscaleobj[i + 1] - config.xscaleobj[i];
            } else {
              maxWidth = config.width - config.xscaleobj[i];
            }

            const maxTextLength = (maxWidth * 0.9) / (config.xAxisFontSize || config.fontSize);
            if (text.length > maxTextLength) {
              text = text.slice(0, maxTextLength) + '...';
            }
            return config.xAxisMaxLabelLength && config.xAxisMaxLabelLength
              ? d.label.slice(0, config.xAxisMaxLabelLength)
              : text;
          })

          .attr('font-size', config.xAxisFontSize || config.fontSize)
          .attr('y', (config.xAxisFontSize || config.fontSize) * 1.5)
          .style('fill', config.xAxisColor !== undefined ? config.xAxisColor : 'currentColor')
          .style('text-anchor', 'middle')
          .attr('transform', (d, i) => {
            let textoffset = (d.values.length * config.barWidth) / 2;
            textoffset = textoffset > 0 ? textoffset : 0;

            return 'translate(' + (config.xscaleobj[i] + textoffset) + ',' + 0 + ')';
          });
      }
    });

    return selection;
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export { dataTreeBars, xAxis };
