import * as d3 from 'd3';
import { CatameraGraph, MRParseColors } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import withGraphComponent from '../../Graph-wrapper-HOC';

import ToolTip from '../../tooltip';

let toolTipClass;
const getValuesList = data => {
  let tempArray = [];
  const getValues = data => {
    if (data.value && typeof data.value === 'string') return tempArray.push(parseInt(item.value));
    if (Array.isArray(data)) {
      data.map(item => {
        if (item.values && Array.isArray(item.values)) return getValues(item.values);

        if (item.value && typeof item.value === 'string') tempArray.push(parseInt(item.value));
      });
    }
  };
  getValues(data);
  return tempArray.filter(a => a > 0);
};

class CMHeatMapGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMHeatMapGraph';
    this.colors = {};
    toolTipClass = new ToolTip($ele);
  }

  setData(data) {
    this.data = data.data ? data.data : data;
    const normalize = this.preferences.normalize || false;
    if (normalize && this.data.length) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      this.data = this.normalizeData(this.data);
    }
    this.summary = data.summary ? data.summary : '';
    this.labelsFromData = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    this.dataType = '2D';
    this.processData();
  }

  processData() {
    const self = this;
    let formattedData = [];
    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (let i = 0; i < self.labelsFromData.length; i++) {
        if (!d[self.labelsFromData[i].value] && !d[self.labelsFromData[i].value] === 0) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });

    // process data object with multiple values
    this.filteredData.map(dataset => {
      let item = {
        label: dataset.label,
        values: [],
      };
      for (let key in dataset) {
        if (dataset.hasOwnProperty(key) && key != 'label' && key != '_formatted') {
          let subItem = {
            label: key,
            value: dataset[key],
            dataValue: dataset[key],
          };
          item.values.push(subItem);
        }
      }
      formattedData.push(item);
    });
    this.graphData = formattedData;
  }

  render(animate = true) {
    super.render(animate);

    // this should be added after super.render() because we need to calculate width and height in super.render()
    if (!this.filteredData || !this.width || !this.height) return;

    const values = getValuesList(this.graphData);

    const maxValue = Math.max(...values);
    const minValue = Math.min(...values);
    const range = maxValue - minValue;

    //Setting Legends Range label texts.
    this.labels = [
      {
        label: `< ${minValue + range * 0.25}`,
        value: `< ${minValue + range * 0.25}`,
      },
      {
        label: `>= ${minValue + range * 0.25} and < ${minValue + range * 0.5}`,
        value: `>= ${minValue + range * 0.25} and < ${minValue + range * 0.5}`,
      },
      {
        label: `>= ${minValue + range * 0.5} and < ${minValue + range * 0.75}`,
        value: `>= ${minValue + range * 0.5} and < ${minValue + range * 0.75}`,
      },
      {
        label: `> ${minValue + range * 0.75}`,
        value: `> ${minValue + range * 0.75}`,
      },
    ];

    //******************* */
    //finding max text size in y axis

    let maxTextSize = 0;
    let maxVal = '';
    this.filteredData.forEach(element => {
      if (element && element.label) {
        maxTextSize = Math.max(maxTextSize, (element.label || '').toString().length);
        maxVal = `${element.label}`.length > maxVal.length ? `${element.label}` : maxVal;
      }
    });
    let config = getGraphConfigurations.call(this);
    const { range: graphRange } = config;
    const yAxisWidth = this.getYAxisWidth(maxVal, config);
    graphRange.left = config.showAxisY ? Math.max(yAxisWidth, graphRange.left) : graphRange.left;

    // Setting  Scales
    // y scale
    let yScale = d3
      .scaleBand()
      .range([graphRange.top, config.height - graphRange.bottom])
      .domain(
        this.filteredData.map(function(d, i) {
          return d.label;
        })
      );

    // setting xscale

    // ******************************
    let xScale = d3
      .scaleBand()
      .range([graphRange.left, this.width - graphRange.right])
      .domain(
        this.labelsFromData.map(function(d, i) {
          return d.label;
        })
      );
    config.graphColors = [
      ...(this.preferences.graphColors || []),
      ...['#fce5f1', '#f07fb9', '#e20074', '#802051', '#E8E8E8'],
    ];
    const { graphColors } = config;

    const getBoxColour = value => {
      if (value >= minValue && value <= maxValue) {
        if (value >= minValue && value < minValue + range * 0.25) {
          return MRParseColors(graphColors[0]);
        }
        if (value >= minValue + range * 0.25 && value < minValue + range * 0.5) {
          return MRParseColors(graphColors[1]);
        }
        if (value >= minValue + range / 2 && value < minValue + range * 0.75) {
          return MRParseColors(graphColors[2]);
        }
        if (value >= minValue + range * 0.75) {
          return MRParseColors(graphColors[3]);
        }
      }
      return 'E5E5E5';
    };

    // config for graphs
    config = {
      ...config,
      yScale: yScale,
      xScale: xScale,
      getBoxColour,
      xAxisData: this.labelsFromData,
      x: (d, i) => {
        return xScale(d.label);
      },
      y: (d, i) => {
        return yScale(d.parrent);
      },
      yAxisWidth,
      range: graphRange,
      animate: false,
    };

    let mode = 'heatmap';
    this.$baseGrp.selectAll('*').remove();

    // Show Y axis based on preferences.
    let yAxis = CatameraGraph.yAxis(mode).config(config);
    this.$baseGrp.datum(this.filteredData).call(yAxis);

    // Show X axis based on preferences.
    let xAxis = CatameraGraph.xAxis(mode).config(config);
    this.$baseGrp.datum(this.filteredData).call(xAxis);

    // drawing bars
    let bars = CatameraGraph.heatMap().config(config);
    this.$baseGrp.datum(this.graphData).call(bars);

    //legends
    let legendGroup = CatameraGraph.legendGroup().config(config);
    this.$baseGrp.datum(this.labels).call(legendGroup);

    // tooltip
    let $barFgs = this.$baseGrp.selectAll('.mrgraph-box');
    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;

      let temp = {};
      if (d) {
        const { label, value } = d;
        temp = { label, value };
      }
      return temp;
    };

    if ($barFgs) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: this.graphData,
        ...config,
      });
      $barFgs
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this)(config);
    }
  }
}

export default withGraphComponent(CMHeatMapGraph, { className: 'cm-availability' });
