const makeid = length => {
  let text = '';
  const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  const maxLength = length || 5;

  for (let i = 0; i < maxLength; i += 1) text += possible.charAt(Math.floor(Math.random() * possible.length));

  return text;
};

const Utils = {
  cornerRadius,
  makeid,
};

export default Utils;
