const xBreakPoint = {
  xs: 150,
  sm: 400,
  md: 700,
  lg: 1000,
  fs: 1000,
};

const yBreakPoint = {
  xs: 150,
  sm: 400,
  md: 700,
  lg: 1000,
  fs: 1000,
};

const getWidgetBoxType = options => {
  const aspectRatio = options.clientWidth / options.clientHeight;
  if (aspectRatio > 0 && aspectRatio < 0.5) {
    return 'slim';
  }
  if (aspectRatio >= 0.5 && aspectRatio < 0.75) {
    return 'tall-rectangle';
  }
  if (aspectRatio >= 0.75 && aspectRatio < 1.25) {
    return 'square';
  }
  if (aspectRatio >= 1.25 && aspectRatio < 2.25) {
    return 'rectangle';
  }
  if (aspectRatio >= 2.25 && aspectRatio < 4) {
    return 'bar';
  }
  if (aspectRatio >= 4) {
    return 'long-bar';
  }
  return '';
};

const getWidgetBoxWidth = options => {
  let boxWidth = '';
  Object.entries(xBreakPoint).some(([key, value]) => {
    if (options.clientWidth < value) {
      boxWidth = key;
      return true;
    }
    return false;
  });

  return options.clientWidth > 1000 ? 'fs' : boxWidth;
};

const getWidgetBoxHeight = options => {
  let boxHeight = '';
  Object.entries(yBreakPoint).some(([key, value]) => {
    if (options.clientHeight < value) {
      boxHeight = key;
      return true;
    }
    return false;
  });

  return boxHeight;
};

export { getWidgetBoxType, getWidgetBoxWidth, getWidgetBoxHeight, xBreakPoint, yBreakPoint };
