import * as d3 from 'd3';
import getGraphConfigurations from '../../Graph-Configuration';
import { CatameraGraph, MRFormatter } from '../../GraphLibrary';
import ToolTip from '../../tooltip';
import './index.css';
import withGraphComponent from '../../Graph-wrapper-HOC';
let toolTipClass;
class CMWaterfallGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    toolTipClass = new ToolTip($ele);
    this.graphCustomName = 'CMWaterfallGraph';
  }

  setData(data) {
    const self = this;

    this.data = data.data ? data.data : data;
    const normalize = this.preferences.normalize || false;
    if (normalize && this.data.length) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      this.data = this.normalizeData(this.data);
    }

    this.labels = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    var cumulative = 0;

    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (var i = 0; i < self.labels.length; i++) {
        if (!d[self.labels[i].value]) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });
    var formattedData = [];
    var item = {};

    for (var k = 0; k < this.labels.length; k++) {
      var items = [];
      for (var i = 0; i < this.filteredData.length; i++) {
        item = {
          start: cumulative,
          sumValue: cumulative,
          value: this.filteredData[i].value,
          label: this.filteredData[i].label,
          color: this.filteredData[i].value > 0 ? this.preferences.positiveColor : this.preferences.negativeColor,
          percent: this.filteredData[i].percent === undefined ? '0' : this.filteredData[i].percent,
          class: this.filteredData[i].value >= 0 ? 'positive' : 'negative',
          _formatted: MRFormatter.format(this.filteredData[i].value),
        };
        cumulative += parseInt(this.filteredData[i].value);
        (item.cumulativeValue = cumulative), (item.end = cumulative);
        items.push(item);
      }

      items.push({
        name: 'Total',
        end: cumulative,
        start: 0,
        class: 'total',
        label: 'Total',
        color: this.preferences.baseColor,
        percent: '0',
        value: cumulative,
        sumValue: cumulative,
        _formatted: MRFormatter.format(cumulative),
      });
      formattedData.push(items);
    }
    this.graphData = formattedData;

    this.data = formattedData[0];
  }

  render(animate = true) {
    const self = this;

    let config = getGraphConfigurations.call(this);
    super.render(animate);

    // this should be added after super.render() because we need to calculate width and height in super.render()

    var maxVal = 0;
    var minVal = 0;

    if (this.graphData[0] && typeof this.graphData[0] == 'object') {
      for (var i = 0; i < this.graphData.length; i++) {
        var newMaxVal = d3.max(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.sumValue)) ? parseFloat(d.sumValue) : 0;
        });
        var newMinValue = d3.min(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.sumValue)) ? parseFloat(d.sumValue) : 0;
        });
        maxVal = newMaxVal > maxVal ? newMaxVal : maxVal;
        minVal = newMinValue > minVal ? minVal : newMinValue;
      }
    }
    maxVal = Math.max(maxVal * config.yAxisDomainFactor, this.yDomainMin[1]);
    minVal = Math.min(minVal * config.yAxisDomainFactor, this.yDomainMin[1]);

    maxVal *= config.maxRangeMultiplier;
    minVal *= config.minRangeMultiplier;

    const { range } = config;
    // Set Scales

    var xScale = d3
      .scaleBand()
      .range([range.left, this.width - range.right])
      .domain(
        this.graphData[0].map(function(d, i) {
          return d.label;
        })
      );

    var yScale = d3
      .scaleLinear()
      .range([config.height, range.top * 2])
      .domain([minVal, maxVal]);

    config = {
      ...config,
      xScale: xScale,
      yScale: yScale,
      x: (d, i) => {
        return xScale(d);
      },
      y: (d, i) => {
        return yScale(parseFloat(d));
      },
    };

    xScale.paddingInner(0.4).paddingOuter(0.4);

    var mode = '';

    var bars = CatameraGraph.waterfall().config(config);
    this.$baseGrp.datum(this.graphData).call(bars);
    var yAxis = CatameraGraph.yAxis(mode).config(config);
    this.$baseGrp.datum(this.graphData).call(yAxis);
    var xAxis = CatameraGraph.xAxis(mode).config(config);
    this.$baseGrp.datum(this.graphData).call(xAxis);
    let legendGroup = CatameraGraph.legendGroup().config(config);
    this.$baseGrp.datum(this.graphData[0]).call(legendGroup);
    if (config) {
      const $waterfallChart = this.$baseGrp.selectAll('.bar');

      const dataExtractionMethod = requiredParameters => {
        const { d } = requiredParameters;

        let temp = {};
        if (d) {
          const { label, value } = d;
          temp = { label, value };
        }
        return temp;
      };

      if ($waterfallChart) {
        const eventHandlers = toolTipClass.getEventHandlers({
          dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
          labels: this.label,
          data: this.graphData,
          ...config,
        });
        $waterfallChart
          .on('click', config.onClick && config.onClick.bind(this))
          .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
          .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
          .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

        config.onLoad && config.onLoad.bind(this)(config);
      }
    }
  }
}

// export default CMWaterfallGraph;
export default withGraphComponent(CMWaterfallGraph, { className: 'cm-waterfall' });
