var barBgs = function() {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],

    // type could be background or foreground
    barBgType: 'background',
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;
    config.className = config.barBgType == 'background' ? 'bar-bg-group' : 'bar-fg-group';
    selection.each(function(data, i) {
      let barGrp;
      let barBgs;
      if (config.isClipPath) {
        const defs = selection.selectAll('defs').data([1]);
        defs.exit().remove();
        defs
          .enter()
          .append('defs')
          .merge(defs);

        barBgs = selection
          .selectAll('defs')
          .selectAll('clipPath')
          .data(data, function(d, index) {
            return index;
          });

        barBgs.exit().remove();

        barBgs = barBgs
          .enter()
          .append('clipPath')
          .attr('id', function(d, i) {
            return `${config.uniqueId}-${i}`;
          })
          .selectAll('rect')
          .data(function(d, i) {
            return [d];
          });
      } else {
        barGrp = selection.selectAll('.' + config.className).data([1]);
        barGrp.exit().remove();
        barGrp
          .enter()
          .append('g')
          .attr('class', config.className)
          .merge(barGrp);

        barBgs = selection
          .selectAll('.' + config.className)
          .selectAll('.mrgraph-bar-bg')
          .data(data, function(d, index) {
            return index;
          });
      }

      if (config.showShadow) {
        const defs = selection.selectAll('defs').data([1]);
        defs.exit().remove();
        defs
          .enter()
          .append('defs')
          .merge(defs);

        let linearGradient = selection.select('defs').select('linearGradient');

        if (linearGradient.empty()) {
          linearGradient = selection.select('defs').append('linearGradient');

          linearGradient = linearGradient.attr('id', 'linear-gradient').attr('gradientTransform', 'rotate(90)');

          linearGradient
            .append('stop')
            .attr('offset', '0%')
            .attr('stop-color', '#e4e4e4');

          linearGradient
            .append('stop')
            .attr('offset', '50%')
            .attr('stop-color', '#e3e3e3');

          linearGradient
            .append('stop')
            .attr('offset', '100%')
            .attr('stop-color', '#ffffff');
        }
      }

      barBgs.exit().remove();

      const disableBarBgs = true;

      if (!disableBarBgs && config.height > 0) {
        barBgs
          .enter()
          .append('rect')
          .attr('class', 'mrgraph-bar-bg')
          .merge(barBgs)
          .style('fill', '#e3e3e3')
          .style('cursor', 'pointer')
          // .attr('y', 0)
          .attr('y', function(d, i) {
            if (config.percentageBased) {
              var percentage = 0;
              try {
                percentage = d.accumulated / data[data.length - 1][i].accumulated;
              } catch (e) {
                //
              }
              var yValue = (percentage * 100).toFixed(2);
              return config.yScale(yValue);
            }
            return data.length > 1 ? config.yScale(d.accumulated) : config.yScale(parseFloat(d.value));
          })
          .attr('rx', Utils.cornerRadius(config.xScale.bandwidth(), config.roundedCorner))
          .attr('ry', Utils.cornerRadius(config.xScale.bandwidth(), config.roundedCorner))
          // .attr('height', config.height)
          .attr('height', function(d, i) {
            if (config.percentageBased) {
              var percentage = 0;
              try {
                percentage = d.accumulated / data[data.length - 1][i].accumulated;
              } catch (e) {
                //
              }
              var yValue = (percentage * 100).toFixed(2);
              return config.height - config.yScale(yValue) + 30;
            }
            return (
              config.height - (data.length > 1 ? config.yScale(d.accumulated) : config.yScale(parseFloat(d.value))) + 30
            );
          })
          .attr('x', function(d, i) {
            return config.xScale(d.label);
          })
          .attr('width', config.xScale.bandwidth())
          .attr('opacity', function(d, i) {
            return config.barBgType == 'background' ? 1 : 1;
          });
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var yAxis = function(mode) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    viewRatio: 1,
    animate: true,
    duration: 500,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    maxLabelLength: 10,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function chart(selection) {
    const self = this;
    var duration = config.animate ? config.duration : 0;

    selection.each(function(data, i) {
      if (data[0] && data[0].value) {
        if (typeof data[0].value == 'string' && data[0].value.includes('%')) {
          config.yAxisType = config.yAxisType ? config.yAxisType : 'percentage';
        }
      }

      var fontSize = config.fontSize || Math.max((config.width / 40) * config.viewRatio, 9);
      var yAxisGroup = selection.selectAll('.y').data([data]);

      // Calculate number of ticks
      var numOfTicks = mode == 'simple' ? 3 : 5;
      numOfTicks = Math.floor(numOfTicks / config.viewRatio || 1);
      if (mode !== 'simple') {
        numOfTicks = Math.round((config.height / (config.fontSize * 3)) * (config.tickDensity || 0.5));
        // numOfTicks = 2 + Math.round((config.height / (config.fontSize * 5)) * (config.tickDensity || 0.5));
      }

      numOfTicks = Math.max(3, numOfTicks);

      var yAxis = d3.axisLeft(config.yAxisScaledraw).tickSizeInner(0);

      if (config.yAxisType == 'text') {
        yAxis.tickFormat(function(d, i) {
          if (typeof d == 'string' && d.length > 10) {
            return mode == 'heatmap' ? (i % 6 == 0 ? d.slice(0, 10) + '..' : '') : d.slice(0, 10) + '..';
          }
          return mode == 'heatmap' ? (i % 6 == 0 ? d : '') : d;
        });
      } else {
        yAxis
          .tickFormat(function(d, i) {
            var tickText = mode == 'simple' ? '' : d;
            var text = '';
            if (typeof tickText == 'number' && tickText.toString().length > 4) {
              var formatted = MRFormatter.format(d, 'toNumber');
              text = formatted.fullValue + formatted.unit;
            } else {
              text = tickText;
            }
            if (config.yAxisType == 'percentage') {
              text = text + '%';
            }
            return text;
          })
          .tickSize(-config.width)
          .ticks(1);
      }
      var yAxisWidth = 0;

      /**
       * yaxis needs to be drawn twice. 1st time it is drawn its text width is caluculated.
       * 2nd time tick width and position is adjusted as per yAxis width
       */
      yAxisGroup
        .enter()
        .append('g')
        .attr('class', 'y axis')
        .merge(yAxisGroup)
        .attr('font-size', fontSize)
        .attr('opacity', 0)
        .call(yAxis);

      // if (mode != 'simple') {
      /*
        // calculate yaxis text max width
        var labelsArray = [];
        if (config.yAxisType == 'text') {
          labelsArray = data.map(function(d) {
            return d.label;
          });
        } else {
          labelsArray = config.yScale.ticks();
        }

        labelsArray.forEach(function(d, i) {
          var text = mode == 'simple' ? '' : d;
          if (typeof text == 'number' && text.toString().length > 4) {
            var formatted = MRFormatter.format(text, 'toNumber');
            text = formatted.fullValue + formatted.unit;
          }
          selection
            .selectAll('.y')
            .selectAll('.tick-sample')
            .selectAll('text')
            .text(text)
            .attr('font-size', fontSize);

          var textNodes = selection.selectAll('.y').selectAll('.tick-sample')._groups[0];
          try {
            var textWidth = textNodes ? textNodes[0].getBBox().width * 0.7 : 16;
          } catch (e) {
            var textWidth = textNodes ? textNodes[0].getBoundingClientRect().width * 0.7 : 16;
          }
          if (config.yAxisType == 'text') {
            textWidth = textWidth * 1.1;
          }
          yAxisWidth = textWidth > yAxisWidth ? textWidth : yAxisWidth;
        });
        */

      // calculate yaxis text max width
      //   const newNode = selection.selectAll('.y')._groups[0];
      //   yAxisWidth = newNode[0].getBBox().width - config.width - 3;
      //   if (config.yAxisType == 'text') yAxisWidth = newNode[0].getBBox().width;

      //   yAxis.tickSize(-config.width + yAxisWidth);
      // }

      selection
        .selectAll('.y')
        .style('display', config.showAxisY ? '' : 'none')
        .attr('transform', 'translate(' + (toPx(config.margin.left) + yAxisWidth) + ',' + toPx(config.margin.top) + ')')
        .attr('font-size', fontSize)
        .transition()
        .duration(function(d, i) {
          return duration;
        })
        .attr('opacity', 1)
        .call(yAxis);
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

var xAxis = function(mode) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    viewRatio: 1,
    rotateAxis: false,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  function chart(selection) {
    selection.each(function(data, i) {
      var xAxis = selection.selectAll('.x').data([1]);
      var fontSize = config.fontSize || Math.max(config.width / 40, 8) * config.viewRatio;

      xAxis
        .enter()
        .append('g')
        .attr('class', 'x axis')
        .merge(xAxis)
        .attr('transform', 'translate(' + 0 + ',' + config.height + ')')
        .style('display', config.showAxisX ? '' : 'none')
        .call(
          d3
            .axisBottom(config.xScale)
            .ticks(2)
            .tickSize(-config.svgHeight) // .tickSizeInner(fontSize * 5)
            // .tickPadding(fontSize * 5)
            .tickFormat((d, i) => {
              if (mode == 'simple') {
                return i % 2 == 1 ? '' : d;
              } else if (mode === 'heatmap') {
                return i % 6 == 0 ? d : '';
              } else if (mode === 'column') {
                if (d.length < 4) {
                  return d.toUpperCase();
                } else {
                  return d.slice(0, 3) + '..';
                }
              } else {
                let arrLen = data.length;
                if (data && data.constructor === Array && data[0] && data[0].constructor === Array) {
                  arrLen = data[0].length;
                }
                // arrLen = arrLen && arrLen < 8 ? arrLen : 8;

                /*
                Following code is to limit the text-length & number of labels, displayed in xAxis to prevent their overlap
                */
                const _labelWidth = config.xAxisLabelWidth != null ? config.xAxisLabelWidth : fontSize * 6.5; // Approximate length of the label.
                const _maxTicks = Math.floor(config.width / _labelWidth); // Max number of labels that can be displayed for current graph width.
                const _i = Math.ceil(arrLen / _maxTicks) || 1;

                if (i % _i) {
                  return ''; // Hide '_i' th label
                }

                // TODO: if _maxTicks is 2, show first and last label

                // If there is space for additional characters, add it
                let _extraChar = Math.floor(6 * (1 - ((arrLen / _maxTicks) % 1)));
                if (i >= arrLen - 1) {
                  _extraChar = 0;
                }
                const _maxLabelLength = 8 + _extraChar;

                if (d.toString().length >= _maxLabelLength) {
                  return d.toString().slice(0, _maxLabelLength - 1) + '..';
                }
                return d;
              }
            })
        )
        .selectAll('text')
        .attr('y', function() {
          if (config.timeLinegrid === 'timeLineGrid') {
            return fontSize * 2.5;
          } else {
            return fontSize;
          }
        })
        .attr('font-size', fontSize);

      var maxLength = 0;
      data.map(function(d) {
        if (d.label) {
          maxLength = maxLength > d.label.length ? maxLength : d.label.length;
        }
      });

      selection
        .selectAll('.x')
        .selectAll('line')
        .attr('y2', -config.graphHeight * 0.99)
        .attr('transform', `translate(${config.xScale.step() / 2 - 1}, ${config.svgHeight - config.height})`);

      // selection.selectAll('.x').attr('transform', 'translate(' + 0 + ',' + (config.height + config.width / 50) + ')');

      var disableRotate = true;
      if (!disableRotate && ((config.widgetType === 'l2' && maxLength > 11) || config.rotateAxis)) {
        selection
          .selectAll('.x')
          .selectAll('text')
          .attr('text-anchor', 'end')
          .attr('style', 'transform: rotate(-25deg)');
        selection
          .selectAll('.x')
          .attr('transform', 'translate(' + config.width / 70 + ',' + (config.height + config.width / 70) + ')');
      }
    });

    return selection;
  }

  //   Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

