import * as d3 from 'd3';
import MRParseColors from '../../mr-parsecolor';
import Utils from '../utils';
import getGraphCenter from './get-graph-center';

import { color } from 'd3';

var gaugeMeterTheme2 = function(preferences, widgetPreview) {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    offsetAngle: -50,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color5'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };
  function deg2Rad(deg) {
    return (deg * Math.PI) / 180;
  }

  // percentage to radians, (rotate by -90 deg)
  function calcAngle(percentage) {
    const totalAngle = 180 + config.arcAngle * 2;
    return (deg2Rad(percentage * 1.8) * totalAngle) / 180 - (deg2Rad(90) * totalAngle) / 180;
  }

  // percentage to degrees
  function percToDeg(percentage) {
    const totalAngle = 180 + config.arcAngle * 2;
    return (percentage * 1.8 * totalAngle) / 180 - config.arcAngle;
  }

  function xy(x, y) {
    return x - config.cX + ',' + (y - config.cY);
  }

  // draw the graph here
  function chart(selection) {
    selection.each(function(data, i) {
      if (data == undefined) {
        data = {};
      }
      var arc1, arc2, arc3, innerBarWidth;

      var fx = 3.8;
      this.easing = function(t) {
        return (1 - 1 / t) * 0.04 * fx * Math.sin((25 / fx) * t) + 1;
      };
      this.easing = d3.easeQuad;

      data.safeValueData = parseInt(data.safeValueData);
      var safeValue = data.safeValueData;
      if (safeValue > 100) {
        safeValue = 100;
      } else if (safeValue < 0) {
        safeValue = 0;
      }
      data.safeValue = safeValue;

      // target Value
      if (data.target && isNaN(Number(data.target))) {
        data.target = 0;
      }
      var safeTarget = data.target;
      if (safeTarget > 100) {
        safeTarget = 100;
      } else if (safeTarget < 0) {
        safeTarget = 0;
      }
      data.safeTarget = safeTarget;

      // calculate center position of the graph
      var { centerX, centerY } = getGraphCenter(config);
      var cX = centerX;
      var cY = centerY;
      config.cX = cX;
      config.cY = cY;

      var slices = selection.selectAll('.slices').data([1]);
      slices.exit().remove();
      slices
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slices)
        .attr('transform', 'translate(' + cX + ',' + cY + ')');

      var svg = selection.selectAll('.slices');
      var defs = svg.append('defs');
      var lg = defs
        // .append('svg:fill')
        .append('linearGradient')
        .attr('x1', '0%')
        .attr('x2', '100%')
        .attr('y1', '0%')
        .attr('y2', '0%')
        .attr('id', 'mygrad');

      lg.append('stop')
        .attr('offset', '0%')
        .style('stop-color', '#D9DEE5') //end in red
        .style('stop-opacity', 1);

      lg.append('stop')
        .attr('offset', '100%')
        .style('stop-color', 'white') //start in blue
        .style('stop-opacity', 1);

      var arcEmpty = selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .data([1]);
      arcEmpty.exit().remove();
      if (config.showBackground) {
        arcEmpty
          .enter()
          .append('path')
          .attr('class', 'arc empty')
          .merge(arcEmpty)
          .attr('stroke', '#eef1f6')

          .attr('stroke-width', config.barWidth)
          .attr('stroke-dasharray', '2 3');
      }
      //Traget DOT
      if (data.target) {
        var targetDot = selection.selectAll('.dot.target').data([data]);
        targetDot ? targetDot.exit().remove() : null;
        targetDot
          .enter()
          .append('circle')
          .attr('class', 'dot target')
          .attr('cx', cX)
          .attr('cy', cY)
          .attr('r', '4px')

          .merge(targetDot)

          .transition()
          .duration(config.duration)
          .ease(this.easing)
          .style('fill', function(d, i) {
            return MRParseColors(config.graphColors[0] || '#7eac50');
          })
          .attrTween('transform', function(d, i) {
            const interpolate = d3.interpolate(prevData.safeTarget || 0, data.safeTarget);
            return function(t) {
              const val = interpolate(t);
              const angle = parseFloat(percToDeg(val));
              const angletoRad = (angle * Math.PI) / 180;
              const cos = Math.cos(angletoRad);
              const sin = Math.sin(angletoRad);
              const hypo = config.radius + config.barWidth / 2 + 10;

              return `translate(${hypo * -cos},${hypo * -sin})`;
            };
          });
        //Target DOt end
      }
      d3.selectAll('.dot.target')
        .append('title')
        .text(function(d) {
          return `Target-${d.value}`;
        });

      arc1 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius);
      arc2 = d3
        .arc()
        .outerRadius(config.radius)
        .innerRadius(config.radius);
      arc3 = d3
        .arc()
        .outerRadius(innerBarWidth + config.innerRadius)
        .cornerRadius(Utils.cornerRadius(config.innerRadius, config.roundedCorner))
        .innerRadius(config.innerRadius);
      this.calcAngle = calcAngle;

      // Draw empty arc
      arc3.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc2.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc1.startAngle(calcAngle(0));
      selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .attr('d', arc2);
      selection
        .selectAll('.slices')
        .selectAll('.arc.inner')
        .attr('d', arc3);

      // set prevData for animation purpose
      // auto-detect if we need loading animation or transition animation
      var isRedraw = selection.selectAll('.slices').selectAll('.arc.filled')._groups[0].length > 0;
      var prevData = isRedraw
        ? selection
            .selectAll('.slices')
            .selectAll('.arc.filled')
            .datum()
        : { value: 0, safeValue: 0 };
      var isResize = prevData == data;

      // do not animate when resizing
      if (!isResize) {
        config.animate = true;
      }

      // draw filled arc
      var arcFilled = selection
        .selectAll('.slices')
        .selectAll('.arc.filled')
        .data([data]);
      arcFilled.exit().remove();

      arcFilled
        .enter()
        .append('path')
        .attr('class', 'arc filled')
        .merge(arcFilled)
        .attr('stroke', d => MRParseColors(d.color || config.graphColors[0]))
        .attr('stroke-width', config.barWidth)
        .attr('stroke-dasharray', '2 3')

        .transition()
        .duration(config.duration)
        .ease(this.easing)
        .attrTween('d', function(d, i) {
          var interpolate = d3.interpolate(prevData.safeValue, d.safeValue);
          return function(t) {
            var val = interpolate(t);
            arc1.endAngle(calcAngle(val));
            return arc1(interpolate(t));
          };
        });

      // draw pointer
      var pointerWidth = config.radius / 18;
      var pointerHeight = pointerWidth * 2.5;

      var pointer = selection.selectAll('.pointer').data([1]);
      pointer.exit().remove();

      // only animate when data changed, do not animate when resizing
      if (config.animate) {
        pointer
          .enter()
          .append('rect')
          .attr('class', 'pointer')
          .merge(pointer)
          .attr('fill', '#eef1f6')
          .attr('width', config.innerRadius)
          .attr('height', '6')
          .attr('x', -config.innerRadius - 5)
          .style('fill', 'url(#mygrad)')

          .transition()
          .duration(config.duration)
          .ease(this.easing)
          .attrTween('transform', function(d, i) {
            var interpolate = d3.interpolate(prevData.safeValue, data.safeValue);
            return function(t) {
              var val = interpolate(t);
              var angle = parseFloat(percToDeg(val));
              return 'translate(' + cX + ',' + cY + ') rotate(' + angle + ')';
            };
          });
      } else {
        pointer
          .enter()
          .append('path')
          .attr('class', 'pointer')
          .merge(pointer)
          .attr('transform', function(d, i) {
            var angle = parseFloat(percToDeg(data.safeValue));
            return 'translate(' + cX + ',' + cY + ') rotate(' + angle + ')';
          })
          .attr('fill', MRParseColors('colorneutral'))
          .attr(
            'd',
            [
              'M',
              xy(cX, cY + pointerWidth),
              xy(cX - config.radius / 2.8 - pointerHeight, cY),
              xy(cX, cY - pointerWidth),
              'z',
            ].join(' ')
          );
      }

      return selection;
    });
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    config.barWidth = config.barWidth || config.radius * 0.2;
    config.offsetAngle = config.arcAngle != null ? -config.arcAngle : config.offsetAngle;

    config.radius = config.radius - config.barWidth / 2;

    config.innerRadius = config.radius - config.barWidth * 1.8;
    config.graphValMaxWidth = config.innerRadius * 1.2;

    return chart;
  };

  return chart;
};

var gaugeMeter = function(preferences, widgetPreview) {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    offsetAngle: -50,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color5'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // deg to radians
  function deg2Rad(deg) {
    return (deg * Math.PI) / 180;
  }

  // percentage to radians, (rotate by -90 deg)
  function calcAngle(percentage) {
    const totalAngle = 180 + config.arcAngle * 2;
    return (deg2Rad(percentage * 1.8) * totalAngle) / 180 - (deg2Rad(90) * totalAngle) / 180;
  }
  // percentage to degrees
  function percToDeg(percentage) {
    const totalAngle = 180 + config.arcAngle * 2;
    return (percentage * 1.8 * totalAngle) / 180 - config.arcAngle;
  }
  function xy(x, y) {
    return x - config.cX + ',' + (y - config.cY);
  }

  // draw the graph here
  function chart(selection) {
    selection.each(function(data, i) {
      if (data == undefined) {
        data = {};
      }
      var arc1, arc2, arc3, innerBarWidth;

      var fx = 3.8;
      this.easing = function(t) {
        return (1 - 1 / t) * 0.04 * fx * Math.sin((25 / fx) * t) + 1;
      };
      this.easing = d3.easeQuad;

      data.safeValueData = parseInt(data.safeValueData);
      var safeValue = data.safeValueData;
      if (safeValue > 100) {
        safeValue = 100;
      } else if (safeValue < 0) {
        safeValue = 0;
      }
      data.safeValue = safeValue;

      var innerBarWidth = toPx(0.2);

      // calculate center position of the graph
      var { centerX, centerY } = getGraphCenter(config);
      var cX = centerX;
      var cY = centerY;
      config.cX = cX;
      config.cY = cY;

      var slices = selection.selectAll('.slices').data([1]);
      slices.exit().remove();
      slices
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slices)
        .attr('transform', 'translate(' + cX + ',' + cY + ')');

      var arcInner = selection
        .selectAll('.slices')
        .selectAll('.arc.inner')
        .data([1]);
      arcInner.exit().remove();
      arcInner
        .enter()
        .append('path')
        .attr('class', 'arc inner')
        .merge(arcInner)
        .attr('fill', MRParseColors('colorneutral'));

      var arcEmpty = selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .data([1]);
      arcEmpty.exit().remove();

      if (config.showBackground) {
        arcEmpty
          .enter()
          .append('path')
          .attr('class', 'arc empty')
          .merge(arcEmpty)
          .attr('fill', MRParseColors('colorneutral'));
      }
      arc1 = d3
        .arc()
        .outerRadius(config.radius)
        .cornerRadius(Utils.cornerRadius(config.barWidth, config.roundedCorner))
        .innerRadius(config.radius - config.barWidth);
      arc2 = d3
        .arc()
        .outerRadius(config.radius)
        .cornerRadius(Utils.cornerRadius(config.barWidth, config.roundedCorner))
        .innerRadius(config.radius - config.barWidth);
      arc3 = d3
        .arc()
        .outerRadius(innerBarWidth + config.innerRadius)
        .cornerRadius(Utils.cornerRadius(config.innerRadius, config.roundedCorner))
        .innerRadius(config.innerRadius);
      this.calcAngle = calcAngle;

      // Draw empty arc
      arc3.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc2.startAngle(calcAngle(0)).endAngle(calcAngle(100));
      arc1.startAngle(calcAngle(0));
      selection
        .selectAll('.slices')
        .selectAll('.arc.empty')
        .attr('d', arc2);
      selection
        .selectAll('.slices')
        .selectAll('.arc.inner')
        .attr('d', arc3);

      // set prevData for animation purpose
      // auto-detect if we need loading animation or transition animation
      var isRedraw = selection.selectAll('.slices').selectAll('.arc.filled')._groups[0].length > 0;
      var prevData = isRedraw
        ? selection
            .selectAll('.slices')
            .selectAll('.arc.filled')
            .datum()
        : { value: 0, safeValue: 0 };
      var isResize = prevData == data;

      // do not animate when resizing
      if (!isResize) {
        config.animate = true;
      }

      // draw filled arc
      var arcFilled = selection
        .selectAll('.slices')
        .selectAll('.arc.filled')
        .data([data]);
      arcFilled.exit().remove();
      arcFilled
        .enter()
        .append('path')
        .attr('class', 'arc filled')
        .merge(arcFilled)
        .attr('fill', MRParseColors(config.graphColors[0]))
        .transition()
        .duration(config.duration)
        .ease(this.easing)
        .attrTween('d', function(d, i) {
          var interpolate = d3.interpolate(prevData.safeValue, d.safeValue);
          return function(t) {
            var val = interpolate(t);
            arc1.endAngle(calcAngle(val));
            return arc1(interpolate(t));
          };
        });

      // draw pointer
      var pointerWidth = Math.min(config.radius / 18, config.barWidth * 0.4);
      var pointerHeight = pointerWidth * 2.5;

      var pointer = selection.selectAll('.pointer').data([1]);
      pointer.exit().remove();

      // only animate when data changed, do not animate when resizing
      if (config.animate) {
        pointer
          .enter()
          .append('path')
          .attr('class', 'pointer')
          .merge(pointer)
          .attr('fill', '#e3e3e3')
          .attr(
            'd',
            [
              'M',
              xy(cX - config.innerRadius, cY + pointerWidth),
              xy(cX - config.innerRadius - pointerHeight, cY),
              xy(cX - config.innerRadius, cY - pointerWidth),
              'z',
            ].join(' ')
          )
          .transition()
          .duration(config.duration)
          .ease(this.easing)
          .attrTween('transform', function(d, i) {
            var interpolate = d3.interpolate(prevData.safeValue, data.safeValue);
            return function(t) {
              var val = interpolate(t);
              var angle = parseFloat(percToDeg(val));
              return 'translate(' + cX + ',' + cY + ') rotate(' + angle + ')';
            };
          });
      } else {
        pointer
          .enter()
          .append('path')
          .attr('class', 'pointer')
          .merge(pointer)
          .attr('transform', function(d, i) {
            var angle = parseFloat(percToDeg(data.safeValue));
            return 'translate(' + cX + ',' + cY + ') rotate(' + angle + ')';
          })
          .attr('fill', MRParseColors('colorneutral'))
          .attr(
            'd',
            [
              'M',
              xy(cX - config.innerRadius, cY + pointerWidth),
              xy(cX - config.innerRadius - pointerHeight, cY),
              xy(cX - config.innerRadius, cY - pointerWidth),
              'z',
            ].join(' ')
          );
      }

      return selection;
    });
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    config.barWidth = config.barWidth || config.radius * 0.2;
    config.offsetAngle = config.arcAngle != null ? -config.arcAngle : config.offsetAngle;

    config.innerRadius = config.radius - config.barWidth * 1.8;
    config.graphValMaxWidth = config.innerRadius * 1.2;
    return chart;
  };

  return chart;
};

export { gaugeMeter, gaugeMeterTheme2 };
