const digitaliseData = function(ele, data, prefix, font, unit) {
  let fontunit = '',
    unitfont;
  let unitText;

  if (font <= 15) {
    unitfont = parseFloat(font) * 0.83;
  } else unitfont = parseFloat(font) * 0.7;

  if (font && font.toString().includes('em')) {
    fontunit = 'em';
    unitfont = parseFloat(font) * 0.5;
  }
  if (font && font.toString().includes('px')) {
    fontunit = 'px';
    unitfont = parseFloat(font) * 0.6;
  }

  if (data < 0) {
    unitText = ele
      .text('(')
      .style('font-size', font)
      .style('fill', 'red')
      .attr('font-weight', '300')
      .attr('font-family', 'Rubik')
      .append('tspan')
      .text(function(d) {
        return ` ${prefix}${-data}`;
      })
      .append('tspan')
      .text(')');
  } else {
    unitText = ele
      .text(function(d) {
        return ` ${prefix}${data}`;
      })
      .style('font-size', font)
      .attr('font-weight', '300')
      .attr('font-family', 'Rubik')
      .append('tspan')
      .text(unit)
      .style('font-size', `${unitfont}${fontunit}`)
      .style('fill', '#737b86');
  }

  return unitText;
};
export default digitaliseData;
