import React from 'react';
import './default-tooltip.css';

import { toObject } from '../GraphLibrary/Number';

const converToNumber = (num, round = 2) => {
  if (Number.isNaN(Number(num))) {
    return '-';
  }
  return Number(num).toFixed(round);
};

const getFormattedValue = (value, config = {}) => {
  const num = {
    value: value,
    suffix: config.valueSuffix,
    prefix: config.valuePrefix,
  };

  const options = {
    ...config,
    compactDisplay: 'short',
  };
  return toObject(num, options);
};

import Odometer from '../cm-odometer/index';
const SingleValue = props => {
  const value = getFormattedValue(props.data.value, props.config);
  return (
    <React.Fragment>
      <div className="tooltip-row label">{props.data.label}</div>
      <div className="tooltip-row value-wrapper">
        <div className="units tooltip-prefix">{value.prefix || ''}</div>
        <div className="tooltip-odometer">
          {props.config.hideOdometer ? (
            // converToNumber(props.data.value)
            `${value.value}${value.scale}`
          ) : (
            <Odometer format="(,ddd).ddd" duration={200} value={converToNumber(props.data.value)}></Odometer>
          )}
        </div>
        <div className="units tooltip-suffix" style={{ fontSize: '0.7em', paddingTop: '0.2em' }}>
          {value.suffix || ''}
        </div>
      </div>
      <div className="tooltip-row " style={{ fontWeight: 400 }}>
        {props.data.subLabel}
      </div>
    </React.Fragment>
  );
};

const MultipleValues = props => (
  <React.Fragment>
    <div className="tooltip-row label">{props.data.label}</div>
    <div className="tooltip-row value-wrapper ">
      <div>
        {props.data.value.map(item => {
          const value = getFormattedValue(item.value, props.config);

          return (
            <div
              style={{
                fontWeight: 500,
                width: '100%',
                display: 'flex',
                alignItems: 'center',
                color: 'var(--themeblack)',
                lineHeight: '1em',
              }}
            >
              <div
                style={{ textAlign: 'left', flexGrow: 1, paddingRight: '1em', fontWeight: 300, fontSize: '0.8em' }}
                className="inner-label"
              >{`${item.label}`}</div>
              <div>{value.prefix || ''}</div>
              {/* <div className="tooltip-odometer value">{converToNumber(item.value)}</div> */}
              <div className="tooltip-odometer value">{`${value.value}${value.scale}`}</div>
              <div style={{ fontSize: '0.7em', paddingTop: '0.2em' }}>{value.suffix}</div>
            </div>
          );
        })}
      </div>
    </div>
    <div className="tooltip-row sublabel " style={{ fontWeight: 400 }}>
      {props.data.subLabel}
    </div>
  </React.Fragment>
);

export default props => {
  if (Array.isArray(props.data.value)) {
    return <MultipleValues data={props.data || []} config={props.config}></MultipleValues>;
  }
  return <SingleValue data={props.data} config={props.config}></SingleValue>;
};
