import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import MRFormatter from '../mr-formatter';

var circlesStraightPack = function() {
  var config = {
    width: 0,
    height: 0,
    animate: true,
    graphOffset: 0,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return Math.max(config.width / 35, 15) * em;
  };

  // draw the graph here
  function chart(selection) {
    var t = d3.transition().duration(duration);
    var duration = config.duration;
    duration = config.animate ? 400 : 500;
    var minRadius = config.fontSize * 1.2;
    if (config.boxtype === 'slim' || config.boxtype === 'tall-rectangle') {
      selection.each((data, i) => {
        var svg = selection.selectAll('g');
        if (!data) {
          data = [];
        }
        var dropShadow = DropShadow().config({});
        selection.datum([1]).call(dropShadow);
        const arr = (data || []).map(function(d) {
          return parseFloat(d.value) || 0;
        });
        const length = arr.length;

        let maxRadius = d3.max(arr);

        var radiusScale = d3
          .scaleLinear()
          .domain([0, maxRadius])
          .range([minRadius, Math.min(config.height / (2 * length), config.width / 3)]);
        const t_length = arr.reduce((sum, i) => radiusScale(i) + sum, 0);
        const x = config.height / (2 * t_length);
        var radiusCoordinates = [];
        var displayText = [];
        data.forEach((item, i) => (item.index = i));

        var g = svg.data(data);
        g.exit().remove();

        var gmerge = g
          .enter()
          .append('g')
          .call(function(parent) {
            parent
              .append('circle')
              .attr('class', 'circle-bg')
              .attr('cursor', 'pointer')
              .attr('cx', config.width)
              .attr('r', 0);
            parent
              .append('g')
              .attr('class', 'group-circle-text')
              .attr('text-anchor', 'middle')
              .attr('transform', function(d, i) {
                radiusCoordinates[i] = radiusScale(d.value) * x;
                let sum = 0;
                for (let j = 0; j <= i; j++) {
                  sum = sum + radiusCoordinates[j] + (j > 0 ? radiusCoordinates[j - 1] : 0);
                }
                return 'translate(' + config.width / 2 + ',' + sum + ')';
              });
          })
          .merge(g);

        gmerge
          .select('.circle-bg')
          .attr('r', function(d, i) {
            radiusCoordinates[i] = radiusScale(d.value) * x;
            if (radiusScale(maxRadius) > config.width / 2) {
              return (radiusScale(d.value) * config.width) / (2 * radiusScale(maxRadius));
            }
            return radiusScale(d.value);
          })
          .attr('cx', function(d, i) {
            var ypos = config.width / 2 + toPx(config.margin.right);

            return ypos;
          })
          .attr('cy', function(d, i) {
            let sum = 0;
            for (let j = 0; j <= i; j++) {
              sum = sum + radiusCoordinates[j] + (j > 0 ? radiusCoordinates[j - 1] : 0);
            }
            return sum;
          })

          .style('fill', function(d, i) {
            return MRParseColors(config.colors[i % 8]);
          });

        var text = gmerge.select('.group-circle-text');

        text
          .append('text')
          .attr('dy', '-0.2em')
          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style('font-weight', '400')
          .style('font-size', function(d, i) {
            displayText[i] = getFontSize(d.label, 2 * d.r);
            return displayText[i];
          })
          .text(function(dd) {
            const labelStr = getTrimmedLabel(dd.label, radiusScale(dd.value) * 2);
            return labelStr;
          })
          .transition()
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        text
          .append('text')
          .attr('dy', '1.0em')
          .text(function(d, i) {
            var format = MRFormatter.format(parseFloat(d.value), 'toNumber');
            return numberWithCommas(format.fullValue) + ' ' + format.unit;
          })
          .style('font-size', function(d, i) {
            return displayText[i];
          })
          .text(function(dd) {
            return dd.value;
          })
          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style('font-weight', '400')
          .transition(t)
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        function animate() {
          var labeltext = selection.selectAll('.gmerge');
          labeltext.each(function(d) {
            if (radiusScale(d.value) <= config.toPx(1)) {
              d3.select(this)
                .select('.group-circle-text')
                .style('opacity', '0');
            } else {
              if (radiusScale(d.value) > config.toPx(1)) {
                d3.select(this)
                  .select('.group-circle-text')
                  .style('opacity', '1');
              }
            }
          });
        }

        setTimeout(() => {
          animate();
        }, 10);
      });
      return selection;
    } else {
      selection.each((data, i) => {
        var svg = selection.selectAll('g');

        if (!data) {
          data = [];
        }

        var t = d3.transition().duration(duration);
        var duration = config.duration;
        duration = config.animate ? 400 : 0;
        var minRadius = config.fontSize * 1.2;
        var dropShadow = DropShadow().config({});
        selection.datum([1]).call(dropShadow);

        const arr = (data || []).map(function(d) {
          return parseFloat(d.value) || 0;
        });
        const length = arr.length;

        let maxRadius = d3.max(arr);
        var radiusScale = d3
          .scaleLinear()
          .domain([0, maxRadius])
          .range([minRadius, Math.min(config.width / (2 * length), config.height / 3)]);
        const t_length = arr.reduce((sum, i) => radiusScale(i) + sum, 0);
        const x = config.width / (2 * t_length);
        var radiusCoordinates = [];
        var displayText = [];
        data.forEach((item, i) => (item.index = i));
        var g = svg.data(data);
        g.exit().remove();

        var gmerge = g
          .enter()
          .append('g')
          .call(function(parent) {
            parent
              .append('circle')
              .attr('class', 'circle-bg')
              .attr('cursor', 'pointer')
              .attr('cy', config.height)
              .attr('r', 0);
            parent
              .append('g')
              .attr('class', 'group-circle-text')
              .attr('transform', function(d, i) {
                radiusCoordinates[i] = radiusScale(d.value) * x;
                let sum = 0;
                for (let j = 0; j <= i; j++) {
                  sum = sum + radiusCoordinates[j] + (j > 0 ? radiusCoordinates[j - 1] : 0);
                }
                return 'translate(' + sum + ',' + config.height / 2 + ')';
              });
          })
          .merge(g);

        gmerge
          .select('.circle-bg')
          .attr('r', function(d, i) {
            if (radiusScale(maxRadius) > config.height / 2) {
              return (radiusScale(d.value) * config.height) / (2 * radiusScale(maxRadius));
            }
            return radiusScale(d.value) * 0.9;
          })
          .attr('cx', function(d, i) {
            let sum = 0;
            for (let j = 0; j <= i; j++) {
              sum = sum + radiusCoordinates[j] + (j > 0 ? radiusCoordinates[j - 1] : 0);
            }
            return sum;
          })

          .attr('cy', function(d, i) {
            var ypos = config.height / 2 + toPx(config.margin.top);
            return ypos;
          })
          .style('fill', function(d, i) {
            return MRParseColors(config.graphColors[i % config.graphColors.length]);
          });

        var text = gmerge.select('.group-circle-text');

        text
          .append('text')
          .attr('dy', '-0.2em')

          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style('font-weight', '400')

          .style('font-size', function(d, i) {
            return getFontSize(d.label, 2 * d.r);
          })
          .text(function(dd) {
            const labelStr = getTrimmedLabel(dd.label, radiusScale(dd.value) * 2);
            return labelStr;
          })
          .transition()
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        text
          .append('text')

          .attr('dy', '1.0em')
          .text(function(d, i) {
            var format = MRFormatter.format(parseFloat(d.value), 'toNumber');
            return numberWithCommas(format.fullValue) + ' ' + format.unit;
          })
          .style('font-size', function(d, i) {
            return displayText[i];
          })
          .style('text-anchor', 'middle')
          .style('fill', 'white')
          .style('font-weight', '400')
          .transition(t)
          .duration((duration * 2) / 3)
          .ease(d3.easeSin)
          .attr('opacity', '1');

        function animate() {
          var labeltext = selection.selectAll('.gmerge');
          labeltext.each(function(d) {
            if (radiusScale(d.value) <= config.toPx(0.5)) {
              d3.select(this)
                .select('.group-circle-text')
                .style('opacity', '0');
            } else {
              if (radiusScale(d.value) > config.toPx(2)) {
                d3.select(this)
                  .select('.group-circle-text')
                  .style('opacity', '1');
              }
            }
          });
        }

        setTimeout(() => {
          animate();
        }, 10);
      });
      return selection;
    }
  }

  function getTrimmedLabel(labelStr, maxWidth) {
    const fontSize = getFontSize(labelStr, maxWidth);
    let maxLen = Number.POSITIVE_INFINITY;
    if (fontSize * labelStr.length > maxWidth) {
      maxLen = Math.floor(maxWidth / fontSize);
    }

    return maxLen < labelStr.length ? labelStr.substring(0, maxLen) + '..' : labelStr;
  }
  function getFontSize(text, maxWidth) {
    const scaledFontSize = maxWidth / (text.length + 2);
    const MAX_FONTSIZE = config.fontSize * 2;
    const MIN_FONTSIZE = config.fontSize * 0.75;

    return Math.min(Math.max(scaledFontSize, MIN_FONTSIZE), MAX_FONTSIZE);
  }

  function numberWithCommas(x) {
    var parts = x.toString().split('.');
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    return parts.join('.');
  }

  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };
  return chart;
};

export default circlesStraightPack;
