import * as d3 from 'd3';
import { Utils } from '../..';
import MRParseColors from '../../mr-parsecolor';
import getGraphCenter from './get-graph-center';

const concentricRings = function() {
  // let config = {};
  let config = {
    ...config,
    fontSize: this.fontSize,
    width: this.width,
    fontSize: 15,
    // graph height should be less than svgHeight so we can leave some space for legend group
    height: this.height,
    svgHeight: this.height,
    animDelay: 100,
    animDuration: 600,
    textWidth: 0,
    showTrend: false,
    cropTextLength: 8,

    ...this.preferences,
    arcAngleA: [],
  };
  function chart(selection) {
    selection.each((dataObject, i) => {
      // for loading animation (will not be used when redraw)
      function tweenRadius(r, t) {
        var dampingFactor = 3;
        var dampingFactor2 = 2;
        if (t < 0.25) {
          return r - (r * t) / dampingFactor;
        } else if (t < 0.5) {
          return r + (r * (t - 0.5)) / dampingFactor;
        } else if (t < 0.75) {
          return r + (r * (t - 0.5) * (t - 0.5)) / dampingFactor2;
        } else {
          return r + (r * (t - 1) * (t - 1)) / dampingFactor2;
        }
      }

      let data = dataObject;
      var duration = config.animate ? config.duration : 0;
      var { centerX, centerY } = getGraphCenter(config);
      var ringWidth =
        (config.innerRadius !== undefined ? config.radius - config.innerRadius : config.radius * 0.4) / data.length;
      var ringDistance = config.distanceBetweenRings || 2;
      var outR = config.radius - ringDistance;
      var outI = outR - ringWidth;
      var baseRings = config.showBackground !== undefined ? config.showBackground : true;

      let arcArray = [];
      let rDataArray = [];
      let rDataArrayBase = [];
      let outRArray = [];
      // adding baseRings
      var baseRingsGroup = selection.selectAll('.baseRings').data([1]);
      baseRingsGroup.exit().remove();
      baseRingsGroup
        .enter()
        .append('g')
        .attr('class', 'baseRings')
        .merge(baseRingsGroup)
        .attr('transform', 'translate(' + centerX + ',' + centerY + ')');
      var group = selection.selectAll('.labelGrp').data([1]);
      group.exit().remove();
      group
        .enter()
        .append('g')
        .attr('class', 'labelGrp')
        .merge(group)
        .attr('transform', 'translate(' + centerX + ',' + centerY + ')');

      data.forEach(function(item, i) {
        var arc = d3
          .arc()
          .cornerRadius(Utils.cornerRadius(ringWidth, config.roundedCorner))
          .outerRadius(Math.ceil(outR))
          .innerRadius(Math.floor(outI));
        arcArray.push(arc);
        var rData = [
          {
            on: true,
            val: config.scale(item['value']),
            label: item['label'],
            absoluteValue: item.value,
          },
          {
            on: false,
            val: 100 - config.scale(item['value']),
            label: item['label'],
            absoluteValue: item.value,
          },
        ];
        var rDataBase = [
          {
            on: true,
            val: config.scale(0),
          },
          {
            on: false,
            val: 100 - config.scale(0),
          },
        ];
        rDataArrayBase.push(config.pie(rDataBase)[1]);
        rDataArray.push(config.pie(rData)[0]);
        outRArray.push(outR);
        outR = outR - (ringWidth + ringDistance);
        outI = outR - ringWidth;
      });

      // adding baseRings
      if (baseRings) {
        const slices = baseRingsGroup.selectAll('.basePath').data(rDataArrayBase);

        slices.exit().remove();
        slices
          .enter()
          .append('path')
          .merge(slices)
          .attr('class', 'basePath') // On basepath enter
          .style('fill', '#DCDCDC')
          .attr('d', function(d, i) {
            if (!d.data.on) {
              return arcArray[i](d);
            } else {
              return arcArray[i](d);
            }
          });
      } else {
        baseRingsGroup.selectAll('.basePath').remove();
      }
      //  baseRings completed

      // adding value arcs

      const isRedraw = selection.selectAll('.slices')._groups[0].length > 0;
      // add slicesGrp
      var slicesGrp = selection.selectAll('.slices').data([1]);
      slicesGrp.exit().remove();
      slicesGrp
        .enter()
        .append('g')
        .attr('class', 'slices')
        .merge(slicesGrp)
        .attr('transform', 'translate(' + centerX + ',' + centerY + ')');

      var slices = selection
        .selectAll('.slices')
        .selectAll('path.slice')
        .data(rDataArray);

      // Remove extra slices
      slices.exit().remove();
      // Added slices
      slices
        .enter()
        .append('path')
        .style('fill', function(d, i) {
          return MRParseColors((d.data && d.data.labelColor) || config.graphColors[i % config.graphColors.length]);
        })
        .merge(slices)
        .attr('class', 'slice')
        .attr('pointer-events', 'all')
        .attr('opacity', config.opacity || 1)
        .attr('cursor', 'pointer');

      if (config.showSliceLabel) {
        var sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(rDataArray);
        sliceLabel.exit().remove();
        sliceLabel
          .enter()
          .append('text')
          .attr('class', 'slice-label')
          .attr('opacity', '0')
          .attr('transform', function(d, i) {
            var pos = arcArray[i].centroid(d);
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('pointer-events', 'none');

        sliceLabel = selection
          .selectAll('.slices')
          .selectAll('text.slice-label')
          .data(rDataArray)
          .attr('font-size', config.arcFontSize || config.radius / 10)
          .attr('text-anchor', 'middle')
          .attr('pointer-events', 'none')
          .attr('fill', 'white')
          .text(function(d) {
            if (Math.abs(d.endAngle - d.startAngle) < 0.5) {
              return '';
            }
            var percentage = (((d.endAngle - d.startAngle) / Math.PI / 2) * 100).toFixed(0) + '%';
            return percentage;
          })
          .attr('opacity', function() {
            return config.animate ? 0 : 1;
          })
          .transition()
          .duration(duration)
          .attr('transform', function(d, i) {
            var pos = arcArray[i].centroid(d);
            if (d.endAngle - d.startAngle > 6.283185) return 'translate(' + 0 + ',' + 0 + ')';
            return 'translate(' + pos[0] + ',' + (pos[1] + toPx(0.2)) + ')';
          })
          .attr('opacity', '1');
      }

      // add loading animation / redraw animation
      requestAnimationFrame(() => {
        var slices = selection.selectAll('.slices').selectAll('path.slice');
        let endAngle = 0;
        slices
          .transition()
          .ease(d3.easeCubicOut)
          .duration(function(d, i) {
            // loading animation duration
            let _duration = 0;
            if (!isRedraw) {
              // if (config.animate === true) {
              _duration = (duration + i * 0.095 * duration) * 1.8;
              // redraw animation duration
            } else {
              _duration = duration * 1.3;
            }
            return _duration;
          })
          // animation for slices

          .attrTween('d', function(d, i) {
            const defaultVal = {
              endAngle: d.startAngle,
              startAngle: d.startAngle,
            };
            const _current = this._current || defaultVal;
            // endAngle = d.endAngle;

            var interpolate = d3.interpolate(_current, d);

            this._current = interpolate(0);

            return function(t) {
              // loading animation
              if (!isRedraw) {
                // if (config.animate === true) {
                arcArray[i].outerRadius(function() {
                  return tweenRadius(config.radiusDefault, t);
                });
              }
              // redraw animation
              return arcArray[i](interpolate(t));
            };
          });
      });
      // adding value arcs

      // legends next to rings for concentric Rings

      if (config.showLegends && !(config.arcAngle && config.arcAngle > 1)) {
        const label = group.selectAll('.textStyle').data(rDataArray);
        label.exit().remove();

        label
          .enter()
          .append('text')
          .attr('class', 'textStyle')
          .merge(label)
          .attr('transform', 'translate(0,0)')
          .attr('text-anchor', 'end')
          .text(function(d, i) {
            var croppedLable = d.data['label'];

            var valueText = ` : ${d.data.val.toFixed(2)}%`;
            var cropTextLength = Math.ceil(config.cropTextLength / config.viewRatio);
            cropTextLength = cropTextLength * 2.5 - valueText.length;
            if (croppedLable.length > cropTextLength) {
              croppedLable = croppedLable.slice(0, cropTextLength) + '..';
            }
            return croppedLable + valueText;
          })
          .attr('font-size', function() {
            var fontSize = Math.max(ringWidth * 0.43 * config.viewRatio, 9);
            return fontSize;
          })

          .attr('transform', function(d, i) {
            var fontSize = Math.max(ringWidth * 0.5 * config.viewRatio, 9);
            var tY = outRArray[i] - ringWidth * 0.6 + fontSize * 0.5;
            return 'translate(-' + ringWidth * 0.5 + ',' + tY + ')';
          });
      }
    });
    // legends next to rings for concentric Rings

    return selection;
  }

  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };
  return chart;
};

export default concentricRings;
