import * as d3 from 'd3';

export default function() {
  let size = [1, 1],
    value = function(d) {
      return d.value;
    },
    coordinates;

  let percentageValues = function(data) {
    let values = data.map(value);
    let sum = d3.sum(values, d => {
      return +d;
    });
    let percentValues = data.map((d, i) => {
      d.value = +values[i];
      return (values[i] / sum) * 100;
    });
    percentValues.sort((a, b) => {
      return b - a;
    });
    return percentValues;
  };
  let coordinatesCalculation = function(data) {
    let w = size[0],
      h = size[1],
      ratio = w / 2 / h,
      percentValues = percentageValues(data),
      coordinates = [],
      area_of_triangle = (w * h) / 2;
    function d3Sum(i) {
      return d3.sum(percentValues, (d, j) => {
        if (j >= i) {
          return d;
        }
      });
    }
    for (let i = 0, len = data.length; i < len; i++) {
      let selectedPercentValues = d3Sum(i),
        area_of_element = (selectedPercentValues / 100) * area_of_triangle,
        height1 = Math.sqrt(area_of_element / ratio),
        base = 2 * ratio * height1,
        xwidth = (w - base) / 2;
      if (i === 0) {
        coordinates[i] = { values: [{ x: w / 2, y: 0 }, { x: xwidth, y: height1 }, { x: base + xwidth, y: height1 }] };
      } else {
        coordinates[i] = {
          values: [
            { ...coordinates[i - 1].values[1] },
            { x: xwidth, y: height1 },
            { x: base + xwidth, y: height1 },
            { ...coordinates[i - 1].values[2] },
          ],
        };
      }
    }
    coordinates[0].values[1] = coordinates[coordinates.length - 1].values[1];
    coordinates[0].values[2] = coordinates[coordinates.length - 1].values[2];
    let first_data = coordinates.splice(0, 1);
    coordinates = coordinates.reverse();
    coordinates.splice(0, 0, first_data[0]);
    let xspace = 2 / 1.73;
    const deepCopyedCoordinates = JSON.parse(JSON.stringify(coordinates));
    deepCopyedCoordinates.forEach((item, i) => {
      if (i >= coordinates.length - 1) {
        return;
      }
      if (i != 0) {
        item.values[0].y -= 2;
        item.values[3].y -= 2;
        item.values[0].x += xspace;
        item.values[3].x -= xspace;
      } else if (i === 0) {
        item.values[1].y -= 2;
        item.values[2].y -= 2;
        item.values[1].x += xspace;
        item.values[2].x -= xspace;
      }
    });

    return deepCopyedCoordinates;
  };
  function pyramid(data) {
    let i = 0,
      coordinates = coordinatesCalculation(data);

    data.sort((a, b) => {
      return a.value - b.value;
    });

    data.forEach(() => {
      data[i].coordinates = coordinates[i].values;
      i++;
    });
    return data;
  }
  pyramid.size = function(s) {
    if (s.length === 2) {
      size = s;
    }
    return pyramid;
  };
  pyramid.value = function(v) {
    if (!arguments.length) return value;
    value = v;
    return pyramid;
  };
  return pyramid;
}
