import * as d3 from 'd3';
import getGraphConfigurations from '../../Graph-Configuration';
import { CatameraGraph, MRFormatter } from '../../GraphLibrary';
import ToolTip from '../../tooltip';
import './cm-variable-columns.element.css';
import withGraphComponent from '../../Graph-wrapper-HOC';

let toolTipClass;

const getxScaleObject = (data, config) => {
  // creates object which maps label index to corresponding x-coordinate
  const xScaleObj = {};
  let prevOffset = config.xAxisOffset || 40;
  for (let index = 0; index < data.length; index++) {
    if (index === 0) {
      xScaleObj[index] = prevOffset;
    } else {
      prevOffset = xScaleObj[index - 1];
      let previousWidth = config.barWidth * data[index - 1].values.length;
      xScaleObj[index] = prevOffset + previousWidth + config.gutterspace;
    }
  }
  return xScaleObj;
};
const getOpacityGradient = (data, config) => {
  // creates object which maps label index to corresponding x-coordinate
  const xScaleOpacityGradient = {};

  data.forEach((item, i) => {
    xScaleOpacityGradient[i] = 0.5 / item.values.length;
  });
  return xScaleOpacityGradient;
};

class CMVariableColumnsGraph extends CatameraGraph.GraphTooltip(CatameraGraph.d3baseGraph) {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMVariableColumnsGraph';
    toolTipClass = new ToolTip($ele);
  }

  setData(data) {
    this.data = data.data ? data.data : data;

    const normalize = this.preferences.normalize || false;
    if (normalize && this.data.length) {
      //passing the non-normalised, non-grouped  data so as to be normalized according to the height and width
      this.data = this.normalizeData(this.data);
    }
    this.summary = data.summary ? data.summary : '';
    this.labels = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    this.dataType = '2D';
    this.processData();
  }

  processData() {
    const self = this;

    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (var i = 0; i < self.labels.length; i++) {
        if (!d[self.labels[i].value] && !d[self.labels[i].value] === 0) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });

    var formattedData = [];
    var item = {};
    // process data object with multiple values
    for (var k = 0; k < this.labels.length; k++) {
      var items = [];
      for (var i = 0; i < this.filteredData.length; i++) {
        item = {
          label: this.filteredData[i].label,
          value: this.filteredData[i][this.labels[k].value],
          labelText: this.labels[k].label,
        };
        items.push(item);
      }
      formattedData.push(items);
    }
    this.graphData = formattedData;
    formattedData = [];
    this.filteredData.map((dataset, i) => {
      let item = {
        label: dataset.label,
        values: [],
      };

      for (var key in dataset) {
        if (dataset.hasOwnProperty(key) && key != 'label') {
          let subItem = {
            label: key,
            value: dataset[key],
            sign: this.filteredData[i].sign,
          };
          item.values.push(subItem);
        }
      }
      formattedData.push(item);
    });

    this.newFormatedData = formattedData;
  }

  render(animate = true) {
    super.render(animate);
    let config = getGraphConfigurations.call(this);

    var maxVal = 0;
    var minVal = 0;
    var maxTotalVal = 0;
    if (this.graphData[0] && typeof this.graphData[0] == 'object') {
      for (var i = 0; i < this.graphData.length; i++) {
        var newMaxVal = d3.max(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.value)) ? parseFloat(d.value) : 0;
        });
        var newMinVal = d3.min(this.graphData[i], function(d) {
          return !Number.isNaN(Number(d.value)) ? parseFloat(d.value) : 0;
        });
        minVal = Math.min(newMinVal, minVal);
        maxVal = newMaxVal > maxVal ? newMaxVal : maxVal;
      }
    }

    let calculatedminvalue = minVal;
    maxVal = Math.max(
      Math.ceil(maxVal * config.yAxisDomainFactor),
      Math.ceil(maxTotalVal * config.yAxisDomainFactor),
      this.yDomainMin[1]
    );

    minVal = Math.min(minVal * config.yAxisDomainFactor, this.yDomainMin[1]);

    const { range } = config;
    const yAxisWidth = this.getYAxisWidth(maxVal, config);

    // Set Scales
    if (minVal < 0) {
      config.negativeAxis = true;
    }

    // Configuration to adjust the range of max value
    maxVal *= config.maxRangeMultiplier || 1.1;
    minVal *= config.minRangeMultiplier;

    var yScale = d3
      .scaleLinear()
      .range([range.top, config.height - range.bottom])
      .domain([maxVal, minVal]);

    if (config.percentageBased) {
      yScale = d3
        .scaleLinear()
        .range([range.top, config.height - range.bottom])
        .domain([100, this.yDomainMin[0]]);
    }
    // config for graphs
    config = {
      ...config,
      yScale: yScale,
      yAxisWidth,
      calculatedminvalue,
      range,
      gutterspace: (this.preferences && parseInt(this.preferences.gutterspace)) || 30,
    };

    const maxTextSize = Math.max(maxVal.toString().length, 3);
    config.xAxisOffset = Math.max(+range.left || 0, config.yAxisWidth || config.fontSize * maxTextSize);
    let totalBars = 0;
    this.newFormatedData.forEach(dataset => {
      totalBars = totalBars + dataset.values.length;
    });
    config.barWidth =
      (this.width - (config.gutterspace * (this.newFormatedData.length - 2) + (config.xAxisOffset * 2 || 0))) /
      totalBars;
    config.xscaleobj = getxScaleObject(this.newFormatedData, config);
    config.xScaleOpacityGradient = getOpacityGradient(this.newFormatedData, config);

    // y-axis
    var mode = '';
    this.newFormatedData = this.mapLabelsToColors(this.newFormatedData);
    this.labels = this.mapLabelsToColors(this.labels);

    var yAxis = CatameraGraph.yAxis(mode).config(config);
    this.$baseGrp.datum(this.filteredData).call(yAxis);

    var bars = CatameraGraph.dataTreeBars().config(config);
    this.$baseGrp.datum(this.newFormatedData).call(bars);

    var xAxis = CatameraGraph.xAxisUneven(mode).config(config);
    this.$baseGrp.datum(this.newFormatedData).call(xAxis);

    var legendGroup = CatameraGraph.legendGroup().config(config);
    if (this.labels.length > 1) {
      this.$baseGrp.datum(this.labels).call(legendGroup);
    } else {
      this.$baseGrp.datum([]).call(legendGroup);
    }

    const $slices = this.$baseGrp.selectAll('.mrgraph-bar');
    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;
      let temp = {};

      if (d) {
        const { label, value, parrent } = d;
        temp = { label, value };
        if (`${parrent}`.toLowerCase() != 'label') {
          temp.subLabel = parrent;
        }
      }
      return temp;
    };

    if ($slices) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.labels,
        data: this.graphData,
        ...config,
      });
      $slices
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this)(config);
    }
  }
}

export default withGraphComponent(CMVariableColumnsGraph, { className: 'cm-datatree-columns' });
