/* eslint-disable */
import * as d3 from 'd3';
import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import Pyramid from './pyramid';

let pyramid = function() {
  let config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    showBackground: true,
    showSliceLabel: true,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['#37c2cf', '#fad11b', '#5556fd'],
  };

  // Creating a responsive scale
  let toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    selection.each(data => {
      if (!data) {
        data = [];
      }
      let labelfontSize = config.labelFontSize || config.fontSize;

      let valueColor = config.labelColor || '#737b86';
      if (config.showBandColors) {
        valueColor = undefined;
      }

      // setting the Data
      let PyramidReturnValue = Pyramid();
      var pyramid = PyramidReturnValue.size([config.pyramidWidth, config.height]).value(function(d) {
        return d.value;
      });
      // ************************************************************************************************

      // setting the path variable
      var line = d3
        .line()
        .curve(d3.curveLinearClosed)

        .x(function(d, i) {
          return d.x;
        })
        .y(function(d, i) {
          return d.y;
        });

      // ****************************************************************************************************
      //  drawing Pyramid
      var pyramidBaseGrp = selection.selectAll('.pyramid-base-group').data([0]);
      pyramidBaseGrp.exit().remove();
      pyramidBaseGrp
        .enter()
        .append('g')
        .attr('class', 'pyramid-base-group')
        .merge(pyramidBaseGrp);

      let pyramidSlices = pyramidBaseGrp.selectAll('.pyramid-group').data(pyramid(data));
      pyramidSlices.exit().remove();
      pyramidSlices
        .enter()
        .append('path')
        .attr('class', 'pyramid-group')
        .merge(pyramidSlices)
        .style('fill', function(d, i) {
          return MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length]);
        })
        .transition()
        .duration(config.duration)
        .delay(function(d, i) {
          const delay = (config.duration || 50) / ((data && data.length) || 1);
          return i * delay;
        })
        .attr('d', function(d) {
          return line(d.coordinates);
        });
      // *********************************************************************************************************

      // Adding Labels
      var textBaseGrp = selection.selectAll('.text-base-group').data([0]);
      textBaseGrp.exit().remove();
      textBaseGrp
        .enter()
        .append('g')
        .attr('class', 'text-base-group')
        .merge(textBaseGrp);

      var textGroup = textBaseGrp.selectAll('.pyramid-text').data(pyramid(data));
      textGroup.exit().remove();

      if (config.showLabelGroup) {
        textGroup
          .enter()
          .append('text')
          .attr('class', 'pyramid-text')
          .merge(textGroup);

        selection
          .selectAll('.pyramid-text')
          .transition()
          .attr('transform', (d, i) => {
            return `translate(${config.labelPosition - labelfontSize * 3},${(d.coordinates[0].y + d.coordinates[1].y) /
              2})`;
          })
          .attr('font-family', '"Rubik", sans-serif')
          .attr('fill', (d, i) =>
            MRParseColors(valueColor || d.labelColor || config.graphColors[i % config.graphColors.length])
          )
          .attr('font-weight', 500)
          .attr('font-size', (d, i) => {
            const bandHeight = Math.abs(d.coordinates[0].y - d.coordinates[1].y);
            if (bandHeight < labelfontSize) {
              return 0;
            }
            return labelfontSize;
          })
          .text(function(d) {
            let returnedText = `${d.label}`;
            if (config.showValues) {
              returnedText += `: ${d.value.toFixed()}`;
            }

            return `${returnedText}`;
          })
          .style('text-anchor', 'end');
      }
      return selection;
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    config.labelPosition = config.labelPosition || config.width;
    config.pyramidWidth = config.pyramidWidth || config.width;

    // set default config
    return chart;
  };

  return chart;
};
export default pyramid;
