import React, { Component } from 'react';
import { MRParseColors } from '../../GraphLibrary';
import './index.css';

class CMSummaryList extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];

    this.state = {
      colors: props.colors || this.colors,
      data: props.data && props.data.data,
      labels: props.data && props.data.labels,
    };
  }
  _getColor(colors, index) {
    index = index % colors.length;
    return MRParseColors(colors[index]);
  }
  _dataChanged(d) {
    if (d) {
      this.setState({
        colors: this.props.colors || this.colors,
        data: d ? d.data || [] : [],
        labels: d ? d.labels || [] : [],
      });
    }
  }
  render() {
    let data = this.state.data.slice(0, 3);
    const { graphColors = this.state.colors, fontSize = '1.5em' } = this.props.preferences;

    if (this.state.labels && this.state.labels.length > 1) {
      data = [];

      this.state.labels.forEach(d => {
        const label = d.label;
        const key = d.value;

        const obj = {};

        this.state.data.forEach(item => {
          const property = item.label;
          const value = item[key];
          if (value) {
            obj[property] = value;
          }
        });

        if (Object.keys(obj).length > 0) {
          obj.label = label;

          data.push(obj);
        }
      });
    }

    const subtext = (this.props.data && this.props.data.summary && this.props.data.summary.subtext) || '';
    if (data && data.length) {
      return (
        <div className="cm-summary-list cm-graph-container ">
          <div className="owner-container">
            {data.map((item, index) => (
              <div className="owner" key={index}>
                {item.logo ? (
                  <img className="cm-summary-logo" src={item.logo} alt="logo" />
                ) : (
                  <div
                    className="cm-summary-circle"
                    style={{
                      backgroundColor: this._getColor(graphColors, index),
                      width: '2em',
                      height: '2em',
                      fontSize,
                    }}
                  >
                    {item.label.substr(0, 2)}
                  </div>
                )}
                <div className="username">
                  <div className="name">{item.label}</div>
                  <div className="position">{item.subtext || subtext}</div>
                </div>
                <div className="ownership-value">
                  <div>{item.value}</div>
                  <div className="bottom-line" style={{ borderBottomColor: this._getColor(graphColors, index) }} />
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    }
    return null;
  }
}

export default CMSummaryList;
