import * as d3 from 'd3';
import MRFormatter from '../../mr-formatter';
import MRParseColors from '../../mr-parsecolor';
import { toString } from '../../Number';

const formatNumber = (config, value) => {
  const num = {
    value: parseInt(value),
    suffix: config.valueSuffix,
    prefix: config.valuePrefix,
  };
  const options = {
    ...config,
    maximumSignificantDigits: 3,
    notation: 'compact',
    compactDisplay: 'short',
  };
  const text = toString(num, options);
  return text;
};

const legendGroup = function() {
  var config = {
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 600,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    // stackedGraphColors: ['#2e365d', '#4471c1', '#44bff5', '#72d4dd', '#f0597c'],

    hideLegendValue: true,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    selection.each(function(data) {
      var legendsData = [];
      data.forEach(function(d) {
        if (d.label) {
          var item = {
            label: d.label,
            value: d.value,
            color: d.color || d.labelColor,
            arcLabel: d.arcLabel,
          };
          legendsData.push(item);
        }
      });

      var cols = config.cols;
      var fontSize = config.fontSize || Math.max((config.width / 40) * config.viewRatio, 9);

      var rows = Math.ceil(legendsData.length / cols);
      var colSpacing = (fontSize * 0.1) / config.viewRatio;
      var bulletSpacing = fontSize * 1.2;
      var colWidth = config.width / cols - (cols - 1) * colSpacing;
      var maxWidth = colWidth - fontSize * 2;

      var negetiveValues = config.negetiveValue ? '.pie-negetive-legend' : '.pie-legend-group';
      selection.selectAll(negetiveValues).remove();
      var legendGroup = selection.selectAll(negetiveValues).data([0]);
      legendGroup.exit().remove();
      if (config.showLegends) {
        legendGroup
          .enter()
          .append('g')
          .attr('class', function() {
            return config.negetiveValue ? 'pie-negetive-legend' : 'pie-legend-group';
          })
          .merge(legendGroup);

        var legend = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .data(legendsData);
        legend.exit().remove();
        // Add nodes
        legend
          .enter()
          .append('g')
          .attr('class', 'mr-legend')
          .merge(legend);

        legend.append('circle');

        var circle = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .selectAll('.legend-circle')
          .data([1]);
        circle.exit().remove();
        circle
          .enter()
          .append('circle')
          .attr('class', 'legend-circle')
          .merge(circle);

        var legendText = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .selectAll('.legend-text')
          .data([1]);
        legendText.exit().remove();
        legendText
          .enter()
          .append('text')
          .attr('class', 'legend-text')
          .attr('text-anchor', 'start')
          .merge(legendText);

        var legendLabel = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .selectAll('.legend-text')
          .selectAll('.legend-label')
          .data([1]);
        legendLabel.exit().remove();
        legendLabel
          .enter()
          .append('tspan')
          .attr('class', 'legend-label')
          .attr('fill', '#777')
          .merge(legendLabel);

        var legendValue = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .selectAll('.legend-text')
          .selectAll('.legend-value')
          .data([1]);
        legendValue.exit().remove();
        legendValue
          .enter()
          .append('tspan')
          .attr('class', 'legend-value')
          .merge(legendValue);

        legendValue
          .enter()
          .append('title')
          .attr('class', 'legend-callout');

        // Update all nodes
        legend = selection
          .selectAll(negetiveValues)
          .selectAll('.mr-legend')
          .data(legendsData)
          .merge(legend);
        legend.attr('transform', (d, i) => {
          return !config.negetiveValue
            ? `translate(${(colWidth + colSpacing) * Math.floor(i / rows)},-${fontSize *
                1.8 *
                (rows - ((i % rows) - 0.6)) -
                fontSize * 2.6})`
            : `translate(${(colWidth + colSpacing) * Math.floor(i / rows)},${config.height * 0.2})`;
        });

        var legendBullet = legend
          .select('circle')
          .attr('r', config.legendBulletRadius || config.fontSize * 0.4)
          .attr('cy', -fontSize * 0.4)
          .attr('cx', fontSize * 0.2)
          .attr('fill', (d, i) => {
            return MRParseColors(d.color || d.labelColor || config.graphColors[i % config.graphColors.length]);
          });

        legendText = legend
          .select('text')
          .attr('font-size', fontSize)
          .attr('dx', bulletSpacing);

        if (config.hideLegendValue === false) {
          legendText.select('.legend-value').text(d => {
            // if MRFormatter could handle this unit, store the unit
            var formatted = MRFormatter.format(d.value);
            var legend = { value: formatted.value, unit: formatted.unit ? formatted.unit : '' };
            if (!formatted.unit) {
              // if value is pure number or contains other units
              legend.value = formatted.parts[0].value;
            }
            return ' - ' + legend.value + (config.overrideUnit || legend.unit);
            // if (!Number.isNaN(parseFloat(d.value))) {
            //   var formatted = MRFormatter.format(d.value, 'toNumber');
            //   return ` - ${formatted.value}`;
            // } else {
            //   return '';
            // }
          });
        }

        // Callout
        legendText.select('.legend-callout').text(d => {
          return d.label;
        });

        legendText.select('.legend-label').text(d => {
          return d.label;
        });

        // clip legend text width
        legendText.select('.legend-label').text(function(d, i) {
          try {
            var textWidth = selection.selectAll('.legend-label')._groups[0][i].getBBox().width;
          } catch (e) {
            var textWidth = selection.selectAll('.legend-label')._groups[0][i].getBoundingClientRect().width;
          }
          var text = d.label || '';
          if (config.legendsMaxLength && config.legendsMaxLength < text.length) {
            text = text.slice(0, +config.legendsMaxLength) + '...';
          }
          const { mode, formatLegendLabels } = config;
          if (mode === 'Choropleth' && !d.label.includes('%') && formatLegendLabels) {
            var labels = d.label.split('-');
            var text1 = formatNumber(config, parseInt(labels[0]));
            var text2 = formatNumber(config, parseInt(labels[1]));
            return `${text1}-${text2}`;
          }

          if (textWidth > maxWidth) {
            text = text.slice(0, Math.ceil((text.length * maxWidth) / textWidth) + 5);
          } else {
            return text;
          }
          while (textWidth > maxWidth && text.length > 0) {
            text = text.slice(0, -1);
            d3.select(this).text(`${text}..`);
            textWidth = selection.selectAll('.legend-label')._groups[0][i].getBBox().width;
          }
          return `${text}..`;
        });

        try {
          var centerX =
            (config.width -
              selection
                .select(negetiveValues)
                .node()
                .getBBox().width) /
              2 +
            toPx(config.margin.left);
        } catch (e) {
          var centerX =
            (config.width -
              selection
                .select(negetiveValues)
                .node()
                .getBoundingClientRect().width) /
              2 +
            toPx(config.margin.left);
        }
        selection
          .selectAll(negetiveValues)
          .attr('transform', `translate(${centerX + fontSize / 2},${config.clientHeight})`);

        return selection;
      }
    });
  }

  // Getters and Setters for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);

    if (config.legendFontSize) {
      config.fontSize = config.legendFontSize;
    }

    if (!config.cols) {
      config.cols = 2;
    }
    return chart;
  };

  return chart;
};
export default legendGroup;
