import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import d3cloud from 'd3-cloud';

const getFontScaling = config => {
  const { minFontSize, maxFontSize, fontSize } = config;
  let minFont = minFontSize || Math.min(config.width / 40, 10);
  let maxFont = maxFontSize || Math.max(config.width / 20, 60);
  return { maxFont, minFont };
};

const tagCloud = function() {
  var config = {
    width: 0,
    height: 0,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
    animate: true,
  };
  function chart(selection) {
    selection.each((dataObject, i) => {
      var wordCloud = selection.selectAll('.wordCloud').data(dataObject);
      wordCloud.exit().remove();

      wordCloud
        .enter()
        .append('g')
        .attr('class', 'wordCloud')
        .merge(wordCloud)
        .attr('transform', 'translate(' + config.width / 2 + ',' + config.height / 1.8 + ')');

      var wordCloud = selection.select('.wordCloud');

      const arr = (dataObject[0] || []).map(function(d) {
        return parseFloat(d.value) || 0;
      });

      let maxValue = d3.max(arr);
      let minValue = d3.min(arr);

      let { maxFont, minFont } = getFontScaling(config);

      var fontScale = d3
        .scaleLinear()
        .domain([minValue, maxValue])
        .range([minFont, maxFont]);

      d3cloud()
        .timeInterval(Infinity)
        .size([config.width, config.height])
        .font('Impact')
        .padding(+config.wordPadding)
        .words(dataObject[0])
        .fontSize(function(d, i) {
          return fontScale(d.value);
        })
        .rotate(function() {
          if (config.width >= config.height) {
            return 0;
          } else {
            return ~~(Math.random() * 2) * 90;
          }
        })
        .text(function(d) {
          return d.label;
        })
        .on('end', draw)
        .start();

      function draw(words) {
        let nodes = wordCloud.selectAll('.mr-wordcloud-text').data(words);
        nodes.exit().remove();

        nodes
          .enter()
          .append('text')
          .attr('text-anchor', 'middle')
          .attr('class', 'mr-wordcloud-text')
          .attr('opacity', 0)
          .merge(nodes)
          .attr('transform', function(d, i) {
            return 'translate(' + [d.x, d.y] + ')rotate(' + d.rotate + ')';
          })
          .attr('fill', function(d, i) {
            return MRParseColors(config.graphColors[Math.floor(Math.random() * config.graphColors.length)]);
          })
          .attr('font-size', '0px')
          .attr('opacity', 0)
          .transition()
          .delay(function(d, i) {
            return 30 * i;
          })
          .duration(function(d, i) {
            return 500;
          })
          .attr('font-size', function(d) {
            return fontScale(d.value) + 'px';
          })
          .attr('opacity', 1.0)
          .text(function(d) {
            return d.label;
          });
      }
    });

    return selection;
  }

  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };
  return chart;
};
export default tagCloud;
