import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import Utils from './utils';

var area = function(animate = true) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    animate: true,
    duration: 500,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    var duration = config.animate ? config.duration : 0;

    if (selection.select('.area-group').empty()) {
      selection.append('g').attr('class', 'area-group');
    }

    selection.each(function(data, i) {
      selection.select('defs').remove();
      var dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      //configuration
      var y = d3.scaleLinear().rangeRound([config.height, 0]);
      y.domain([
        0,
        +d3.max(data, function(datas) {
          if (config.isStacked) {
            return +d3.sum(datas, function(d) {
              return d._formatted.value;
            });
          }
          return +d3.max(datas, function(d) {
            return d._formatted.value;
          });
        }),
      ]);

      var d3line = d3
        .line()
        .x(config.x)
        .y(y(0));

      var d3Newline = d3
        .line()
        .x(config.x)
        .y(config.y1);

      var d3area = d3
        .area()
        .x(config.x)
        .y1(y(0))
        .y0(y(0));

      var d3NewArea = d3
        .area()
        .x(config.x)
        .y1(config.y1)
        .y0(config.y0);

      if (config.curve) {
        d3line.curve(d3[config.curve]);
        d3Newline.curve(d3[config.curve]);
        d3area.curve(d3[config.curve]);
        d3NewArea.curve(d3[config.curve]);
      }

      // Area/Line group
      selection
        .select('.area-group')
        .selectAll('.areaPathGroup')
        .data(data)
        .join(
          enter => enter.append('g').attr('class', 'areaPathGroup'),
          () => {},
          exit => exit.remove()
        );

      // Area
      selection
        .selectAll('.areaPathGroup')
        .data(data)
        .selectAll('.areagraph-path')
        .data(d => [d])
        .join(
          enter =>
            enter
              .append('path')
              .attr('class', 'areagraph-path')
              .attr('fill', function(d) {
                return config.medianArea ? '#efefef' : MRParseColors(d.color);
              })
              .attr('d', d3area),

          update =>
            update
              .transition()
              .ease(d3.easeCubicOut)
              .duration((d, i) => {
                return duration;
              })
              .attr('d', d3NewArea)
              .style('opacity', config.backgroundOpacity),

          exit => exit.attr('opacity', 1e-6).remove()
        );

      // Line
      selection
        .selectAll('.areaPathGroup')
        .data(data)
        .selectAll('.linegraph-path')
        .data(d => [d])
        .join(
          enter =>
            enter
              .append('path')
              .attr('class', 'linegraph-path')
              .attr('fill', 'none')
              .attr('stroke', function(d, i) {
                return MRParseColors(d.labelColor || d.color || config.graphColors[i % config.graphColors.length]);
              })
              .attr('stroke-linejoin', 'round')
              .attr('stroke-linecap', 'round')
              .attr('d', d3line)
              .style('opacity', '1')
              .attr('stroke-width', function(d, i) {
                return config.medianArea ? '2' : config.highlightedStrokeWidthSize || 2;
              }),

          update =>
            update
              .transition()
              .ease(d3.easeCubicOut)
              .duration((d, i) => {
                return config.duration;
              })
              .attr('d', d3Newline),

          exit => exit.attr('opacity', 1e-6).remove()
        );

      // Create unique id for median area mask
      if (!this.medianmaskid) {
        this.medianmaskid = `medianmask${Utils.makeid()}`;
      }

      const medianmaskid = this.medianmaskid;

      if (config.medianArea) {
        selection
          .selectAll('defs')
          .data([1])
          .join(
            enter => enter.append('defs'),
            () => {},
            exit => exit.remove()
          );

        selection
          .selectAll('defs')
          .selectAll('mask')
          .data(data)
          .join(
            enter =>
              enter
                .append('mask')
                .attr('id', medianmaskid)
                .append('rect')
                .attr('x', d => {
                  return config.x(d[0]);
                })
                .attr('width', d => {
                  return config.x(d[d.length - 1]);
                })
                .attr('fill', 'white')
                .attr('y', 0)
                .attr('height', (d, i) => {
                  return Math.abs(
                    this.prevmaskheight || config.y({ _formatted: { value: d.median || config.medianValue } })
                  );
                }),
            update =>
              update
                .transition()
                .duration(duration)
                .ease(d3.easeQuadInOut)
                .attr('height', (d, i) => {
                  this.prevmaskheight = Math.abs(config.y({ _formatted: { value: d.median || config.medianValue } }));
                  return this.prevmaskheight;
                }),
            exit => exit.remove()
          );

        // Highlighted area when median is enabled
        selection
          .selectAll('.areaPathGroup')
          .data(data)
          .selectAll('.areagraph-path-highlight')
          .data(d => [d])
          .join(
            enter =>
              enter
                .append('path')
                .attr('mask', `url(#${medianmaskid})`)
                .attr('class', 'areagraph-path-highlight')
                .attr('fill', function(d) {
                  return MRParseColors(d.color);
                })
                .attr('d', d3area)
                .style('opacity', function(d, j) {
                  return config.medianArea ? '0.1' : 1;
                }),

            update =>
              update
                .transition()
                .ease(d3.easeCubicOut)
                .duration((d, i) => {
                  return duration;
                })
                .attr('d', d3NewArea),

            exit => exit.attr('opacity', 1e-6).remove()
          );

        // Highlighted line when median is enabled
        selection
          .selectAll('.areaPathGroup')
          .data(data)
          .selectAll('.linegraph-path-highlight')
          .data(d => [d])
          .join(
            enter =>
              enter
                .append('path')
                .attr('mask', `url(#${medianmaskid})`)
                .attr('class', 'linegraph-path-highlight')
                .attr('fill', 'none')
                .attr('stroke', function(d, i) {
                  return MRParseColors(d.highlightColor);
                })
                .attr('stroke-linejoin', 'round')
                .attr('stroke-linecap', 'round')
                .attr('d', d3line),

            update =>
              update
                .transition()
                .ease(d3.easeCubicOut)
                .duration(config.duration)
                .attr('d', d3Newline)
                .style('opacity', '1')
                .attr('stroke-width', function(d, i) {
                  return config.medianArea ? '3' : '';
                }),

            exit => exit.attr('opacity', 1e-6).remove()
          );

        selection
          .select('.area-group')
          .selectAll('.areagraph-median')
          .data(() => [data[0]])
          .join(
            enter =>
              enter
                .append('line')
                .attr('class', 'areagraph-median')
                .attr('stroke', (d, i) => {
                  return MRParseColors(config.medianColor || d.highlightColor);
                })
                .attr('x1', d => {
                  return config.x(d[0]);
                })
                .attr('x2', d => {
                  return config.x(d[0]);
                })
                .attr('y1', (d, i) => {
                  return config.y({ _formatted: { value: d.median || config.medianValue } });
                })
                .attr('y2', (d, i) => {
                  return config.y({ _formatted: { value: d.median || config.medianValue } });
                })
                .attr('stroke-dasharray', d => {
                  return '4 3';
                })
                .attr('stroke-dashoffset', d => {
                  return config.x(d[d.length - 1]);
                })
                .attr('stroke-width', 0)
                .attr('stroke-dashoffset', 0)
                .style('opacity', '1')
                .attr('stroke-width', 1),
            update =>
              update
                .transition()
                .duration(duration)
                .ease(d3.easeQuadInOut)
                .attr('x1', d => {
                  return config.x(d[0]);
                })
                .attr('x2', d => {
                  return config.x(d[d.length - 1]);
                })
                .attr('y1', (d, i) => {
                  return config.y({ _formatted: { value: d.median || config.medianValue } });
                })
                .attr('y2', (d, i) => {
                  return config.y({ _formatted: { value: d.median || config.medianValue } });
                })
          );
      }

      // Circles is enabled
      if (config.showCircleOnLines)
        selection
          .selectAll('.areaPathGroup')
          .data(data)
          .selectAll('.areagraph-circles')
          .data(d => {
            const circleData = (d || []).map((k, i) => {
              k.color = d.color;
              k.highlightColor = d.highlightColor;
              k.median = d.median || config.medianValue;
              return k;
            });
            return circleData;
          })
          .join(
            enter =>
              enter
                .append('circle')
                .attr('class', 'areagraph-circles')
                .attr('fill', '#fff')
                .attr('stroke-width', function(d, i) {
                  return config.medianArea ? '2.2' : '';
                })
                .attr('r', 0)
                .attr('cx', config.x)
                .attr('cy', config.height)
                .style('opacity', 0),
            update =>
              update
                .transition()
                .ease(d3.easeCubicOut)
                .duration(config.duration)
                .attr('stroke', function(d, i) {
                  const median = config.y({ _formatted: { value: d.median || config.medianValue } });
                  return config.y1(d) >= median ? MRParseColors(d.color) : MRParseColors(d.highlightColor);
                })
                .attr('r', config.circleGroupRadius || toPx(0.3))
                .attr('cx', config.x)
                .attr('cy', (d, i) => {
                  return config.y1(d);
                })
                .style('opacity', 1)
          );
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default area;
