import * as d3 from 'd3';
// import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';
import Utils from './utils';

var area = function(animate = true) {
  // One messy configurations
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 500,
    showScenario: false,
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    const self = this;
    // var duration = config.animate? config.duration:0;

    if (selection.select('.area-group').empty()) {
      selection.append('g').attr('class', 'area-group');
    }

    selection.each(function(data, i) {
      selection.select('defs').remove();
      var dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      //
      //configuration
      var y = d3.scaleLinear().rangeRound([config.height, 0]);
      y.domain([
        0,
        +d3.max(data, function(datas) {
          if (config.isStacked) {
            return +d3.sum(datas, function(d) {
              return d._formatted.value;
            });
          }
          return +d3.max(datas, function(d) {
            return d._formatted.value;
          });
        }) + 20,
      ]);
      var scenarioY = d3.scaleLinear().rangeRound([config.height, 0]);
      scenarioY.domain([
        0,
        +d3.max(data, function(datas) {
          if (config.isStacked) {
            return +d3.sum(datas, function(d) {
              return d.scenarioValue;
            });
          }
          return +d3.max(datas, function(d) {
            return d.scenarioValue;
          });
        }) + 20,
      ]);
      var d3line = d3
        .line()
        .x(config.x)
        .y(y(0));
      var d3Newline = d3
        .line()
        .x(config.x)
        .y(config.y1);
      // .y(config.y);

      var d3scenarioline = d3
        .line()
        .x(config.x)
        .y(scenarioY(0));
      var d3scenarioNewline = d3
        .line()
        .x(config.x)
        .y(config.y1Scenario);

      var d3area = d3
        .area()
        .x(config.x)
        .y1(y(0))
        .y0(y(0));

      var d3NewArea = d3
        .area()
        .x(config.x)
        .y1(config.y1)
        .y0(config.graphHeight);
      // .y0(y(0));

      //Join

      let reversedData = data;
      // if (config.invertStack) {
      //   reversedData = config.reverse(data);
      // }

      var areaGrp = selection
        .select('.area-group')
        .selectAll('.areaGrpclass')
        .data(reversedData);

      var pathsArea = selection.selectAll('.areagraph-path').data(reversedData);
      var pathsAreaHighlight = selection.selectAll('.areagraph-path-highlight').data(reversedData);

      var pathsLine = selection.selectAll('.linegraph-path').data(reversedData);

      var scenariopathsLine = selection.selectAll('.linegraph-scenario-path').data(reversedData);

      var pathsHighlight = selection.selectAll('.linegraph-path-highlight').data(reversedData);
      var medianLines = selection.selectAll('.areagraph-median').data(reversedData);

      var pathsCircles = selection
        .selectAll('.circleGrpclass')
        .data(reversedData)
        .selectAll('.areagraph-circles')
        .data(d => {
          (d || []).forEach(k => {
            k.color = d.color;
            k.highlightColor = d.highlightColor;
            k.median = d.median;
          });
          return d;
        });

      var pathsScenarioCircle = selection
        .selectAll('.circleScenarioGrpclass')
        .data(reversedData)
        .selectAll('.areagraph-scenario-circles')
        .data(d => {
          (d || []).forEach(k => {
            k.color = d.color;
            k.highlightColor = d.highlightColor;
            k.median = d.median;
          });
          return d;
        });

      // if (data.median) {
      let defs = selection.selectAll('defs').data([1]);
      defs = defs.enter().append('defs');
      defs.exit().remove();

      if (!this.medianmaskid) {
        this.medianmaskid = `medianmask${Utils.makeid()}`;
      }

      const medianmaskid = this.medianmaskid;

      if (config.type === 'medianarea') {
        let masks = defs.selectAll('mask').data(data);
        masks.exit().remove();
        masks = masks
          .enter()
          .append('mask')
          .append('rect');

        defs
          .selectAll('mask')
          .attr('id', medianmaskid)
          // defs
          .selectAll('rect')
          .attr('x', d => {
            return config.x(d[0]);
          })
          .attr('width', d => {
            return config.x(d[d.length - 1]);
          })
          .attr('fill', 'white')
          .attr('y', 0)
          .attr('height', (d, i) => {
            return this.prevmaskheight || config.y({ _formatted: { value: d.median } });
          })
          .transition()
          .duration(config.duration)
          .ease(d3.easeQuadInOut)
          .attr('height', (d, i) => {
            this.prevmaskheight = config.y({ _formatted: { value: d.median } });
            return this.prevmaskheight;
          });
      }

      //EXIT

      areaGrp.exit().remove();

      pathsArea
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      pathsAreaHighlight
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      pathsLine
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      scenariopathsLine
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      pathsHighlight
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      medianLines
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      pathsCircles
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      pathsScenarioCircle
        .exit()
        .attr('opacity', 1e-6)
        .remove();

      //UPDATE

      pathsArea
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('d', d3NewArea);

      pathsAreaHighlight
        // .attr('mask', `url(#${medianmaskid})`)
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('d', d3NewArea);

      pathsLine
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('d', d3Newline);

      scenariopathsLine
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('d', d3scenarioNewline);

      pathsHighlight
        // .attr('mask', `url(#${medianmaskid})`)
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('d', d3Newline);

      medianLines
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('x1', d => {
          return config.x(d[0]);
        })
        .attr('x2', d => {
          return config.x(d[d.length - 1]);
        })
        .attr('y1', (d, i) => {
          return config.y({ _formatted: { value: d.median } });
        })
        .attr('y2', (d, i) => {
          return config.y({ _formatted: { value: d.median } });
        });

      pathsCircles
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('cx', config.x)
        .attr('cy', (d, i) => {
          return config.y1(d);
        })
        .attr('r', toPx(0.3));

      pathsScenarioCircle
        .transition()
        .duration(config.duration)
        .ease(d3.easeQuadInOut)
        .attr('cx', config.x)
        .attr('cy', (d, i) => {
          return config.y1Scenario(d);
        })
        .attr('r', toPx(0.3));
      //ENTER

      var areas = areaGrp
        .enter()
        .append('g')
        .attr('class', 'areaGrpclass');

      var lines = areaGrp
        .enter()
        .append('g')
        .attr('class', 'lineGrpclass');

      var scenarioLines = areaGrp
        .enter()
        .append('g')
        .attr('class', 'scenarioGrpclass');

      var mouseBrushLine = areaGrp
        .enter()
        .append('g')
        .attr('class', 'mouseBrushLine');

      var circleCluster = areaGrp
        .enter()
        .append('g')
        .attr('class', 'circleGrpclass');

      var circleScenarioCluster = areaGrp
        .enter()
        .append('g')
        .attr('class', 'circleScenarioGrpclass');
      if (config.enableBrush) {
        areas
          .append('rect')
          .attr('class', 'areagraph-bg')
          .attr('fill', '#f7f7f7')
          .attr('opacity', '0.6');
        areaGrp
          .select('.areagraph-bg')
          .attr('x', config.brush.x1)
          .attr('y', config.brush.y1)
          .attr('width', config.brush.x2 - config.brush.x1)
          .attr('height', config.brush.y2 - config.brush.y1);
      }

      // Draw Area
      areas
        .append('path')
        .attr('class', 'areagraph-path')
        .attr('fill', function(d) {
          // return
          return config.type === 'medianarea' || config.enableBrush ? '#edf0f4' : MRParseColors(d.color);
        })
        .attr('d', d3area)
        .transition()
        .ease(d3.easeCubicOut)
        .duration((d, i) => {
          return config.duration;
        })
        .attr('d', d3NewArea);

      if (config.type === 'medianarea' || config.enableBrush) {
        areas
          .append('path')
          .attr('mask', config.type === 'medianarea' ? `url(#${medianmaskid})` : null)
          .attr('clip-path', config.enableBrush ? `url(#${config.clipPathId})` : null)
          .attr('class', 'areagraph-path-highlight')
          // .merge(allgrouppath)
          .attr('fill', function(d) {
            return MRParseColors(d.color);
          })
          .attr('d', d3area)
          .style('opacity', function(d, j) {
            return config.type === 'medianarea' ? '0.1' : 1;
          })
          .transition()
          .ease(d3.easeCubicOut)
          .duration((d, i) => {
            return config.duration;
          })
          .attr('d', d3NewArea);
      }

      lines
        .append('path')
        .attr('class', 'linegraph-path')
        .attr('fill', 'none')
        .attr('stroke', function(d, i) {
          return MRParseColors(d.color);
        })
        .attr('stroke-linejoin', 'round')
        .attr('stroke-linecap', 'round')
        .attr('d', d3line)
        .attr('stroke-width', 0)
        .style('opacity', config.enableBrush ? '1' : '1')
        .attr('stroke-width', function(d, i) {
          return config.type === 'medianarea' ? '3' : '';
        })
        .transition()
        .ease(d3.easeCubicOut)
        // .delay(config.duration)
        .duration((d, i) => {
          // return config.type === 'medianarea' ? config.duration : i * 100 + config.duration;
          return config.duration;
        })
        // .delay((d, i) => {
        //   return config.type === 'medianarea' ? config.duration : 0;
        // })
        .attr('d', d3Newline);

      scenarioLines
        .append('path')
        .attr('class', 'linegraph-scenario-path')
        .attr('fill', 'none')
        .style('stroke-dasharray', '5, 5')
        .attr('stroke', function(d, i) {
          return MRParseColors(d.color);
        })
        .attr('stroke-linejoin', 'round')
        .attr('stroke-linecap', 'round')
        .attr('d', d3scenarioline)
        .attr('stroke-width', 0)
        .style('opacity', config.enableBrush ? '1' : '1')
        .attr('stroke-width', function(d, i) {
          return config.type === 'medianarea' ? '3' : '';
        })
        .transition()
        .ease(d3.easeCubicOut)
        .duration((d, i) => {
          // return config.type === 'medianarea' ? config.duration : i * 100 + config.duration;
          return config.duration;
        })
        // .delay((d, i) => {
        //   return config.type === 'medianarea' ? config.duration : 0;
        // })
        .attr('d', d3scenarioNewline);

      if (config.type === 'medianarea') {
        // Highlighted line path
        lines
          .append('path')
          .attr('mask', `url(#${medianmaskid})`)
          .attr('class', 'linegraph-path-highlight')
          .attr('fill', 'none')
          .attr('stroke', function(d, i) {
            return MRParseColors(d.highlightColor);
          })
          .attr('stroke-linejoin', 'round')
          .attr('stroke-linecap', 'round')
          .attr('d', d3line)
          .transition()
          .ease(d3.easeCubicOut)
          .duration(config.duration)
          .attr('d', d3Newline)
          .style('opacity', '1')
          .attr('stroke-width', function(d, i) {
            return config.type === 'medianarea' ? '2' : '';
          });

        // median line
        lines
          .append('line')
          .attr('class', 'areagraph-median')
          .attr('stroke', (d, i) => {
            return MRParseColors(d.highlightColor);
          })
          .attr('x1', d => {
            return config.x(d[0]);
          })
          .attr('x2', d => {
            return config.x(d[0]);
            // return config.x(d[d.length - 1]);
          })
          .attr('y1', (d, i) => {
            return config.y({ _formatted: { value: d.median } });
          })
          .attr('y2', (d, i) => {
            return config.y({ _formatted: { value: d.median } });
          })
          .attr('stroke-dasharray', d => {
            return '4 3';
          })
          .attr('stroke-dashoffset', d => {
            return config.x(d[d.length - 1]);
          })
          .attr('stroke-width', 0)
          .attr('stroke-dashoffset', 0)
          .style('opacity', '1')
          .attr('stroke-width', 1)
          .transition()
          .ease(d3.easeCubicOut)
          .duration(config.duration)
          .attr('x2', d => {
            return config.x(d[d.length - 1]);
          });
      }

      if (config.type === 'medianarea' || config.enableBrush) {
        // let circleGroup = circleCluster.append('g').attr('class', 'areagraph-circlegroup');

        pathsCircles
          .data(d => {
            (d || []).forEach(k => {
              k.color = d.color;
              k.highlightColor = d.highlightColor;
              k.median = d.median;
            });
            return d;
          })
          .enter()
          .append('circle')
          .attr('class', 'areagraph-circles')
          .attr('fill', '#21264e')
          // .attr('display', function(d, i) {
          //   const showOrNot = 'none';
          //   showOrNot = config.showScenario ? 'block' : 'none';
          //   return showOrNot;
          // })
          .attr('stroke', function(d, i) {
            if (config.enableBrush) return '#fff';
            const median = config.y({ _formatted: { value: d.median } });
            return config.y1(d) >= median ? MRParseColors(d.color) : MRParseColors(d.highlightColor);
          })
          .attr('stroke-width', function(d, i) {
            return config.type === 'medianarea' || config.enableBrush ? '2' : '';
          })
          .attr('cx', config.x)
          .attr('cy', (d, i) => {
            return config.y1(d);
          })
          .style('opacity', function(d) {
            if (d.value === 0) return 0;
            return 1;
          })
          .attr('r', toPx(0.3));

        circleCluster
          .style('opacity', 0)
          .transition()
          .ease(d3.easeCubicOut)
          .delay(config.duration)
          .duration(config.duration)
          .style('opacity', 1);

        // let show = config.showScenario;

        pathsScenarioCircle
          .data(d => {
            (d || []).forEach(k => {
              k.color = d.color;
              k.highlightColor = d.highlightColor;
              k.median = d.median;
            });

            return d;
          })

          .enter()
          .append('circle')
          .attr('class', 'areagraph-scenario-circles')
          .merge(pathsScenarioCircle)
          .attr('fill', '#21264e')
          .attr('stroke', function(d, i) {
            if (config.enableBrush) return '#fff';
            const median = config.y({ _formatted: { value: d.median } });
            return config.y1(d) >= median ? MRParseColors(d.color) : MRParseColors(d.highlightColor);
          })
          .attr('stroke-width', function(d, i) {
            return config.type === 'medianarea' || config.enableBrush ? '2' : '';
          })

          .attr('cx', config.x)
          .attr('cy', (d, i) => {
            return config.y1Scenario(d);
          })
          .attr('display', function() {
            const showOrnot = config.showScenario ? 'block' : 'none';
            return showOrnot;
          })
          .style('opacity', function(d) {
            if (d.value === 0) return 0;
            return 1;
          })
          .attr('r', toPx(0.3));

        circleScenarioCluster
          .style('opacity', 0)
          .transition()
          .ease(d3.easeCubicOut)
          .delay(config.duration)
          .duration(config.duration)
          .style('opacity', 1);
      }
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default area;
