import * as d3 from 'd3';
import { CatameraGraph, MRParseColors } from '../../GraphLibrary';
import stackedBar from './stacked-bar-graph.js';
import ToolTip from '../../tooltip';

let toolTipClass;
class CMSingleBarElement extends CatameraGraph.GraphTooltip(CatameraGraph.d3baseGraph) {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMSingleBarElement';
    this.margin = {
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
    };
    toolTipClass = new ToolTip($ele);
  }
  setData(data) {
    const self = this;
    this.data = data.data;
    this.summary = data.summary;
    this.labels = this.data;
    this.widgetType = data.widgetType ? data.widgetType : this.widgetType;
  }
  getDataLabels = data => {
    let labels = [];
    this.data.map(d => {
      labels.push(d.label);
    });
    return labels;
  };

  render(animate = true) {
    this.margin = {
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
    };
    super.render();
    let self = this;
    if (!this.data) return;
    if (
      !this.height ||
      !this.width ||
      Number.isNaN(this.height) ||
      Number.isNaN(this.width) ||
      this.height <= 0 ||
      this.width <= 0
    )
      return;

    let xScale = d3
      .scaleLinear()
      .domain([0, d3.sum(this.data, d => d.value)])
      .range([0, this.width]);

    let barHeight = this.height / 20; //TODO depend on screen size
    var fontSize = Math.max((this.width / 40) * this.preferences.viewRatio, 9);
    let config = {
      width: this.width,
      height: this.height * 0.5,
      clientHeight: this.height,
      xScale: xScale,
      yScale: barHeight,
      barHeight: barHeight,
      tooltipsEnabled: true,
      svgHeight: this.height,
      widgetType: 'l1',
      cols: 3,
      fontSize: this.fontSize,
      hideLegendValue: true,
      displayLegends: true,
      wrapText: true,
      displayLabels: true,
      labels: this.labels,
      graphColors: this.colors || ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
      viewRatio: 0.9,

      y: (d, i) => {
        return yScale(d.label);
      },
      x: (d, i) => {
        return xScale(parseFloat(d.value));
      },
      x0: (d, i) => {
        return xScale(parseFloat(d.accumulated || 0));
      },
    };

    config = Object.assign(config, this.config);

    this.data.forEach((d, i) => {
      d.percentage = (parseFloat(d.value / this.summary.value) * 100).toFixed(2) + '%';
      d.valueSubText = config.valueSubText;
      d.index = i;
      // d.color = "color"+ (i+1);
      d.summary = this.summary.value;
    });
    var gridLines = stackedBar().config(config);
    this.$baseGrp.datum(this.data).call(gridLines);

    // legend group

    if (config.showLegends) {
      var legendGroup = CatameraGraph.legendGroup().config(config);
      this.$baseGrp.datum(this.labels).call(legendGroup);
    }
    if (config.tooltipsEnabled) {
      let singleBar = this.$baseGrp.select('.single-bar').selectAll('.path');
      singleBar
        .on('mouseenter', function(data) {
          let positionInfo = this.getBBox();
          let x = positionInfo.x;
          let width = positionInfo.width;
          let boxWidth = self.$ele.firstElementChild.offsetWidth;
          let defaultMargin = self.width / 6;
          let totlaX = x + defaultMargin + width;
          let leftPos = Math.max(0, totlaX - boxWidth / 2);
          let actualLeftPos = `${leftPos}px`;
          self.$ele.querySelector('#box').style.opacity = 1;
          self.$ele.firstElementChild.innerHTML = self.getBarInfo(actualLeftPos, data);
        })
        .on('mouseleave', () => {
          this.heighlightFirstNode(this);
        });

      const dataExtractionMethod = requiredParameters => {
        const { d } = requiredParameters;
        let temp = {};
        if (d) {
          const { label, value } = d;
          temp = { label, value };
        }
        return temp;
      };
      if (singleBar) {
        const eventHandlers = toolTipClass.getEventHandlers({
          dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
          labels: this.label,
          data: this.data,
          ...config,
        });
        singleBar
          .on('click', config.onClick && config.onClick.bind(this))
          .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
          .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
          .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

        config.onLoad && config.onLoad.bind(this);
      }
    }

    let firstPageLoad = true;
    self.$ele.firstElementChild.style.opacity = 0;
    if (firstPageLoad) {
      firstPageLoad = false;
      let firstChild = this.$baseGrp
        .select('.single-bar')
        .selectAll('.path')
        .nodes()[0];
      if (firstChild) {
        let positionInfo = firstChild.getBBox();
        let x = positionInfo.x;
        let width = positionInfo.width;
        let boxWidth = self.$ele.firstElementChild.offsetWidth;
        let defaultMargin = self.width / 6;
        let totlaX = x + defaultMargin + width;
        let leftPos = Math.max(0, totlaX - boxWidth / 2);
        let actualLeftPos = `${leftPos}px`;
        self.$ele.firstElementChild.style.opacity = 1;
        self.$ele.firstElementChild.innerHTML = self.getBarInfo(actualLeftPos, this.data[0]);
      }
    }
  }

  heighlightFirstNode(self) {
    let firstChild = self.$baseGrp
      .select('.single-bar')
      .selectAll('.path')
      .nodes()[0];
    if (firstChild) {
      let positionInfo = firstChild.getBBox();
      let x = positionInfo.x;

      let width = positionInfo.width;
      let height = positionInfo.height;
      let boxWidth = self.$ele.firstElementChild.offsetWidth;
      let defaultMargin = self.width / 6;
      let totlaX = x + defaultMargin + width;
      let leftPos = Math.max(0, totlaX - boxWidth / 2);
      let actualLeftPos = `${leftPos}px`;
      self.$ele.firstElementChild.style.opacity = 1;
      self.$ele.firstElementChild.innerHTML = self.getBarInfo(actualLeftPos, this.data[0]);
    }
  }

  getBarInfo(leftPos, data) {
    return `
			<div class="detail-box" style="left : ${leftPos}px">
						<div class="box-heading">
				${data.label}
			</div>
			<div style='color:${MRParseColors(data.color)};'
            class= 'box-value'
            >
				${data.percentage}
				<span >
					${' '}(${parseInt(data.value)}/${parseInt(data.summary)})
				</span>
			</div>
		</div>
			
			`;
  }
}

export default CMSingleBarElement;
