/** @module cm-line.js
 *   @author
 *   @description React Component wrapper for cm-line graph
 */

import React, { Component } from 'react';
import { toObject } from '../../GraphLibrary/Number';

import './index.css';

const roundToThousand = value => {
  if (value >= 1000) {
    return `${(value / 1000).toFixed(2)}k`;
  }
  return value;
};

class CMNumbers extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
  }

  render() {
    let { summary, title } = this.props.data;

    const { preferences = {} } = this.props;

    const number = {
      value: summary.value,
      prefix: preferences.valuePrefix,
      suffix: preferences.valueSuffix,
    };

    const options = { ...preferences };
    const style = {};
    if (preferences.fontSize) {
      style.fontSize = preferences.fontSize;
      if (!((preferences.fontSize || '').includes('px') || (preferences.fontSize || '').includes('em'))) {
        style.fontSize += 'px';
      }
    }

    const num = toObject(number, options);
    return (
      <div style={style} className={`cm-line cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" style={{ display: 'flex' }}>
          <div className="text-box-wrapper ">
            <div className="text-box-content">
              <label className="graph-value">
                <span className="val-prefix">
                  {num.prefix}
                  {num.prefixSeparator}
                </span>
                <span className="val-value">{num.value}</span>
                <span className="val-scale">
                  {num.scaleSeparator}
                  {num.scale}
                </span>
                <span className="val-suffix">
                  {num.suffixSeparator}
                  {num.suffix}
                </span>
              </label>
              {/* <label className="graph-value">{roundToThousand(summary.value)} </label> */}
              <span
                className={`graph-trend ${summary.trend ? (summary.trend === 'up' ? 'trend-up' : 'trend-down') : ''}`}
              />
              {/* <div className="graph-subtext">{summary.subtext}</div> */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CMNumbers;
