import * as d3 from 'd3';
import floatingTooltip from './tooltip';
import getGraphConfigurations from '../../Graph-Configuration';
import { CatameraGraph, MRFormatter } from '../../GraphLibrary';
import ToolTip from '../../tooltip';

let toolTipClass;
class CMGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMClusterGraph';
    this.data = [];
    // this.init($ele);
    this.colors = {};
    toolTipClass = new ToolTip($ele);

    // this.myBubbleChart = bubbleChart(this.$svgNode);
  }

  setData(data) {
    const parsed = [];
    const { summary } = data;
    this.data = data.data;
    this.data.map((item, index) => {
      item.id = index;

      if (item.value < ((summary && summary.range && summary.range.low) || 50)) {
        item.group = 'low';
      } else if (
        item.value >= ((summary && summary.range && summary.range.low) || 50) &&
        item.value < ((summary && summary.range && summary.range.medium) || 100)
      ) {
        item.group = 'medium';
      } else {
        item.group = 'high';
      }
    });
  }

  redraw(csvstring) {
    // let config = getGraphConfigurations.call(this);
    this.setData(csvstring);
    // this.myBubbleChart.draw(this.$svg, this.data, config);
  }

  render(animate = true) {
    const self = this;
    const { preferences } = this || { preferences: {} };
    super.render(animate);
    let config = getGraphConfigurations.call(this);
    config = {
      ...config,
      ...preferences,
    };

    // if (animate) {
    if (config) {
      var clusterbubble = CatameraGraph.clusterBubble(this.$svg, this.data).config(config);
      this.$baseGrp.datum(this.data).call(clusterbubble);
    }
    var $barFgs = d3.selectAll('.bubble');

    const dataExtractionMethod = requiredParameters => {
      const { d } = requiredParameters;
      console.log('dataextr', d);
      let temp = {};
      if (d) {
        const { label, value } = d;
        temp = { label, value };
      }
      return temp;
    };

    if ($barFgs) {
      const eventHandlers = toolTipClass.getEventHandlers({
        dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
        labels: this.label,
        data: this.data,
        ...config,
      });
      $barFgs
        .on('click', config.onClick && config.onClick.bind(this))
        .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
        .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
        .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

      config.onLoad && config.onLoad.bind(this);
    }

    // this.myBubbleChart(this.$svg, this.data, config);
    // } else '';

    if (this.width < 0) {
      return;
    }
  }
}

export default CMGraph;
