import * as d3 from 'd3';
import MRFormatter from '../mr-formatter';
import MRParseColors from '../mr-parsecolor';
import DropShadow from './drop-shadow';

var treeMapDraw = function(animate = true) {
  // One messy configurations
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    colors: {
      default: '#666',
    },
    fontSize: 15,
    animate: true,
    duration: 500,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  };

  // Creating a responsive scale
  var toPx = function(em) {
    return config.fontSize * em;
  };

  // draw the graph here
  function chart(selection) {
    const self = this;
    var duration = config.animate ? config.duration : 0;

    selection.each(function(data, i) {
      var dropShadow = DropShadow().config({});
      selection.datum([1]).call(dropShadow);

      var treemap = d3
        .treemap()
        .size([config.width, config.height])
        .round(false)
        .paddingInner(data.length > 1 ? toPx(0.02) : 0);

      var totalLeaves = data[0].length;
      var arr = [];
      data.forEach(item => arr.push({ children: item, name: '' }));
      data = { name: '', children: arr };

      var root = d3
        .hierarchy(data)
        .eachBefore(function(d) {
          d.data.id = (d.parent ? d.parent.data.id + '.' : '') + d.data.label;
        })
        .sum(function(d) {
          if (!d.children) return d._formatted.value;
        })
        .sort(function(a, b) {
          return b.height - a.height || b.value - a.value;
        });

      treemap(root);

      selection.selectAll('.footprintRect').remove();

      var rect = selection
        .selectAll('.footprintRect')
        .data(data.children)
        .enter()
        .append('g')
        .attr('class', 'footprintRect');

      var $cells = rect
        .selectAll('.footrect')
        .data(function(d, i) {
          return root.children[i].leaves();
        })
        .enter()
        .append('g')
        .attr('class', 'footrect')
        .style('cursor', 'pointer')
        .style('stroke', 'white')
        .style('stroke-width', 1);

      $cells.append('rect');
      $cells
        .append('text')
        .append('tspan')
        .attr('class', 'label');
      $cells
        .select('text')
        .append('tspan')
        .attr('class', 'value');
      $cells
        .select('text')
        .append('tspan')
        .attr('class', 'labelreal');
      $cells
        .select('text')
        .append('tspan')
        .attr('class', 'valuereal');

      $cells = selection.selectAll('g').each(function(d, i) {
        d.animation2 = { delay: 800 + 0 * i, duration: duration + 200 * i };
        d.animation = { delay: 90 * (totalLeaves - i), duration: duration + 270 * i };
      });

      $cells
        .select('rect')
        .attr('id', function(d) {
          return d.data.id;
        })
        .attr('fill', function(d, i) {
          if (i == 0) return;
          return MRParseColors(config.colors[i - 1]);
        })
        .attr('x', function(d, i) {
          return d.x0 * 1.01;
        })
        .attr('y', function(d, i) {
          return d.y0;
        })
        .attr('width', function(d, i) {
          return config.xScale(d.x1 - d.x0) + toPx(1);
        })
        .attr('height', function(d, i) {
          return config.yScale(d.y1 - d.y0);
        })
        .transition()
        .ease(d3.easeQuad)
        .delay(function(d, i) {
          return i * duration * 0.05;
        })
        .duration(function(d, i) {
          return duration + i * duration * 0.08;
        })
        .attr('x', function(d) {
          return d.x0;
        })
        .attr('width', function(d) {
          return config.xScale(d.x1 - d.x0);
        });

      $cells
        .select('text')
        .attr('clip-path', function(d, i) {
          return 'url(#clip-' + i + ')';
        })
        .style('opacity', function(d, i) {
          if (config.xScale(d.x1 - d.x0) <= toPx(1) || config.xScale(d.y1 - d.y0) <= toPx(1)) return 0;
        });

      $cells
        .select('tspan.label')
        .style('font-size', '' + toPx(0.7))
        .style('fill', 'white')
        .style('font-weight', '100')
        .transition()
        .delay(300)
        .duration(duration / 4)
        .attr('opacity', 0)
        .transition()
        .attr('x', function(d) {
          return toPx(3) + d.x0;
        })
        .attr('y', function(d) {
          return toPx(2.5) + d.y0;
        })
        .transition()
        .ease(d3.easeQuad)
        .delay(function(d, i) {
          return i * duration * 0.02;
        })
        .duration(function(d, i) {
          return duration + i * duration * 0.05;
        })
        .attr('opacity', 0.9)

        .attr('x', function(d) {
          return toPx(0.5) + d.x0;
        })
        .text(function(d) {
          try {
            if (config.xScale(d.x1 - d.x0) / toPx(0.48) < d.data.label.length) {
              var textLength = Math.floor(config.xScale(d.x1 - d.x0) / toPx(0.48));
              return d.data.label.substring(0, textLength - 2) + '..';
            }
            return d.data.label;
          } catch (err) {
            console.error(err);
          }
        });

      $cells
        .select('tspan.labelreal')
        .style('font-size', '' + toPx(0.001))
        .style('visibility', 'hidden')
        .text(function(d) {
          return d.data.label;
        });

      // append value
      $cells
        .select('tspan.value')
        .style('font-size', '' + toPx(1.25))
        .style('font-weight', '200')
        .style('fill', 'white')
        .transition()
        .delay(300)
        .duration(duration / 4)
        .attr('opacity', 0)
        .transition()
        .attr('x', function(d) {
          return toPx(2) + d.x0;
        })
        .attr('opacity', 1)
        .attr('y', function(d) {
          return toPx(1.5) + d.y0;
        })
        .transition()
        .ease(d3.easeQuad)
        .delay(function(d, i) {
          return i * duration * 0.03;
        })
        .duration(function(d, i) {
          return duration + i * duration * 0.07;
        })
        .attr('x', function(d) {
          return toPx(0.5) + d.x0;
        })
        .text(function(d) {
          var format = MRFormatter.format(parseFloat(d.data.value), 'toNumber');
          var value = numberWithCommas(format.fullValue) + '' + format.unit;
          if (config.xScale(d.x1 - d.x0) / toPx(0.48) < value.length) {
            var textLength = Math.floor(config.xScale(d.x1 - d.x0) / toPx(0.48));
            return value.substring(0, textLength - 2) + '..';
          }
          return value + '%';
        })
        .style('opacity', function(d, i) {
          if (config.xScale(d.y1 - d.y0) <= toPx(1.55)) return 0;
        });

      $cells
        .select('tspan.valuereal')
        .style('font-size', '' + toPx(0.001))
        .style('visibility', 'hidden')
        .text(function(d) {
          return d.data.value;
        });
    });

    return selection;
  }

  function numberWithCommas(x) {
    var parts = x.toString().split('.');
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    return parts.join('.');
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };

  return chart;
};

export default treeMapDraw;
