import MRFormatter from '../../mr-formatter';
import { toString } from '../../Number';

import * as d3 from 'd3';
let yAxis = function(mode) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    viewRatio: 1,
    animate: true,
    duration: 500,
    margin: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
    maxLabelLength: 10,
  };

  function chart(selection) {
    const self = this;

    let duration = config.animate ? config.duration : 0;

    selection.each(function(data, i) {
      if (data[0] && data[0].value) {
        if (typeof data[0].value == 'string' && data[0].value.includes('%')) {
          config.yAxisType = config.yAxisType ? config.yAxisType : 'percentage';
        }
      }

      let fontSize = +config.fontSize || Math.max((config.width / 40) * config.viewRatio, 9);
      let yAxisGroup = selection.selectAll('.y').data([data]);

      // Calculate number of ticks

      let numOfTicks = mode == 'simple' ? 3 : 5;
      numOfTicks = Math.floor(numOfTicks / config.viewRatio || 1);
      if (mode !== 'simple') {
        numOfTicks = Math.round((config.height / (config.fontSize * 3)) * (config.tickDensity || 0.5));
      }

      numOfTicks = Math.max(2, numOfTicks);

      let yAxis = d3.axisLeft(config.yScale).tickSizeInner(0);
      let { yAxisWidth } = config;
      let maxTextSize = 3;
      if (config.yAxisType == 'text') {
        yAxis.tickFormat(function(d, i) {
          if (typeof d == 'string' && d.length > 10) {
            return mode == 'heatmap' ? (i % 6 == 0 ? d.slice(0, 10) + '..' : '') : d.slice(0, 10) + '..';
          }
          return mode == 'heatmap' ? (i % 6 == 0 ? d : '') : d;
        });
      } else {
        yAxis
          .tickFormat(function(d, i) {
            let tickText = mode == 'simple' ? '' : d;
            let text = '';

            if (typeof tickText == 'number') {
              const num = {
                // prefix: sign,
                value: d,
                suffix: config.valueSuffix,
                prefix: config.valuePrefix,
              };
              const options = {
                ...config,
                maximumSignificantDigits: 3,
                notation: 'compact',
                compactDisplay: 'short',
              };
              text = toString(num, options);
            } else {
              let arrLen = data.length;
              if (data && data.constructor === Array && data[0] && data[0].constructor === Array) {
                arrLen = data[0].length;
              }

              /*
                Following code is to limit the text-length & number of labels, displayed in xAxis to prevent their overlap
                */
              const _labelWidth = config.yAxisLabelWidth != null ? config.yAxisLabelWidth : fontSize * 6.5; // Approximate length of the label.
              const _maxTicks = Math.floor(config.width / _labelWidth); // Max number of labels that can be displayed for current graph width.
              const _i = Math.ceil(arrLen / _maxTicks) || 1;

              if (i % _i) {
                return ''; // Hide '_i' th label
              }

              // TODO: if _maxTicks is 2, show first and last label

              // If there is space for additional characters, add it
              let _extraChar = Math.floor(6 * (1 - ((arrLen / _maxTicks) % 1)));
              if (i >= arrLen - 1) {
                _extraChar = 0;
              }
              let _maxLabelLength = 8 + _extraChar;
              if (_maxLabelLength <= 0) {
                return '';
              }
              if (d.toString().length >= _maxLabelLength) {
                if (_maxLabelLength > 2) return d.toString().slice(0, _maxLabelLength - 2) + '..';
                else return d.toString().slice(0, _maxLabelLength);
              }

              text = d;
            }
            maxTextSize = Math.max(maxTextSize, text.toString().length);

            if (config.yAxisType == 'percentage') {
              text = text + '%';
            }
            if (config.overrideUnit) text = text + config.overrideUnit;

            return text;
          })
          .tickSize(
            (() => {
              if (mode === 'heatmap' || mode === 'simple') return 0;

              yAxisWidth = config.yAxisWidth || config.fontSize * (maxTextSize - 0.5);

              return -config.width + (yAxisWidth - config.offsetY);
            })()
          )
          .ticks(numOfTicks);
      }

      /**
       * yaxis needs to be drawn twice. 1st time it is drawn its text width is caluculated.
       * 2nd time tick width and position is adjusted as per yAxis width
       */
      yAxisGroup
        .enter()
        .append('g')
        .attr('class', 'y axis')
        .merge(yAxisGroup)
        .attr('font-size', fontSize)
        .transition()
        .duration(duration)
        .call(yAxis);

      yAxisGroup
        .selectAll('.title')
        .data([config.yAxisTitle])
        .join(enter =>
          enter
            .append('text')
            .attr('class', 'yaxis title')
            .attr('text-anchor', 'middle')
            .text(d => d)
        )
        .attr('transform', `translate(${-yAxisWidth + config.offsetY + fontSize}, ${config.height / 2}) rotate(-90)`);

      // calculate yaxis text max width
 if(config.showYAxisFonts){
      selection
        .selectAll('.y')
        .style('display', config.showAxisY ? '' : 'none')
        .transition()
        .duration(duration)
        .attr('transform', d => {
          let paddingTop;

          if (data.length > 22 && config.width < 1000) {
            paddingTop = 4;
          } else {
            paddingTop = 0;
          }
          return 'translate(' + (yAxisWidth - config.offsetY) + ',' + paddingTop + ')';
        })
        .attr('font-size',fontSize)
        .attr('opacity', 1);
      }

      selection
        .selectAll('.y')
        .selectAll('text')
        .attr('fill', config.yAxisColor || 'currentColor');

      //Dashed Y Axis

      selection
        .selectAll('.y')
        .selectAll('line')
        .style('stroke-width', d => {
          if (config.dashedYAxis) {
            const value = d ? 1 : 2;
            return value;
          }
        })
        .style('stroke-dasharray', d => {
          if (config.dashedYAxis) {
            const value = d ? '6' : 'none';
            return value;
          }
        });
    });

    return selection;
  }

  // Getters and Setters  for config
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }

    config = Object.assign(config, val);
    config.fontSize = config.yAxisFontSize || config.fontSize;

    return chart;
  };

  return chart;
};
export default yAxis;
