/* eslint-disable */
import * as d3 from 'd3';
import MRParseColors from '../mr-parsecolor';

var clusterBubble = function(svgnode) {
  var config = {
    x: undefined,
    y: undefined,
    width: 0,
    height: 0,
    fontSize: 15,
    gapBetweenGroups: 10,
    animate: true,
    duration: 500,
    graphColors: ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'],
  };

  function chart(selection) {
    selection.each(function(data) {
      //****************************** */ /set size for rendering ****************************************//

      let svgHeight, svgWidth, centerX, centerY;
      var svg = svgnode;
      svgHeight = config.height;
      svgWidth = config.width;

      var diameter = Math.min(svgWidth, svgHeight);
      centerX = svgWidth / 2;
      centerY = svgHeight / 2;

      svgnode.attr('transform', 'translate(' + centerX + ',' + centerY + ')');

      let colorRange = config.colorRange ? config.colorRange : 5;

      //color for circle
      var color = d3
        .scaleLinear()
        .domain([0, colorRange])
        .range(['hsl(152,80%,80%)', 'hsl(228,30%,40%)'])
        .interpolate(d3.interpolateHcl);

      //initiate circle pack
      var pack = d3
        .pack()
        .size([svgWidth, svgHeight])
        .padding(2);

      var root = data.data;

      root = d3.hierarchy(root).sum(function(d) {
        return d.value;
      });

      var focus = root,
        view;
      pack(root).descendants();

      //****************************** */ Drawing circle  to clusterBubble ****************************************//

      var circle = svgnode.selectAll('circle').data(root.descendants());
      circle.exit().remove();
      circle
        .enter()
        .append('circle')
        .attr('class', function(d) {
          return d.parent ? (d.children ? 'node' : 'node node--leaf') : 'node node--root';
        })
        .attr('pointer-events', d => (!d.children ? 'none' : null))
        .style('fill', function(d, i) {
          return d.children ? color(d.depth) : 'white';
        });

      selection
        .selectAll('circle')
        .style('fill', function(d, i) {
          if (config.themeColor) {
            return MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length]);
          } else {
            return d.children ? color(d.depth) : 'white';
          }
        })
        .on('mouseover', function() {
          d3.select(this).attr('stroke', '#000');
        })
        .on('mouseout', function() {
          d3.select(this).attr('stroke', null);
        })
        .on('click', function(d) {
          if (focus !== d) {
            zoom(d, this), d3.event.stopPropagation();
          }
        });

      //****************************** */ Defining texts to cluster bubble ****************************************//

      var text = svgnode.selectAll('.label').data(root.descendants());
      text.exit().remove();
      text
        .enter()
        .append('text')
        .attr('class', 'label');

      selection
        .selectAll('text')
        .style('fill-opacity', function(d) {
          return d.parent === root ? 1 : 0;
        })
        .style('display', function(d) {
          return d.parent === root ? 'inline' : 'inline';
        })
        .text(function(d) {
          return d.data.label;
        })
        .style('opacity', function(d) {
          return config.showLabelGroup ? 1 : 0;
        })
        .style('font-weight', function(d) {
          return 600;
        })
        .style('fill', function(d) {
          return config.labelColor ? config.labelColor : 'black';
        });

      svg.style('background', color(-1)).on('click', function() {
        zoom(root, this);
      });
      d3.selectAll('.node.node--root').style('fill', function(d, i) {
        if (config.packingCircle || config.packingCircle === undefined) {
          if (config.themeColor) {
            return MRParseColors(d.labelColor || config.graphColors[i % config.graphColors.length]);
          } else {
            return color(d.depth);
          }
        } else {
          return 'transparent';
        }
      });
      zoomTo([root.x, root.y, root.r * 2]);
      //****************************** */onclick of Bubbles ****************************************//
      function zoom(d, ele) {
        var focus0 = focus;
        focus = d;
        var transition = d3
          .transition()
          .duration(d3.event.altKey ? 7500 : 750)
          .tween('zoom', function(d) {
            var i = d3.interpolateZoom(view, [focus.x, focus.y, focus.r * 2]);
            return function(t) {
              zoomTo(i(t), focus, ele);
            };
          });

        text
          .filter(function(d) {
            return d.parent === focus || this.style.display === 'inline';
          })
          .transition(transition)
          .style('fill-opacity', function(d) {
            return d.parent === focus ? 1 : 0;
          })
          .on('start', function(d) {
            if (d.parent === focus) this.style.display = 'inline';
          })
          .on('end', function(d) {
            if (d.parent !== focus) this.style.display = 'none';
          });
      }

      //****************************** */ Position of bubble ****************************************//

      function zoomTo(v, focus, ele) {
        var k = diameter / v[2];

        view = v;
        text.attr('transform', d => `translate(${(d.x - v[0]) * k},${(d.y - v[1]) * k})`);
        circle.attr('transform', function(d) {
          return 'translate(' + (d.x - v[0]) * k + ',' + (d.y - v[1]) * k + ')';
        });

        circle.attr('r', function(d) {
          return d.r * k;
        });

        text
          .style('font-size', function(d) {
            const fontsize = Math.max(8, (d.r * k * 2) / d.data.label.length);
            return config.labelFontSize ? config.labelFontSize : fontsize;
          })
          .style('text-anchor', 'middle');
      }
    });
    return selection;
  }
  chart.config = function(val) {
    if (!arguments.length) {
      return config;
    }
    config = Object.assign(config, val);
    return chart;
  };
  return chart;
};
export default clusterBubble;
