// window.CatameraGraph = window.CatameraGraph || {};
import * as d3 from 'd3';
import MRFormatter from './mr-formatter';
import customConfig from './customconfig';
import { showExtraNegativeTick } from './helpers';
import { normalize, findSliceValue } from './normalize';

class BaseGraph {
  constructor($ele) {
    this.$ele = undefined;
    this.$svg = undefined;
    this.$baseGrp = undefined;

    this.data = [];

    this.width = undefined;
    this.height = undefined;
    this.animate = true;
    this.yDomainMin = [0, 0];
    this.xDomainMin = [0, 0];

    this.preferences = {};
    this.graphConfig = {};

    this.init($ele);

    this.padding = {
      left: 0,
      top: 0,
      right: 0,
      bottom: 0,
    };

    this.margin = {
      left: 0,
      top: 0,
      right: 0,
      bottom: 0,
    };
  }

  setGraphConfig(graphConfig) {
    const pref = { ...this.graphConfig, ...graphConfig };
    this.graphConfig = pref;
  }

  setPreferences(preferences) {
    const pref = { ...this.preferences, ...preferences };

    pref.viewRatio = pref.viewRatio || 1;

    this.preferences = pref;
  }

  init($ele) {
    this.$ele = $ele;
    this.$svg = d3.select($ele).append('svg');
    this.$svgNode = this.$svg.node();
    this.$baseGrp = this.$svg.append('g');
  }

  customConfig = (options, graph) => {
    const config = customConfig(options, graph);

    return config;
  };

  sliceValue() {
    const options = {
      clientHeight: this.clientHeight,
      clientWidth: this.clientWidth,
      width: this.width,
      height: this.height,
    };

    return this.preferences.normalizationFactor || findSliceValue(this.graphCustomName, options);
  }

  showExtraNegativeTick(...params) {
    return showExtraNegativeTick(...params);
  }

  normalizeData(data) {
    if (this.preferences.normalizationFactor) {
      return normalize(JSON.parse(JSON.stringify(data)), this.preferences.normalizationFactor);
    }
    return data;
  }

  overRideLegendDisplay(value) {
    this.displayLegends = value;
  }

  groupExtraValues(data) {
    // when we have more than the required labels, aggregate labels with smaller values to "Others"
    // find out if "Others" already exists to avoid adding another "Other" label
    //SubLabels were added for bubble graph and it normally has computed data ex-'$2.10T'
    let subLabelExist = false,
      units = {};
    subLabelExist = data.filter(item => item.subLabel).length > 0;
    if (subLabelExist) {
      const prefix = data[0].subLabel.slice(0, 1);
      const suffix = data[0].subLabel.slice(-1);
      units = {
        prefix,
        suffix,
      };
    }

    const sliceValue = this.sliceValue();
    let others = data.filter(obj => obj.label === 'Others');
    const numOfArcs = others.length > 0 ? sliceValue : sliceValue - 1; // number of labels we'll have except "Others"
    if (others.length === 0) {
      others = { label: 'Others', value: 0, subLabel: 0 };
    } else {
      others = {
        label: 'Others',
        subLabel: 0,
        value: others[0].value ? parseFloat(others[0].value) : 0,
      };
    }
    const tempData = [];
    for (let i = 0; i < data.length; i += 1) {
      if (i < numOfArcs && data[i].label !== 'Others') {
        tempData.push(data[i]);
      } else if (i >= numOfArcs) {
        others.value += parseFloat(data[i].value);
        if (subLabelExist) {
          const { subLabel, sign } = data[i];
          const signedIntegar = sign + subLabel.substring(1, subLabel.length - 1);
          others.subLabel += parseFloat(signedIntegar);
        }
      }
    }
    if (others.value > 0) {
      if (subLabelExist) {
        if (others.subLabel.toString().indexOf('-') >= 0) {
          others.sign = '-';
          others.subLabel = units.prefix + others.subLabel.toString().slice(1) + units.suffix;
        } else {
          others.sign = '+';
          others.subLabel = units.prefix + others.subLabel.toString() + units.suffix;
        }
      }
      tempData.push(others);
    }
    return tempData;
  }

  setData(data) {
    try {
      this.data = JSON.parse(JSON.stringify(data));
    } catch (e) {
      this.data = data;
    }
  }

  toPx(em) {
    const valInPx = parseInt(this.fontSize * em, 10);

    if (valInPx) {
      return valInPx;
    }
    return 0;
  }
  // test

  getColorsFromData = (defaultColors, dataArray) => {
    if (!defaultColors) {
      defaultColors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
    }
    if (!dataArray) {
      return defaultColors;
    }

    if (defaultColors.length < dataArray.length) {
      const colors = [...defaultColors];
      for (let i = defaultColors.length - 1; i < dataArray.length; i += 1) {
        defaultColors[i] = colors[i % colors.length];
      }
    }

    dataArray.forEach((d, i) => {
      if (i < 6 && d.format) {
        if (['gray', 'neutral'].indexOf(d.format) !== -1) {
          defaultColors[i] = 'colorneutral';
        } else if (['green', 'positive'].indexOf(d.format) !== -1) {
          defaultColors[i] = 'colorpositive';
        } else if (['green', 'red'].indexOf(d.format) !== -1) {
          defaultColors[i] = 'colornegative';
        }
      }
    });

    return defaultColors;
  };

  getYAxisWidth = (maxVal, config = {}) => {
    if (config.yAxisWidth) return config.yAxisWidth;

    let len = 0;
    let yAxisPadding = 0;

    const { yAxisFontSize = this.fontSize, yAxisTitle } = config;
    const yAxisTitleWidth = yAxisTitle ? yAxisFontSize * 1.6 : 0;

    if (config.showAxisY) {
      len = maxVal.toString().length;

      if (typeof maxVal == 'number') {
        len = Math.min(Math.max(len, 2), 4);

        // const formatted = MRFormatter.format(maxVal, 'toNumber');
        // len = `${parseInt(formatted.fullValue) + formatted.unit}`.length;
      }
      if (config.yAxisType === 'percentage') len += 1;
      if (config.overrideUnit) len += `${config.overrideUnit}`.length;
      if (config.valuePrefix) {
        len += `${config.valuePrefix}`.length;
      }

      if (config.valueSuffix) {
        len += `${config.valueSuffix}`.length;
      }

      yAxisPadding = config.offsetY || yAxisFontSize * 0.5;
    }

    // let maxTextSize = Math.min(parseInt(maxVal).toString().length, 4);
    // var yAxisWidth = config.yAxisWidth || config.fontSize * (maxTextSize || 2) * 0.7;
    // calculating max  y value
    // return len;
    // const yAxisLabelLength = parseInt(maxVal).toString().length;
    // const maxTextSize = Math.min(Math.max(yAxisLabelLength, 4), 5);

    return yAxisFontSize * 0.6 * len + yAxisPadding + yAxisTitleWidth;
  };

  getGraphHeight(config = {}) {
    const { xAxisFontSize = this.fontSize, yAxisFontSize = this.fontSize } = config;

    const yAxisBottomOffset = config.showAxisY ? yAxisFontSize : 0; // To prevent cropping of yaxis text when xAxis is disabled
    let xAxisHeight = config.showAxisX ? xAxisFontSize * 2 : yAxisBottomOffset;
    if (config.xAxisTitle) xAxisHeight += xAxisFontSize * 1.6; // 1em title height + 0.6em padding

    if (!this.getLegendHeight()) return this.height - xAxisHeight;

    return this.height - this.getLegendHeight() - xAxisHeight;
  }

  findNumberOfCols() {
    if (this.preferences.numberOfCols) {
      return this.preferences.numberOfCols;
    }
    const labels = this.dataType === '2D' ? this.labels : this.data;
    const length = (labels || []).length;
    if (length > 4 || length % 3 === 0) return 3;
    return 2;
  }

  getLegendHeight = () => {
    const fontSize = this.preferences.legendFontSize || this.getFontSize();
    const legendPadding = fontSize * 0;
    const legendRowHeight = fontSize * 1.8;

    if (this.displayLegends) {
      const labels = this.dataType === '2D' ? this.labels : this.data;
      const cols = this.findNumberOfCols() || 3;
      const rows = Math.ceil(labels.length / cols);
      return labels.length > 1 ? rows * legendRowHeight + legendPadding : 0;
    }

    return 0;
  };

  getFontSize = () => {
    const winfontSize = window.innerHeight * 0.02;
    const boxSize = Math.min(this.height, this.width);
    if (boxSize <= 300) {
      return this.preferences.fontSize || Math.max(0.04 * boxSize, 8);
    }
    return this.preferences.fontSize || winfontSize;
  };

  mapLabelsToColors(data) {
    if (this.preferences.labelsToColorsMap) {
      const { labelsToColorsMap } = this.preferences;

      Object.keys(labelsToColorsMap).forEach(key => {
        data.forEach(item => {
          if (!Array.isArray(item)) {
            if (`${item.label}`.toLowerCase() === key.toLowerCase()) {
              item.labelColor = labelsToColorsMap[key];
            }
            if (Array.isArray(item.values)) {
              item.values.forEach(subItem => {
                if (`${subItem.label}`.toLowerCase() === key.toLowerCase()) {
                  subItem.labelColor = labelsToColorsMap[key];
                }
              });
            }
          } else {
            item.forEach(subItem => {
              const labelString = `${data.length > 1 ? subItem.labelText : subItem.label}`;
              if (labelString.toLowerCase() === key.toLowerCase()) {
                subItem.labelColor = labelsToColorsMap[key];
              }
            });
          }
        });
      });
    }

    return data;
  }

  setDimensions() {
    this.fontSize = this.getFontSize();
    this.$baseGrp.displayLegends = false;

    let clientHeight = 0,
      clientWidth = 0;

    if (this.$svgNode.clientHeight !== 0) clientHeight = this.$svgNode.clientHeight;
    else clientHeight = this.$svgNode.getBoundingClientRect().height;

    if (this.$svgNode.clientWidth !== 0) clientWidth = this.$svgNode.clientWidth;
    else clientWidth = this.$svgNode.getBoundingClientRect().width;

    this.height = clientHeight;
    this.width = clientWidth;
    this.clientHeight = clientHeight;
    this.clientWidth = clientWidth;

    if (clientHeight === 0) {
      // Dont redraw
      return;
      // if (this.retryCount === undefined) {
      //   this.retryCount = 1;
      // }
      // // sometimes height is 0, so redraw in few ms (happens in nightmare.js)
      // if (this.retryCount > 0) {
      //   setTimeout(() => {
      //     this.retryCount -= 1;
      //     this.render(this.animate);
      //   }, 500);
      // } else {
      //   this.retryCount = undefined;
      // }
      // return;
    }

    // NOT SURE OF THE PURPOSE COMMENTING FOR NOW
    // this.$svgNode.style.transform = `translate(${this.toPx(-1 * this.margin.left)}px,${this.toPx(
    //   -1 * this.margin.top
    // )}px)`;
  }

  getXScale() {
    return d3.scaleLinear().range([0, this.width]);
  }

  getYScale() {
    return d3.scaleLinear().range([0, this.height]);
  }

  getXDomain() {
    this.x.domain(
      this.data.map((d, i) => {
        return d.label;
      })
    );
  }

  getYDomain() {
    let maxVal = d3.max(this.data, d => {
      var formattedVal = MRFormatter.format(d.value);

      return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value * 1.2;
    });

    maxVal = Math.max(maxVal, this.yDomainMin[1]);
    this.y.domain([this.yDomainMin[0], maxVal]);
  }

  unmount = () => {
    try {
      this.$baseGrp.selectAll('*').remove();
    } catch (error) {
      //
    }
  };

  drawXAxis = () => {};

  drawYAxis = () => {};

  setupScales() {
    this.setDimensions();

    // create scales
    this.x = this.getXScale();
    this.y = this.getYScale();

    this.getXDomain();
    this.getYDomain();

    this.drawYAxis();
    this.drawXAxis();
  }

  render(animate) {
    this.animate = animate !== false;
    this.setupScales();
  }
}
// window.CatameraGraph.d3baseGraph = BaseGraph;

export default BaseGraph;
