/** @module cm-line.js
 *   @author
 *   @description React Component wrapper for cm-line graph
 */

import React, { Component } from 'react';
import getGraphConfigurations from '../../Graph-Configuration';
import './index.css';
// import image from './image.svg';

class CMImage extends Component {
  constructor(props) {
    super(props);
    this.colors = ['color1', 'color2', 'color3', 'color4', 'color5', 'color6'];
    this.graphRef = React.createRef();
  }

  render() {
    let { summary, title, data } = this.props.data;
    // let config = getGraphConfigurations.call(this.props.preferences);
    const graphData = data && data.length > 0 ? data[data.length - 1] : {};
    const defaultImg = '';
    return (
      <div className={`cm-line cm-graph-container ${this.props.className || ''}`}>
        <div className="cm-graph-wrp" ref={this.graphRef} style={{ display: 'flex' }}>
          <img
            className="maxWidth"
            src={
              this.props.preferences && this.props.preferences.image ? this.props.preferences.image : graphData.value
            }
          />
        </div>
      </div>
    );
  }
}

export default CMImage;
