import * as d3 from 'd3';
import { CatameraGraph, MRFormatter, Utils } from '../../GraphLibrary';
import getGraphConfigurations from '../../Graph-Configuration';
import ToolTip from '../../tooltip';
import withGraphComponent from '../../Graph-wrapper-HOC';

let toolTipClass;
class CMButterflyGraph extends CatameraGraph.d3baseGraph {
  constructor($ele) {
    super($ele);
    this.graphCustomName = 'CMButterflyGraph';
    this.colors = {};
    toolTipClass = new ToolTip($ele);
  }
  setData(data) {
    if (typeof data == 'string') {
      data = JSON.parse(data);
    }
    const self = this;

    this.data = data.data ? data.data : data;
    const normalize = this.preferences.normalize || false;

    if (normalize && this.data.length) {
      this.data = this.normalizeData(this.data);
    }

    this.summary = data.summary ? data.summary : '';
    this.labels = data.labels && data.labels.length > 0 ? data.labels : [{ label: 'label', value: 'value' }];
    this.filteredData = this.data.filter(function(d) {
      // filter out data object with one or more value undefined
      for (var i = 0; i < self.labels.length; i++) {
        if (!d[self.labels[i].value]) {
          d.pass = false;
        }
      }
      return d.pass != false;
    });

    var formattedData = [];
    var item = {};

    // process data object with multiple values
    for (var k = 0; k < this.labels.length; k++) {
      var items = [];
      for (var i = 0; i < this.filteredData.length; i++) {
        item = {
          label: this.filteredData[i].label,
          value: this.filteredData[i][this.labels[k].value],
          labelText: this.labels[k].label,
          _formatted: MRFormatter.format(this.filteredData[i][this.labels[k].value]),
        };
        items.push(item);
      }
      formattedData.push(items);
    }
    this.graphData = formattedData;
  }

  getAllXYScales(graphHeight, graphWidth, maxVal) {
    var yScale = d3
      .scaleBand()
      .range([0, graphHeight])
      .domain(
        this.filteredData.map(function(d, i) {
          return d.label;
        })
      );

    var xScale1 = d3
      .scaleLinear()
      .range([0, graphWidth / 2])
      .domain([maxVal, this.xDomainMin[0]]);

    var xScale2 = d3
      .scaleLinear()
      .range([graphWidth / 2, graphWidth])
      .domain([maxVal, this.xDomainMin[0]]);

    return { yScale, xScale1, xScale2 };
  }

  render(animate = true) {
    const self = this;
    // must set this before super.render() so we could resize the graph with provided padding value

    if (!this.filteredData) return;

    super.render();

    // responsive font size
    var fontSize = this.getFontSize();

    // Creating a responsive scale
    var toPx = function(em) {
      return fontSize * em;
    };

    //get xDomain
    var maxVal = 0;
    var maxLen = 0;
    var maxTotalVal = 0;
    if (this.graphData[0] && typeof this.graphData[0] == 'object') {
      for (var i = 0; i < this.graphData.length; i++) {
        var newMaxVal = d3.max(this.graphData[i], function(d) {
          var formattedVal = MRFormatter.format(parseFloat(d.value));
          d._formatted = formattedVal;
          return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value;
        });
        maxVal = newMaxVal > maxVal ? newMaxVal : maxVal;
      }
      this.filteredData.map(function(d, k) {
        maxLen = d.label && d.label.length > maxLen ? d.label.length : maxLen;

        var i = 0;
        var totalVal = 0;
        for (var label in self.labels) {
          totalVal += parseFloat(d[self.labels[i].value]);
          self.graphData[i][k].accumulated = totalVal;
          i++;
        }
        maxTotalVal = maxTotalVal > totalVal ? maxTotalVal : totalVal;
      });
    } else {
      maxVal = d3.max(this.graphData, function(d) {
        maxLen = d.label && d.label.length > maxLen ? d.label.length : maxLen;
        var formattedVal = MRFormatter.format(parseFloat(d.value));
        d._formatted = formattedVal;
        return formattedVal.maxValue ? formattedVal.maxValue : formattedVal.value * 1.1;
      });
    }

    maxVal = Math.max(maxVal * 1.1, this.xDomainMin[1]);

    // Get the config.
    var config = getGraphConfigurations.call(this);

    // Set Scales using getALLXYScale function

    var scale = this.getAllXYScales(config.height, config.width, maxVal);
    var { yScale, xScale1, xScale2 } = scale;
    var yScalePadding = 0.4;
    yScale
      .paddingInner(0.1)
      .paddingOuter(0)
      .padding(yScalePadding);

    // config for graphs
    config = {
      ...config,
      xScale1: xScale1,
      xScale2: xScale2,
      yScale: yScale,
      bandWidth: yScale.bandwidth(),
      uniqueId: `bar${Utils.makeid()}`,
      ...this.preferences,
    };

    var barConfig = Object.assign({}, config, {
      bandWidth: yScale.bandwidth(),
      y: (d, i) => {
        return yScale(d.label);
      },
      x1: (d, i) => {
        return config.width - xScale1(parseFloat(d.value));
      },
      x2: (d, i) => {
        return xScale2(parseFloat(d.value));
      },
      animate: false,
    });

    if (config.viewType != 'NA') {
      // bars should use graphData instead of filteredData because of different data structure
      var bars = CatameraGraph.butterflyElements().config(barConfig);
      this.$baseGrp.datum(this.graphData).call(bars);

      ////  Handling custom mouse events ////

      // tooltip

      if (config.tooltipsEnabled) {
        var $barFgs = this.$baseGrp.selectAll('.butterfly-group').selectAll('.mrgraph-bar');
        var $mainLabel = this.$baseGrp.selectAll('.base-bar-group').selectAll('.mrgraph-base-bar');

        // for label bars
        const dataExtractionMethod = requiredParameters => {
          const { d } = requiredParameters;
          let temp = {};
          if (d) {
            const { label, value } = d;
            temp = { label, value };
          }
          return temp;
        };

        if ($barFgs) {
          const eventHandlers = toolTipClass.getEventHandlers({
            dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
            labels: this.label,
            data: this.graphData,
            ...config,
          });
          $barFgs
            .on('click', config.onClick && config.onClick.bind(this))
            .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
            .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
            .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

          config.onLoad && config.onLoad.bind(this);
        }

        if ($mainLabel) {
          const eventHandlers = toolTipClass.getEventHandlers({
            dataExtractionMethod: config.dataExtractionMethod || dataExtractionMethod,
            labels: this.label,
            data: this.graphData,
            ...config,
          });
          $mainLabel
            .on('click', config.onClick && config.onClick.bind(this))
            .on('mouseenter', (config.onMouseEnter && config.onMouseEnter.bind(this)) || eventHandlers.mouseOver)
            .on('mousemove', (config.onMouseMove && config.onMouseMove.bind(this)) || eventHandlers.onMouseMove)
            .on('mouseleave', (config.onMouseLeave && config.onMouseLeave.bind(this)) || eventHandlers.onMouseOut);

          config.onLoad && config.onLoad.bind(this);
        }
      }

      //click

      if (config) {
        var $barFgs = this.$baseGrp.selectAll('.butterfly-group').selectAll('.mrgraph-bar');
        $barFgs.on('click', function(d) {
          if (config.onClick) config.onClick.bind(this).call(d);
        });

        //onLoaded
        config.onLoad && config.onLoad.bind(this)(config);
      }
    } else {
      this.$baseGrp.selectAll('*').remove();
    }
  }
}
export default withGraphComponent(CMButterflyGraph, { className: 'cm-bar' });
